
import React, { useState, useEffect } from 'react';

function Speaker({x, y, width, serialCom, displayMode}) {
    const [frequency, setFrequency] = useState(0);
    const [time, setTime] = useState(1);


    function handleToneUpdate(){
        serialCom("Speaker", {freq: frequency, time: time});
    }

    function mHover(class_name, attribute, colorA, colorB){
        document.querySelectorAll("." + class_name).forEach(element =>{
                element.setAttribute(attribute, element.getAttribute(attribute) == colorA ? colorB : colorA);
            }
        );
    }

  return (
      <>
        <svg 
            width={width} 
            viewBox={ "0 0 500 150"}
            x={x}
            y={y}
            preserveAspectRatio="xMinYMin"
        >   
        
        
        {/* Logo  
        {displayMode === "Basic" ?
        <>
          <line x1={100} x2={100} y1={25} y2={125} stroke="white" strokeWidth="4" />
          <svg viewBox="0 0 50 50" x={25} y={30 } width={55} preserveAspectRatio="xMinYMin">
            <rect x={5} y={5} rx={5} ry={5} width={40} height={40} fill="transparent" stroke="white" strokeWidth={4} />
          </svg>
          <text x={47.5} y={110} dominantBaseline="middle" textAnchor="middle" fill="white" style={{fontSize:"1.5em", fontWeight:"bolder"}}>Speaker</text>
        </> : <></>}*/} 
        <g transform =  "translate(-205 5)">
          {/* Frequency Selector */}
          <rect x={250} y={25} width={125} height={62.5} fill="white" stroke="black" strokeWidth="2"/>
          <foreignObject x={250} y={32.5} width={125} height={55}>
            <input type="number" step="1" min={0} value={frequency} onChange={e => {setFrequency(e.target.value)}} style={{color: "black", width:"70%", margin:"0 auto", fontSize: "2.5em", display:"block", fontWeight: "bold"}}/>
          </foreignObject>
          <rect x={250} y={90} width={125} height={25} fill="white" stroke="black" strokeWidth="2"/>
          <text x={312.5} y={104.5} dominantBaseline="middle" textAnchor="middle" fill="black" style={{fontSize:"1.5em", fontWeight:"bold"}}>Frequency</text>
          
          {/* Time Selector */}
          <rect x={400} y={25} width={125} height={62.5} fill="white" stroke="black" strokeWidth="2"/>
          <foreignObject x={400} y={32.5} width={125} height={55}>
            <input type="number" step="1" min={0} value={time} onChange={e => {setTime(e.target.value)}} style={{color: "black", width:"70%", margin:"0 auto", fontSize: "2.5em", display:"block", fontWeight: "bold"}}/>
          </foreignObject>
          <rect x={400} y={90} width={125} height={25} fill="white" stroke="black" strokeWidth="2"/>
          <text x={462.5} y={104.5} dominantBaseline="middle" textAnchor="middle" fill="black" style={{fontSize:"1.5em", fontWeight:"bold"}}>Time</text>
          
          {/* Update Tone */}
          <rect x={550} y={25} class="tone_update_button" width={125} height={90} fill="white" stroke="black" strokeWidth="2"/>
          <text x={612.5} y={75} class="tone_update_button" dominantBaseline="middle" textAnchor="middle" fill="black" style={{fontSize:"1.75em", fontWeight:"bold"}}>Play</text>
          <rect x={550} y={25} width={125} height={90} fill="transparent" style={{cursor:"pointer"}} onMouseEnter={()=>{mHover("tone_update_button", "fill", "black", "white")}} onMouseLeave={()=>{mHover("tone_update_button", "fill", "black", "white")}} onClick={()=> {handleToneUpdate()}} />
        </g>
        </svg>
        
         
     </>

    );
}

export default Speaker