import React, { useState, useEffect } from "react";

//Todo add field validation for the type
export function Variable_Field({name, starting_value, update, type}) {
  const [value, setValue] = useState(starting_value);
  const [color, setColor] = useState("black");
  const [weight, setWeight] = useState("normal");
  useEffect(() =>{
    setValue(starting_value);//Update if prop changes 
  },[starting_value]);
  
  //Default type is 1 (String)
  let input= <input type="text" value={value} onChange={e => {variableChange(e.target.value)}} style={{color: color,  fontWeight: weight, width: "50%"}}/>;
  switch(type){
    case '1'://Integer
      input = <input type="number" step="1" value={value} onChange={e => {variableChange(e.target.value)}} style={{color: color,  fontWeight: weight, width: "50%"}}/>;
      break;

    case '2'://Decimal
      input = <input type="number" step="any" value={value} onChange={e => {variableChange(e.target.value)}} style={{color: color,  fontWeight: weight, width: "50%"}}/>;
      break;

    case '4'://Boolean
      input = <select onChange={e => {variableChange(e.target.value)}} style={{color: color,  fontWeight: weight, width: "50%"}}>
                <option value="true" selected={value ==="true"}>true</option>
                <option value="false" selected={value ==="false"}>false</option>
              </select>;
      break;
  }

  const handleSubmit = (evt) => {
      evt.preventDefault();
      if(!(type === '1' && value.includes(".")))
      {//Ensure Integers (1) do not contain decimal points, must be whole numbers
        update("VAR", {name: name, value: value, type: type});
        setColor("black");
        setWeight("normal");
      }
      else{
        alert(`Warning: ${name} is an Integer. Decimal points are not allowed.`);
      }
  }

  function variableChange(newValue)
  {
    setColor("red");
    setWeight("bold");
    setValue(newValue);
  }


  return (
    <form onSubmit={handleSubmit}>
      {input}
      <input style={{width: "50%", color: color, fontWeight: weight}} type="submit" value="Update" />
    </form>
  );
}


export default Variable_Field