
import React, { useState, useEffect } from 'react';

//Todo update highlighting (mHover) for buttons to also update font color to use --fontColorX
//Todo, accept a max value for pwm - micro:bit uses 1023, arduino uses 255
function Led({x, y, width, pinList, selectedPin, setSelectedPin, serialCom, deviceData, useVerticalLayout}) {
    //Digtal Specific
    const [digitalOutputState, setDigitalOutputState] = useState(0);

    //Pwm specific
    const [pwmVal, setPwmVal] = useState(0);
    const [pwmColor, setPwmColor] = useState("black");
    const [pwmWeight, setPwmWeight] = useState("normal");

    let view_width = 500;//Default width for view box
    useEffect(() =>{//Add modifyier warning when pin changes
        if(selectedPin.output)
        {
            if(deviceData.pwm.hasOwnProperty(selectedPin.pwm))
            {
                setPwmVal(deviceData.pwm[selectedPin.pwm]);
            }
        }
        else
        {
            for(let i = 0; i < pinList.length; i++)
            {//Set to first pin with input
                if(pinList[i].output)
                {
                    setSelectedPin(pinList[i]);
                    break;
                }
            }
        }
      }, [selectedPin]);

    //This assumes that all pins have the ability to send/receive a digital signal
    let pinListOptions = [];
    for(let i = 0; i < pinList.length; i++)
    {
        if(pinList[i].output){
            pinListOptions.push(<option value={`${i}`}>{pinList[i].name}</option>);
        }
    }

    let led_state = <></>;
    if(digitalOutputState == 0)
    {//Low
        led_state = <>
                        <svg width={240} viewBox="110 7.5 240 57.5" fill="none" x={240} y={25} preserveAspectRatio="xMinYMin">
                            <text x={117.5} y={85} fill="black" style={{fontWeight: "bold", fontSize: "5em"}}>OFF</text>
                            <rect fill="black"  x={267.5} y={20} rx={10} ry={10} width ={30} height={75}></rect>
                            <rect fill="white"  x={270} y={65} rx={8} ry={8} width={25} height={25}></rect>
                            <rect fill="transparent" x={267.5} y={20} rx={10} ry={10} width ={30} height={75} style={{cursor:'pointer'}} onClick={() =>  { serialCom("DIGITAL", {pin: selectedPin.digital, value: 1}); setDigitalOutputState(1); setPwmVal(255);}}>
                            <title>Turn on LED</title>
                            </rect>
                        </svg>
                    </>;
    }
    else
    {//High Output
        led_state = <>
                        <svg width={240} viewBox="110 7.5 240 57.5" fill="none" x={240} y={25} preserveAspectRatio="xMinYMin">
                        <text x={117.5} y={85} fill="red" style={{fontWeight: "bold", fontSize: "5em"}}>ON</text>
                            <rect fill="black"  x={267.5} y={20} rx={10} ry={10} width ={30} height={75}></rect>
                            <rect fill="white"  x={270} y={25} rx={8} ry={8} width={25} height={25}></rect>
                            <rect fill="transparent"  x={267.5} y={20} rx={10} ry={10} width ={30} height={75} style={{cursor:'pointer'}} onClick={() => {serialCom("DIGITAL", {pin: selectedPin.digital, value: 0}); setDigitalOutputState(0); setPwmVal(0);}}>
                            <title>Turn off LED</title>
                            </rect>
                        </svg>
                    </>;
    }

    let pwm_display = <> </>;

    if(selectedPin.hasOwnProperty("pwm"))
    {
        view_width = 700;//Increase viewsize to include pwm display.
        pwm_display = <>
            {/* Brightness Level (PWM)*/}
            <rect  x={475} y={25} height={100} width={300} fill="white" stroke="black" strokeWidth="2" />

            {/* Slide Selector */}
            <line x1={500} x2={700} y1={45} y2={45} stroke="black" strokeWidth="4"/>
            <circle cx={500 + 250 * (pwmVal / 255) > 700 ? 700 : 500 + 250 * (pwmVal / 255)} cy={45} fill="white" r={10} stroke="black" strokeWidth="2" />

            {/* PWM/Brigthness Value */}
            <text x={550} y={90} dominantBaseline="middle" textAnchor="middle" fill="black" style={{fontSize:"2em", fontWeight:"bold"}}>Brightness</text>
            <foreignObject x={610} y={65} width={125} height={55}>
                <input type="number" step="1" max={255} min={0} value={pwmVal} onChange={e => {pwmChange(e.target.value)}} style={{color: pwmColor, width:"70%", margin:"0 auto", fontSize: "2.5em", display:"block", fontWeight: pwmWeight}}/>
            </foreignObject>
            
            {/* Update PWM Button */}
            <rect x={725} y={25} width={50} height={100} className="pwm_update_button" fill="white" stroke="black" strokeWidth="2"/>
            <text className="pwm_update_button" x={760} y={65} transform="rotate(-90 760,75)" dominantBaseline="middle" textAnchor="middle" fill="black" style={{fontSize:"1.75em", fontWeight:"bold"}}>Update</text>
            <rect x={725} y={25} width={50} height={100} fill="transparent" style={{cursor:"pointer"}} onMouseEnter={()=>{mHover("pwm_update_button", "fill", "black", "white")}} onMouseLeave={()=>{mHover("pwm_update_button", "fill", "black", "white")}} onClick={()=> {handlePwmUpdate()}} />
        </>;
    }

    function mHover(class_name, attribute, colorA, colorB){
        document.querySelectorAll("." + class_name).forEach(element =>{
                element.setAttribute(attribute, element.getAttribute(attribute) == colorA ? colorB : colorA);
            }
        );
    }

    function handlePwmUpdate(){
        if(pwmVal <= 255 && pwmVal >=0)
        {//Ensure Integers (1) do not contain decimal points, must be whole numbers
          serialCom("PWM", {pin: selectedPin.pwm, value: pwmVal});
          setPwmColor("black");
          setPwmWeight("normal");
          if(pwmVal > 0)
          {
            setDigitalOutputState(1);
          }
          else
          {
            setDigitalOutputState(0);
          }
          
        }
        else{
          alert(`Warning: The pwm value must be between [0-255]`);
        }
    }
  
    function pwmChange(newValue)
    {
      setPwmColor("red");
      setPwmWeight("bold");
      setPwmVal(newValue);
    }

    function changePin()
    {
        var pinSelect = document.getElementById("pinSelecter_HardwarePin");
        var value = pinSelect.options[pinSelect.selectedIndex].value;
        pinSelect.selectedIndex = 0;
        if(value !== "")
        {
            setSelectedPin(pinList[value]);
            serialCom("DIGITAL", {value: 0, pin: pinList[value].digital});//Turn off pin that has been selected
            setDigitalOutputState(0);
        }
    }

  return (
      <>
        <svg 
            width={width} 
            viewBox={`0 0 ${view_width} 150`}
            x={x}
            y={y}
            preserveAspectRatio="xMinYMin"
        > 
        {/* Logo  
        {displayMode !== "Basic" ? <> </> : 
            <>
                <line x1={100} x2={100} y1={25} y2={125} stroke="white" strokeWidth="4" />
                <circle cx={50} cy={60} r={15} fill="white" />
                <text x={50} y={115} dominantBaseline="middle" textAnchor="middle" fill="white" style={{fontSize:"2.5em", fontWeight:"bolder"}}>LED</text>
            </>}*/}
        {/* Display/Select Pin*/}
        <g transform= {useVerticalLayout ? "translate(-100 15) scale(0.85)" :"translate(-100)"}>
            <g transform={useVerticalLayout ? "translate(-50 25) scale(1.6)" : ""}>
                <rect x={125} y={25} height={65} width={75} fill="white" stroke="black" strokeWidth="2" />
                <text x={162.5} y={62.5} dominantBaseline="middle" textAnchor="middle" fill="black" style={{fontSize:"3em", fontWeight:"bold"}}>{selectedPin.name}</text>
                <rect className="pin_select" x={125} y={90} height={35} width={75} fill="white" stroke="black" strokeWidth="2" />
                <foreignObject x={127.5} y={95} height={35} width={70.5}> 
                    <select id="pinSelecter_HardwarePin" style={{width:"100%", fontSize:"1.15em", borderColor:"white"}} onChange={()=>changePin()}>
                        <option value="" selected>Select</option>
                        {pinListOptions}
                    </select>
                </foreignObject>
            
                {/* LED output */}
                <rect  x={225} y={25} height={100} width={235} fill="white" stroke="black" strokeWidth="2" />
                {led_state}
            </g>
            <g transform={useVerticalLayout ? "translate(-450 225) scale(1.6)" : ""}>
                {pwm_display}
            </g>
        </g>
        </svg> 
     </>

    );
}

export default Led