import React, {} from "react";
import LedArray from "../../PinControls/Actuators/LedArray.js";

function Board_Overlay({serialCom, deviceData, x, y, width, height, selectedPin, setSelectedPin, selectedSensor}) {
    let pinPointer = <> </>;
    let pinColors = {P0: "white", P1: "white", P2: "white", P5: "white", P8: "#d4af1c", P11: "white", P13: "#d4af1c", P14: "#d4af1c", P15: "#d4af1c", P16: "#d4af1c", P19: "#d4af1c", P20: "#d4af1c"}
    if(selectedSensor === "Pins")
    {//5 8 16 11 20 19 15 14 13
      switch(selectedPin.digital)
      {
          case "P0": //Fill color of micro:bit pins: #d4af1c
            pinPointer = <polygon points="18.25,115 28.25,115 23.25,125" fill="red" stroke="white" strokeWidth="0.5"/>
            pinColors["P0"] = "red";
            break;

          case "P1": 
            pinPointer = <polygon points="56.25,115 66.25,115 61.25,125" fill="red" stroke="white" strokeWidth="0.5"/>
            pinColors["P1"] = "red";
            break;

          case "P2": 
            pinPointer = <polygon points="98.25,115 108.25,115 103.25,125" fill="red" stroke="white" strokeWidth="0.5"/>
            pinColors["P2"] = "red";
            break;

          case "P5": 
            pinPointer = <polygon points="37.5,128 42.5,128 40,138" fill="red" stroke="white" strokeWidth="0.5"/>
            pinColors["P5"] = "red";
            break;

          case "P8": 
            pinPointer = <polygon points="70.75,128 75.75,128 72.75,138" fill="red" stroke="white" strokeWidth="0.5"/>
            pinColors["P8"] = "red";
            break;

          case "P11": 
            pinPointer = <polygon points="84.5,128 89.5,128 87,138" fill="red" stroke="white" strokeWidth="0.5"/>
            pinColors["P11"] = "red";
            break;

          case "P13": 
            pinPointer = <polygon points="112.5,128 117.5,128 115,138" fill="red" stroke="white" strokeWidth="0.5"/>
            pinColors["P13"] = "red";
            break;

          case "P14": 
            pinPointer = <polygon points="117.5,128 122.5,128 120,138" fill="red" stroke="white" strokeWidth="0.5"/>
            pinColors["P14"] = "red";
            break;
          
          case "P15": 
            pinPointer = <polygon points="123,128 127,128 125,138" fill="red" stroke="white" strokeWidth="0.5"/>
            pinColors["P15"] = "red";
            break;

          case "P16": 
            pinPointer = <polygon points="126.5,128 131.5,128 129,138" fill="red" stroke="white" strokeWidth="0.5"/>
            pinColors["P16"] = "red";
            break;

          case "P19": 
            pinPointer = <polygon points="159.75,128 164.75,128 162.25,138" fill="red" stroke="white" strokeWidth="0.5"/>
            pinColors["P19"] = "red";
            break;

          case "P20": 
            pinPointer = <polygon points="169.5,128 164.5,128 167,138" fill="red" stroke="white" strokeWidth="0.5"/>
            pinColors["P20"] = "red";
            break;
      }
    }
      return (
        <>
          <svg
            viewBox="0 0 204.058 205.025"
            height={height}
            width={width}
            x={x}
            y={y}
            >
            
            {/* Ensure the Pin/Sensor Selectors are placed on top of the microcontroller */}
                {/*Pin Holes */}
                <circle className="clickable_black clickable_1_5" style={{cursor:"pointer"}} cx={23.25} cy={133} onClick={()=>setSelectedPin({name: "P0", digital: "P0", analog: "P0", pwm: "P0", output:true, input:true})} r={7.5} fill={pinColors["P0"]}>
                  <title>Pin 0</title>
                </circle>
                <circle className="clickable_black clickable_1_5" style={{cursor:"pointer"}} cx={61.25} cy={133} onClick={()=>setSelectedPin({name: "P1", digital: "P1", analog: "P1", pwm: "P1", output:true, input:true})} r={7.5} fill={pinColors["P1"]}>
                  <title>Pin 1</title>
                </circle>
                <circle className="clickable_black clickable_1_5" style={{cursor:"pointer"}} cx={103.25} cy={133} onClick={()=>setSelectedPin({name: "P2", digital: "P2", analog: "P2", pwm: "P2", output:true, input:true})} r={7.5} fill={pinColors["P2"]}>
                  <title>Pin 2</title>
                </circle>
                
                {/* Pin Edge Connector */}
                {/*<path d="M7.75 138 H13.5 V155.5 Q 8 155, 7.75 138 Z" stroke={pinColors["P3"]} fill={pinColors["P3"]} style={{cursor:"pointer"}} onClick= />*/}
                <rect x={38} y={138.5} width={4} height={18.75} style={{cursor:"pointer"}} onClick={()=>setSelectedPin({name: "P5", digital: "P5", output:false, input:true})} fill={pinColors["P5"] === "white" ? "#d4af1c" : pinColors["P5"]}>
                  <title>P5 (Button A Pin)</title>
                </rect>

                <rect x={71} y={138.5} width={4} height={18.75} style={{cursor:"pointer"}} onClick={()=>setSelectedPin({name: "P8", digital: "P8", output:true, input:true})} fill={pinColors["P8"]}>
                  <title>P8</title>
                </rect>

                <rect x={85} y={138.5} width={4} height={18.75} style={{cursor:"pointer"}} onClick={()=>setSelectedPin({name: "P11", digital: "P11", output:false, input:true})} fill={pinColors["P11"] === "white" ? "#d4af1c" : pinColors["P11"]}>
                  <title>P11 (Button B Pin)</title>
                </rect>

                <rect x={113} y={138.5} width={4} height={18.75} style={{cursor:"pointer"}} onClick={()=>setSelectedPin({name: "P13", digital: "P13", output:true, input:true})} fill={pinColors["P13"]}>
                  <title>P13</title>
                </rect>

                <rect x={118} y={138.5} width={4} height={18.75} style={{cursor:"pointer"}} onClick={()=>setSelectedPin({name: "P14", digital: "P14", output:true, input:true})} fill={pinColors["P14"]}>
                  <title>P14</title>
                </rect>

                <rect x={122.5} y={138.5} width={4} height={18.75} style={{cursor:"pointer"}} onClick={()=>setSelectedPin({name: "P15", digital: "P15", output:true, input:true})} fill={pinColors["P15"]}>
                  <title>P15</title>
                </rect>

                <rect x={127} y={138.5} width={4} height={18.75} style={{cursor:"pointer"}} onClick={()=>setSelectedPin({name: "P16", digital: "P16", output:true, input:true})} fill={pinColors["P16"]}>
                  <title>P16</title>
                </rect>

                <rect x={160.25} y={138.5} width={4} height={18.75} style={{cursor:"pointer"}} onClick={()=>setSelectedPin({name: "P19", digital: "P19", output:true, input:true})} fill={pinColors["P19"]}>
                  <title>P19</title>
                </rect>

                <rect x={165} y={138.5} width={4} height={18.75} style={{cursor:"pointer"}} onClick={()=>setSelectedPin({name: "P20", digital: "P20", output:true, input:true})} fill={pinColors["P20"]}>
                  <title>P20</title>
                </rect>

                {/* Button A*/}
                <circle className="clickable_black clickable_1_5" style={{cursor:"pointer"}} cx={29} cy={82} onClick={()=>setSelectedPin({name: "P5", digital: "P5", output:false, input:true})} r={7.5} fill={pinColors["P5"]}>
                  <title>Button A</title>
                </circle>

                {/* Button B*/}
                <circle className="clickable_black clickable_1_5" style={{cursor:"pointer"}} cx={180} cy={82} onClick={()=>setSelectedPin({name: "P11", digital: "P11", output:false, input:true})} r={7.5} fill={pinColors["P11"]}>
                  <title>Button B</title>
                </circle>

                {/* LED Array */}
                <LedArray x={68} y={30} trigger={serialCom} led_settings={deviceData.led_array}/>

                {pinPointer}
            </svg>
         </>
            
    )
}

export default Board_Overlay