import React, {useState} from "react";
import share_project from "../../../images/share_project.gif"

//Todo add mHover option to the makecode and text based icons 
function Code_Updater({updateCode, x, y, height}) {    
    const [codeType, setCodeType] = useState("block");
    let selection =<>
        {/* Placeholder, Todo: add in a gif that shows how to retrieve a shareable code link*/} 
        <foreignObject x={10} y={65} width={200} height={150}>
            <img src={share_project} width="150%" style={{marginLeft: "-25%"}} alt="Animation demonstrating how to get the shared project link on MakeCode" />
        </foreignObject>
        <rect x={10} y={65} rx={5} ry={5} width={200} height={150} fill="transparent" stroke="white" strokeWidth="5"/>
        <rect x={10} y={65} rx={5} ry={5} width={200} height={150} fill="transparent" stroke="black" strokeWidth="1.5"/>
        <rect x={225} y={65} rx={5} ry={5} width={200} height={150} fill="white" stroke="black" strokeWidth="1.5"/>
        <text x={325} y={105} fill="black" textAnchor="middle" style={{fontSize:"1.5em", fontWeight:"bold"}}>
            MakeCode
        </text>
        <text x={325} y={130} fill="black" textAnchor="middle" style={{fontSize:"1.5em", fontWeight:"bold"}}>
            Project URL:
        </text>
        <foreignObject x={237.5} y={140} width={175} height={100}>
            <input type="text" id="makeCode_Share_URL" placeholder="https://makecode.microbit.org/_projectid" style={{width: "100%", fontSize:"0.55em"}}></input>
            <button type="button" style={{width: "100%", marginTop:"1%"}} onClick={() => {updateCode({type: "block", code: document.getElementById("makeCode_Share_URL").value})}}> Update</button>
        </foreignObject>
    </>;

    if(codeType === "text")
    {
        selection =<>
                        <foreignObject x={35} y={65} width={375} height={175}>
                            <textarea rows="12" id="text_code_update" style={{width: "100%", fontSize: "0.5em"}} placeholder="Copy + Paste your code here"></textarea>
                            <label for="select_language" style={{width:"50%", fontSize:"1em", fontWeight:"bold", textAlign:"right", paddingRight: "10px"}}>Programming Language: </label>
                            <select id="select_language" style={{width:"50%", fontSize:"1em", fontWeight:"bold", border: "solid 2px black"}} >
                                    <option value="arduino">Arduino</option>
                                    <option value="javascript">Javascript</option>
                                    <option value="python">Python</option>
                            </select> 
                            <button type="button" style={{width: "100%", marginTop:"1%"}} onClick={() => {updateCode({type: "text", language: document.getElementById("select_language").value, code: document.getElementById("text_code_update").value})}}> Update</button>
                        </foreignObject>
                    </>;
    }



    //Stylize controls on hover
    function mHover(chosen_class){
        document.querySelectorAll("." + chosen_class).forEach(element =>{
                element.setAttribute("fill", element.getAttribute("fill") === "white" ? "black" : "white");
            }
        );
        document.getElementById(chosen_class + "_line").setAttribute("stroke",  document.getElementById(chosen_class + "_line").getAttribute("stroke") === "white" ? "black" : "white");
    }
    
    return (
    <>
        <svg
        viewBox={`0 0 437.5 250`}
        height={height}
        x={x}
        y={y}
        preserveAspectRatio="xMinYMin"
        >
            {/* Makecode Selector*/}
            <rect className="block_code_selector" x={110} y={10} rx={5} ry={5} width={100} height={40} fill="white" stroke="black" strokeWidth="1.5"/>
            <svg x={115} y={14} height={32} viewBox="0 0 32 32" preserveAspectRatio="xMinYMin">
                <path className="block_code_selector" fill="black" d="M32 15.995v-.011c0-.108-.005-.209-.016-.303h-.001c-.032-.285-.112-.504-.239-.656-.171-.204-.459-.435-.864-.691a4.275 4.275 0 01-.986-.827c-.252-.296-.442-.734-.57-1.316-.049-.227-.087-.719-.112-1.473V8.697c0-.38-.031-.723-.076-1.049a12.313 12.313 0 00-.004-.188c-.009-.782-.18-1.475-.513-2.078a3.462 3.462 0 00-1.416-1.401c-.611-.33-1.305-.495-2.081-.495H6.271s-.674.067-.962.201a4.021 4.021 0 00-.521.295 3.502 3.502 0 00-.798.59c-.693.688-1.061 1.601-1.113 2.734a9.172 9.172 0 00-.088 1.283v2.558a6.802 6.802 0 01-.183 1.373 2.311 2.311 0 01-.532 1.033 2.67 2.67 0 00-.202.196c-.265.238-.638.511-1.13.823-.404.262-.64.633-.715 1.108l-.002.023a2.195 2.195 0 00-.023.287L0 16.007v.011c0 .108.005.209.016.303h.001c.032.285.112.504.239.656.171.204.459.435.864.691.406.256.735.532.986.827.252.296.442.734.57 1.316.049.227.087.719.112 1.473v2.019c0 .38.031.723.076 1.049l.004.188c.009.782.18 1.475.513 2.078a3.462 3.462 0 001.416 1.401c.611.33 1.305.495 2.081.495H25.73c.35 0 .673-.067.961-.201.184-.085.356-.187.521-.296.291-.159.559-.353.798-.59.693-.688 1.061-1.601 1.113-2.734.056-.398.088-.823.088-1.283v-2.558c.019-.495.079-.953.183-1.373.104-.419.282-.762.532-1.033a2.67 2.67 0 00.202-.196c.265-.238.638-.511 1.13-.823.404-.262.64-.633.715-1.108h.002l.002-.023c.013-.092.022-.187.023-.287v-.014z"/>
                <path className="block_code_selector" fill="white" d="M27.366 16.522l.819-.518-.821-.516a4.547 4.547 0 01-.927-.744 3.033 3.033 0 01-.554-.896 5.166 5.166 0 01-.328-1.339 16.319 16.319 0 01-.103-1.961c0-.781-.016-1.365-.05-1.786-.039-.489-.123-.852-.258-1.112a1.36 1.36 0 00-.718-.661c-.223-.089-.527-.154-.924-.199a.532.532 0 01-.356-.213c-.11-.133-.165-.344-.165-.627 0-.41.086-.811 1.121-.811.636 0 1.196.127 1.666.376.464.247.818.589 1.085 1.046.267.458.406.995.413 1.597.064 3.554.129 4.265.184 4.492.153.63.382 1.109.701 1.464.287.319.635.602 1.035.843.29.175.501.341.627.493.035.043.116.181.116.554 0 .383-.12.628-.39.794-.606.373-1.041.699-1.331.997a2.734 2.734 0 00-.678 1.24c-.112.437-.177 1.05-.198 1.875-.02.773-.042 1.741-.066 2.904-.021.941-.306 1.668-.873 2.222-.568.554-1.317.823-2.291.823-.409 0-.71-.077-.895-.23-.155-.128-.226-.32-.226-.604 0-.179.03-.336.093-.479a.682.682 0 01.188-.268.463.463 0 01.24-.094c.414-.055.725-.127.951-.219.317-.131.567-.378.723-.716.124-.269.2-.625.233-1.089.029-.402.044-.968.044-1.725.016-1.061.077-1.896.182-2.481.095-.53.274-.987.533-1.359.261-.375.665-.736 1.198-1.073zm-16.618 6.519h10.458a.75.75 0 00.751-.751l-.001-3.709c-.134-.449-.518-.492-.878-.15-.033 0-.393.443-1.22.443a2.033 2.033 0 01-1.944-1.715h-.011a6.59 6.59 0 010-.526h.011a2.03 2.03 0 011.944-1.715c.827 0 1.187.443 1.22.443.35.332.738.252.879-.147l.001-3.432a.769.769 0 00-.562-.741c-.061.016-3.259 0-3.259 0-.786.065-1.063-.466-.624-.928 0-.033.443-.393.443-1.22a2.033 2.033 0 00-1.715-1.944v-.011a6.59 6.59 0 00-.526 0v.011A2.03 2.03 0 0014 8.893c0 .827.443 1.187.443 1.22.439.462.254.97-.624.928 0 0-3.198.016-3.259 0a.77.77 0 00-.562.741V22.29a.75.75 0 00.75.751zm-6.197-3.692c.055.227.12.938.184 4.492.007.602.146 1.138.413 1.597.267.458.622.8 1.085 1.046.47.25 1.03.376 1.666.376 1.035 0 1.121-.4 1.121-.811 0-.283-.056-.493-.165-.627a.537.537 0 00-.356-.213c-.397-.045-.701-.11-.925-.199a1.367 1.367 0 01-.718-.661c-.135-.259-.219-.623-.258-1.112a24.159 24.159 0 01-.05-1.786c0-.751-.035-1.41-.103-1.961a5.174 5.174 0 00-.329-1.339 3.028 3.028 0 00-.554-.896 4.52 4.52 0 00-.927-.744l-.821-.516.819-.518c.533-.337.936-.698 1.199-1.075.259-.372.438-.829.533-1.359.105-.585.166-1.419.182-2.481 0-.757.014-1.323.044-1.725.033-.464.109-.82.233-1.089.156-.337.406-.584.723-.715.226-.092.537-.163.951-.219a.463.463 0 00.24-.094.662.662 0 00.189-.267 1.18 1.18 0 00.093-.479c0-.284-.072-.476-.226-.604-.186-.153-.487-.23-.895-.23-.974 0-1.723.269-2.291.823-.567.554-.852 1.281-.873 2.222a588.806 588.806 0 01-.065 2.904c-.021.825-.086 1.438-.198 1.875a2.731 2.731 0 01-.679 1.24c-.289.298-.725.624-1.331.997-.27.166-.39.41-.39.794 0 .373.081.511.116.554.127.153.338.319.627.493.4.24.748.524 1.035.843.319.355.548.834.701 1.464z"/>
            </svg>
            <line id="block_code_selector_line" x1={152} x2={152} y1={10} y2={50} stroke="black" strokeWidth="1.5" />
            <text className="block_code_selector" x={160} y={27.5} fill="black" style={{fontSize:"1em", fontWeight:"bold"}}>
                Block 
            </text>
            <text className="block_code_selector" x={160} y={42.5} fill="black" style={{fontSize:"1em", fontWeight:"bold"}}>
                Code 
            </text>
            <rect x={110} y={10} rx={5} ry={5} width={100} height={40} fill="transparent" style={{cursor:"pointer"}} onClick={()=>{setCodeType("block")}} onMouseEnter={() => mHover("block_code_selector")}  onMouseLeave={() => mHover("block_code_selector")}/>


            {/* Textbased code selector */}
            <rect className="text_code_selector" x={225} y={10} rx={5} ry={5} width={100} height={40} fill="white" stroke="black" strokeWidth="1.5"/>
            <text className="text_code_selector" x={229} y={37.5} fill="black" style={{fontSize:"1.5em", fontWeight:"bold"}}>
                {"</>"}
            </text>
            <line id="text_code_selector_line" x1={267} x2={267} y1={10} y2={50} stroke="black" strokeWidth="1.5" />
            <text className="text_code_selector" x={275} y={27.5} fill="black" style={{fontSize:"1em", fontWeight:"bold"}}>
                Text 
            </text>
            <text className="text_code_selector" x={275} y={42.5} fill="black" style={{fontSize:"1em", fontWeight:"bold"}}>
                Code 
            </text>
            <rect x={225} y={10} rx={5} ry={5} width={100} height={40} fill="transparent" style={{cursor:"pointer"}} onClick={()=>{setCodeType("text")}} onMouseEnter={() => mHover("text_code_selector")}  onMouseLeave={() => mHover("text_code_selector")}/>
            {selection}
        </svg>
    </>
            
    )
}

export default Code_Updater