import React, {} from "react";

function Code_Selector({showCodeSelector, x, y, height}) {

    //Stylize controls on hover
    function mHover(){
        document.querySelectorAll(".code_selector").forEach(element =>{
                element.setAttribute("fill", element.getAttribute("fill") == "white" ? "black" : "white");
            }
        );
        document.querySelectorAll(".code_selector_line").forEach(element =>{
                element.setAttribute("stroke", element.getAttribute("stroke") == "white" ? "black" : "white");
            }
        );
    }

      return (
        <>
          <svg
            viewBox={`0 0 230 50`}
            height={height}
            x={x}
            y={y}
            preserveAspectRatio="xMinYMin"
            >
                {/* Background */}
                <rect class="code_selector" x={85} y={5} rx="5" ry="5" width={140} height={40} fill="white" />
                <line class="code_selector_line" x1={130} x2={105} y1={35} y2={35} stroke="black" strokeWidth="4" strokeLinecap="round"/>
                <line class="code_selector_line" x1={130} x2={130} y1={35} y2={25} stroke="black" strokeWidth="4" strokeLinecap="round" />
                <line class="code_selector_line" x1={105} x2={105} y1={35} y2={25} stroke="black" strokeWidth="4" strokeLinecap="round" />
                <line class="code_selector_line" x1={117.5} x2={117.5} y1={27.5} y2={15} stroke="black" strokeWidth="4" strokeLinecap="round" />
                <line class="code_selector_line" x1={112.5} x2={117.5} y1={22.5} y2={15} stroke="black" strokeWidth="4" strokeLinecap="round" />
                <line class="code_selector_line" x1={122.5} x2={117.5} y1={22.5} y2={15} stroke="black" strokeWidth="4" strokeLinecap="round" />
                <text class="code_selector" x={175} y={27.5} dominantBaseline="middle" textAnchor="middle" fill="black" style={{fontWeight:"bolder", fontSize:"2em"}}>
                    Code
                </text>
                {/* Transparent Overlays */}
                <rect x={85} y={5} rx="5" ry="5" width={140} height={40} style={{cursor:"pointer"}} fill="transparent" stroke="black" strokeWidth="2.5" onMouseEnter={()=>mHover()} onMouseLeave={()=>mHover()} onClick={()=>{showCodeSelector()}}>
                    <title>Upload Code</title>
                </rect>
            </svg>
         </>
            
    )
}

export default Code_Selector