
import React, { useState, useEffect } from 'react';
import SmoothLineGraph from "../GraphingUtilities/SmoothLineGraph.js";

function Capacitive({x, y, width, font_size="2.25em", pinList, selectedPin, setSelectedPin, serialCom, deviceData, useVerticalLayout}) {
    const [maxVal, setMaxVal] = useState(null);
    const [minVal, setMinVal] = useState(null);

    useEffect(()=>{
        serialCom("Capacitive", { pin: selectedPin.digital});
        setMaxVal(Math.max(...deviceData.capacitive[selectedPin.digital]));
        setMinVal(Math.min(...deviceData.capacitive[selectedPin.digital]));
    }, [ selectedPin]);

    let pinListOptions = [];
    for(let i = 0; i < pinList.length; i++)
    {
        if(pinList[i].capacitive){
            pinListOptions.push(<option value={`${i}`}>{pinList[i].name}</option>);
        }
    }

    let graph = <></>;
    if(deviceData.capacitive[selectedPin.digital].length > 0)
    {
        if(maxVal === null){
            setMaxVal(deviceData.capacitive[selectedPin.digital][0])
        }
        else if(deviceData.capacitive[selectedPin.digital][deviceData.capacitive[selectedPin.digital].length - 1] > maxVal)
        {
            setMaxVal(deviceData.capacitive[selectedPin.digital][deviceData.capacitive[selectedPin.digital].length - 1]);
        }
        if(minVal === null){
            setMinVal(deviceData.capacitive[selectedPin.digital][0])
        }
        else if(deviceData.capacitive[selectedPin.digital][deviceData.capacitive[selectedPin.digital].length - 1] < minVal)
        {
            setMinVal(deviceData.capacitive[selectedPin.digital][deviceData.capacitive[selectedPin.digital].length - 1]);
        }
        graph = <SmoothLineGraph x={300} y={25.5} height={99} width={275} x_axis_offset={10} y_axis_offset={12.5} data={deviceData.capacitive[selectedPin.digital].slice(-25)} data_max={maxVal} data_min={minVal} reset_min_max={reset_min_max}/>
    }

    function mHover(class_name, attribute, colorA, colorB){
        document.querySelectorAll("." + class_name).forEach(element =>{
                element.setAttribute(attribute, element.getAttribute(attribute) == colorA ? colorB : colorA);
            }
        );
    }

    function changePin()
    {
        var pinSelect = document.getElementById("pinSelecter_HardwarePin");
        var value = pinSelect.options[pinSelect.selectedIndex].value;
        pinSelect.selectedIndex = 0;
        if(value !== "")
        {
            setSelectedPin(pinList[value]);
            serialCom("Capacitive", { pin: pinList[value].digital});
            //serialCom("PIN_MODE", {mode: 0, pin: pinList[value].digital});
            //serialCom("PIN_READ", {signalType: 0, pin: pinList[value].digital});
        }
    }
    function reset_min_max(){
        setMaxVal(deviceData.capacitive[selectedPin.digital].length > 0 ? Math.max(...deviceData.capacitive[selectedPin.digital].slice(-25)) : 0);
        setMinVal(deviceData.capacitive[selectedPin.digital].length > 0 ? Math.min(...deviceData.capacitive[selectedPin.digital].slice(-25)) : 0);
    }

  return (
      <>
        <svg 
            width={width} 
            viewBox={ "0 0 500 150"}
            x={x}
            y={y}
            preserveAspectRatio="xMinYMin"
        >   
        
        {/* Logo 
        {logo}
        <line x1={110} x2={110} y1={25} y2={125} stroke="white" strokeWidth="4" />
        <text x={52.5} y={112.5} dominantBaseline="middle" textAnchor="middle" fill="white" style={{fontSize:font_size, fontWeight:"bolder"}}>{sensor_name}</text>
*/}  
        <g transform ={useVerticalLayout ? "scale(0.9) translate(30 -15)": ""}>
            <g transform ={useVerticalLayout ? "translate(-50 0) scale(1.5)": "translate(-55 30) scale(0.5)"}>
                {/* Display/Select Pin*/}
                <rect x={125} y={25} height={65} width={75} fill="white" stroke="black" strokeWidth="2" />
                <text x={162.5} y={62.5} dominantBaseline="middle" textAnchor="middle" fill="black" style={{fontSize:"3em", fontWeight:"bold"}}>{selectedPin.name}</text>
                <rect className="pin_select" x={125} y={90} height={35} width={75} fill="white" stroke="black" strokeWidth="2" />
                <foreignObject x={127.5} y={95} height={35} width={70.5}>
                    <select id="pinSelecter_HardwarePin" style={{width:"100%", fontSize:"1.15em", borderColor:"white"}} onChange={()=>changePin()}>
                        <option value="" selected>Select</option>
                        {pinListOptions}
                    </select>
                </foreignObject>
            </g>
            <g transform ={useVerticalLayout ? "translate(70 0) scale(1.5)": "translate(-35 25) scale(0.7)"}>
                Current Value
                <rect x={125} y={25} height={65} width={100} fill="white" stroke="black" strokeWidth="2" />
                                                                                                            {/* This code dynamically adjusts the font size so ensure that the text doesn't run outside of the designated box. 10 was chosen because it looked nice upon testing, no real significance */}
                <text x={175} y={62.5} dominantBaseline="middle" textAnchor="middle" fill="black" style={{fontSize: "2em", fontWeight:"bold"}}>{deviceData.capacitive[selectedPin.digital].length > 0 ? deviceData.capacitive[selectedPin.digital][deviceData.capacitive[selectedPin.digital].length-1] !== undefined ? deviceData.capacitive[selectedPin.digital][deviceData.capacitive[selectedPin.digital].length-1] : 0 : "NA"}</text>
                <rect x={125} y={90} height={35} width={100} fill="white" stroke="black" strokeWidth="2" />
                <text  x={175} y={110} dominantBaseline="middle" textAnchor="middle" fill={"black"}  style={{fontSize:"1.5em", fontWeight:"bold"}}>Value</text>
            </g>
            <g transform ={useVerticalLayout ? "translate(-250 165) scale(1.25)": "translate(-100)"}>
                {/*Data graph*/}
                <rect  x={230} y={25} height={100} width={355}  fill="white" stroke="black" strokeWidth="2" />
                {graph}
            </g>
          </g>
        </svg> 
     </>

    );
}

export default Capacitive