import React, { useState, Suspense} from 'react';
import './index.css';
import Header from "./Debugger_Components/Layouts/Header.js";
import { TextEncoderStream, TextDecoderStream } from "@stardazed/streams-text-encoding";
import Controller from './Debugger_Components/Controller.js';
import useWindowDimensions from './Debugger_Components/Layouts/WindowDimensions.js';


function App(props){
  const [board, setBoard] = useState("PlaygroundClassic");
  const [displayHelper, setDisplayHelper] = useState(false);
  const [displayCode, setDisplayCode] = useState(false);
  const [port, setPort] = useState("");
  const [writer, setWriter] = useState(null);
  const [writableStreamClosed , setWritableStreamClosed] = useState(null);
  const [reader, setReader] = useState(null);
  const [readableStreamClosed , setReadableStreamClosed] = useState(null);
  const { height, width } = useWindowDimensions();
  /****************************************************
   *  Using the Google Serial Api, this method will open a port request window in the Chrome Browser and allow the user to select a Serial Device connected to their computer.
   *  This method is the main point for updating/setting the port state, serial writer, and serial reader.
   * 
   */
  
  //Set event listener, if the port is disconnected, will need to reset port
  navigator.serial.addEventListener("disconnect", (event) => {
    setPort("");
    console.log("Port Disconnected");
  });

  async function connect(){
    if ('serial' in navigator) {
      try {
          const port = await navigator.serial.requestPort();
          await port.open({ baudRate: 57600, bufferSize: 5000 });//Higher baudrade of 115200 causes problems with variable transmissions. Too many characters/data are lost
          
          //Prepare Serial REader
          const textDecoder = new TextDecoderStream();
          setReadableStreamClosed(port.readable.pipeTo(textDecoder.writable));
          setReader(textDecoder.readable.getReader());
          
          //Prepare Serial Writer
          const textEncoder = new TextEncoderStream();
          setWritableStreamClosed(textEncoder.readable.pipeTo(port.writable));
          setWriter(textEncoder.writable.getWriter());
          setPort(port);     
          let signals = await port.getSignals();
      }
      catch (err) {
          console.error('There was an error opening the serial port:', err);
      }
    }
    else {
        console.error('Web serial doesn\'t seem to be enabled in your browser. Try enabling it by visiting:');
        console.error('chrome://flags/#enable-experimental-web-platform-features');
        console.error('opera://flags/#enable-experimental-web-platform-features');
        console.error('edge://flags/#enable-experimental-web-platform-features');
    }
  } /* End connect() */

  /**
   * This method will clean up a Serial Port connection by closing the writer, reader, and port connection. This is necessary if a student needs to switch
   * from the Live Debugger to a different IDE and have the second IDE connect to the Serial Device. 
   */
  async function disconnect(){
    try{
      if(port !== "" && writer !== null)
      {
        writer.close();
        await writableStreamClosed;
        reader.cancel();
        await readableStreamClosed.catch(() => { /* Ignore the error Issue with Chrome Library?*/ });
        await port.close();
        setPort("");
        setWriter(null);
        setReader(null);
      }
    }
    catch(e)
    {
      console.log("Unable to disconnect. Resetting connection variables to null");
      setPort("");
      setWriter(null);
      setReader(null);
    }
  }/* End disconnect() */

  /**
   *  This method will reset the reader/writer using the selected port. This is necessary if the board is changed AFTER the port has been selected.
   * 
   */
  async function reset()
  {
    if(port)
    {
       try{
         //Close writer
          writer.close();
          await writableStreamClosed;

          //Create new writer
          const textEncoder = new TextEncoderStream();
          setWritableStreamClosed(textEncoder.readable.pipeTo(port.writable));
          setWriter(textEncoder.writable.getWriter());

          //Close reader
          reader.cancel();
          await readableStreamClosed.catch(() => { /* Ignore the error Issue with Chrome Library?*/ });

          //Create new reader
          const textDecoder = new TextDecoderStream();
          setReadableStreamClosed(port.readable.pipeTo(textDecoder.writable));
          setReader(textDecoder.readable.getReader());
          
       }catch(e)
       {
         console.log("Error resetting reader & writer.");
         console.log(e);
       }
    }
    else{
      console.log("Cannot create a new reader or writer. Port has not been set");
    }
  }

  //Verify the device hasn't been disconnected
 /* if(port !== "" && writer !== null)
  {
        writer.write("").catch(() =>{
          setPort("");
          setWriter(null);
          setReader(null);
          setWritableStreamClosed(null);
          setReadableStreamClosed(null);
          console.log("Error accessing device, port forceably closed and must be reconnnected.");
      }
    );
  }*/

  return(
    <>
        <svg 
            width={"99%"} 
            viewBox={`0 0 ${width} ${height}`}
            style={{marginLeft:"0.5%"}}
        >
          <Controller board={board} reader={reader} writer={writer} port={port} reset={reset} displayCode={displayCode} displayHelper={displayHelper}/>
          <Header connect={connect} disconnect={disconnect} reset={reset} connectionStatus={eval(port !== "")} selectBoard={setBoard} board={board} displayCode={displayCode} setDisplayCode={setDisplayCode} displayHelper={displayHelper} setDisplayHelper={setDisplayHelper} useVerticalLayout={width < 1200}/> 
        </svg>
    </>
  );
}

export default App;