
import React from 'react';

//Todo implement download feature for Gestures
function Gesture({ x, y, width, gesture, useVerticalLayout}) {

    return (
      <>
        <svg 
            width={width} 
            viewBox={ "0 0 500 150"}
            x={x}
            y={y}
            preserveAspectRatio="xMinYMin"
        >   
            <g transform={useVerticalLayout ? "translate(110 30) scale(3)" : "translate(175 12.5) scale(1.5)"}>
                <rect  x={0} y={0} height={100} width={97} fill="white" stroke="black" strokeWidth="2" />
                <text x={48.5} y={45} dominantBaseline="middle" textAnchor="middle" fill={ "black"} style={{fontSize:"1.5em", fontWeight:"bold"}}>{gesture !== undefined && gesture !== "" ? gesture.substr(0, gesture.indexOf(" ")) : ""}</text>
                <text x={48.5} y={65} dominantBaseline="middle" textAnchor="middle" fill={ "black"} style={{fontSize:"1.5em", fontWeight:"bold"}}>{gesture !== undefined && gesture !== "" ? gesture.substr(gesture.indexOf(" ") + 1) : ""}</text>
            </g>
        </svg> 
     </>

    );
}

export default Gesture