
import React, { useState, useEffect } from 'react';

function NeoPixel({x, y, width, deviceData, serialCom, displayMode, numPixels, useVerticalLayout}) {
    const [red, setRed] = useState(0);
    const [green, setGreen] = useState(0);
    const [blue, setBlue] = useState(0);
    const [pixel, setPixel] = useState(0);

    let pixelListOptions = [];
    for(let i = 0; i < numPixels; i++)
    {
      pixelListOptions.push(<option value={`${i}`}>{`Pixel #${i}`}</option>)
    }

    function changePixel()
    {
        var pixelSelect = document.getElementById("neoPixel_Selector");
        var value = pixelSelect.options[pixelSelect.selectedIndex].value;
        pixelSelect.selectedIndex = 0;
        if(value !== "")
        {
            setPixel(value);
        }
    }

    function handleColorUpdate(){
      //Ensure rgb values are within expected range of 0-255
      if(red > 255 || red <0)
      {
        alert(`Warning: The red value must be between [0-255]`);
      }
      else if(green > 255 || green <0)
      {
        alert(`Warning: The green value must be between [0-255]`);
      }
      else if(blue > 255 || blue <0)
      {
        alert(`Warning: The blue value must be between [0-255]`);
      }
      else
      {
        serialCom("Neopixel", {pixel: pixel, red: red, green: green, blue: blue});
        document.getElementById(`border_${pixel}`).setAttribute('fill', `rgb(${red},${green},${blue})`)
      }
  }

    function mHover(class_name, attribute, colorA, colorB){
        document.querySelectorAll("." + class_name).forEach(element =>{
                element.setAttribute(attribute, element.getAttribute(attribute) == colorA ? colorB : colorA);
            }
        );
    }

  return (
      <>
        <svg 
            width={width} 
            viewBox={ "0 0 500 300"}
            x={x}
            y={y}
            preserveAspectRatio="xMinYMin"
        >   
        
        {/* Logo  
        {displayMode === "Basic" ? <>
        <line x1={100} x2={100} y1={25} y2={125} stroke="white" strokeWidth="4" />
        <svg viewBox="0 0 50 50" x={25} y={30 } width={55} preserveAspectRatio="xMinYMin">
          <rect x={5} y={5} rx={5} ry={5} width={40} height={40} fill="transparent" stroke="white" strokeWidth={4} />
        </svg>
        <text x={47.5} y={110} dominantBaseline="middle" textAnchor="middle" fill="white" style={{fontSize:"1.5em", fontWeight:"bolder"}}>NeoPixel</text>
        </> : <>  </>}
      */} 
        <g transform ={useVerticalLayout ? "translate(-105 0)" : "translate(-90 -5) scale(0.9)"}>

          {/* Current NeoPixel*/}
          <g transform = {useVerticalLayout ? "scale(1.075) translate(105 -15)" : "scale(0.75) translate(30, -10)"}>
            <rect x={125} y={25} height={65} width={100} fill="white" stroke="black" strokeWidth="2" />
            <text x={175} y={62.5} dominantBaseline="middle" textAnchor="middle" fill="black" style={{fontSize: "4em" }}>{pixel}</text>
            <foreignObject x={125} y={95} height={35} width={100}>
                <select id="neoPixel_Selector" style={{width:"100%", fontSize:"1.15em", fontWeight: "bold", borderColor:"black", borderWidth:"2"}} onChange={()=>changePixel()}>
                    <option value="" selected>Pick Pixel</option>
                    {pixelListOptions}
                </select>
            </foreignObject>
          </g>

          {/* Color Selector */}
          <g transform={useVerticalLayout ? "scale(1.1) translate(-360, -15)" : "scale(0.75) translate(-545, 100)"}>  
            <rect x={700} y={25} width={100} height={62.5} fill={`rgb(${red},${green},${blue})`} stroke="black" strokeWidth="2"/>
            <rect x={700} y={90} class="color_update_button" width={100} height={25} fill="white" stroke="black" strokeWidth="2"/>
            <text x={750} y={104.5} class="color_update_button" dominantBaseline="middle" textAnchor="middle" fill="black" style={{fontSize:"1.5em", fontWeight:"bold"}}>Update</text>
            <rect x={700} y={90} width={100} height={35} fill="transparent" style={{cursor:"pointer"}} onMouseEnter={()=>{mHover("color_update_button", "fill", "black", "white")}} onMouseLeave={()=>{mHover("color_update_button", "fill", "black", "white")}} onClick={()=> {handleColorUpdate()}} />
          </g>
          
          <g transform={useVerticalLayout ? "translate(-30 110)" : ""}> 
            {/* Red Selector */}
            <g transform="scale(0.5) translate(165, -2.5)">
                <rect x={250} y={25} width={125} height={62.5} fill={`rgb(${red},0,0)`} stroke="black" strokeWidth="2"/>
                <foreignObject x={250} y={32.5} width={125} height={55}>
                <input type="number" step="1" max={255} min={0} value={red} onChange={e => {setRed(e.target.value)}} style={{color: "black", width:"70%", margin:"0 auto", fontSize: "2.5em", display:"block", fontWeight: "bold"}}/>
                </foreignObject>
                <rect x={250} y={90} width={125} height={25} fill="white" stroke="black" strokeWidth="2"/>
                <text x={312.5} y={104.5} dominantBaseline="middle" textAnchor="middle" fill="black" style={{fontSize:"1.75em", fontWeight:"bold"}}>Red</text>
            </g>

            {/* Red Scale/Bar */}
            <g>
                <rect x={280} y={11.25} width={310} height={43.75} fill="white" stroke="black" strokeWidth="2"/>
                <line x1={300} y1={28.625} x2={570} y2={28.625} stroke="black" strokeWidth="4"/>
                {/* Lower Bounds */}
                <line x1={300} y1={18.625} x2={300} y2={38.625} stroke="black" strokeWidth="4"/>
                <text x={300} y={38.625} alignmentBaseline="hanging" textAnchor="middle" fill="black" style={{fontSize:"1.25em", fontWeight:"bold"}}>0</text>
                
                {/* Dividing Markers - divides space into 10 equal units */}
                <line x1={300 + (27 * 1)} y1={23.625} x2={300 + (27 * 1)} y2={33.625} stroke="black" strokeWidth="2"/>
                <line x1={300 + (27 * 2)} y1={23.625} x2={300 + (27 * 2)} y2={33.625} stroke="black" strokeWidth="2"/>
                <line x1={300 + (27 * 3)} y1={23.625} x2={300 + (27 * 3)} y2={33.625} stroke="black" strokeWidth="2"/>
                <line x1={300 + (27 * 4)} y1={23.625} x2={300 + (27 * 4)} y2={33.625} stroke="black" strokeWidth="2"/>
                <line x1={300 + (27 * 5)} y1={21.125} x2={300 + (27 * 5)} y2={36.125} stroke="black" strokeWidth="3"/>
                <line x1={300 + (27 * 6)} y1={23.625} x2={300 + (27 * 6)} y2={33.625} stroke="black" strokeWidth="2"/>
                <line x1={300 + (27 * 7)} y1={23.625} x2={300 + (27 * 7)} y2={33.625} stroke="black" strokeWidth="2"/>
                <line x1={300 + (27 * 8)} y1={23.625} x2={300 + (27 * 8)} y2={33.625} stroke="black" strokeWidth="2"/>
                <line x1={300 + (27 * 9)} y1={23.625} x2={300 + (27 * 9)} y2={33.625} stroke="black" strokeWidth="2"/>

                {/* Upper Bounds */}
                <line x1={570} y1={18.625} x2={570} y2={38.625} stroke="black" strokeWidth="4"/>
                <text x={570} y={38.625} alignmentBaseline="hanging" textAnchor="middle" fill="black" style={{fontSize:"1.25em", fontWeight:"bold"}}>255</text>

                {/* Red Value Marker */}
                <line x1={300 + 270 * (red/255)} y1={18.625} x2={300 + 270 * (red/255)} y2={38.625} stroke="red" strokeWidth="5"/> 
            </g>

            {/* Green Selector */}
            <g transform="scale(0.5) translate(15, 102.5)">
                <rect x={400} y={25} width={125} height={62.5} fill={`rgb(0,${green},0)`} stroke="black" strokeWidth="2"/>
                <foreignObject x={400} y={32.5} width={125} height={55}>
                <input type="number" step="1" max={255} min={0} value={green} onChange={e => {setGreen(e.target.value)}} style={{color: "black", width:"70%", margin:"0 auto", fontSize: "2.5em", display:"block", fontWeight: "bold"}}/>
                </foreignObject>
                <rect x={400} y={90} width={125} height={25} fill="white" stroke="black" strokeWidth="2"/>
                <text x={462.5} y={104.5} dominantBaseline="middle" textAnchor="middle" fill="black" style={{fontSize:"1.75em", fontWeight:"bold"}}>Green</text>
            </g>

            {/* Green Scale/Bar */}
            <g>
                <rect x={280} y={64.25} width={310} height={43.75} fill="white" stroke="black" strokeWidth="2"/>
                <line x1={300} y1={81.125} x2={570} y2={81.125} stroke="black" strokeWidth="4"/>
                {/* Lower Bounds */}
                <line x1={300} y1={71.125} x2={300} y2={91.125} stroke="black" strokeWidth="4"/>
                <text x={300} y={91.125} alignmentBaseline="hanging" textAnchor="middle" fill="black" style={{fontSize:"1.25em", fontWeight:"bold"}}>0</text>
                
                {/* Dividing Markers - divides space into 10 equal units */}
                <line x1={300 + (27 * 1)} y1={76.125} x2={300 + (27 * 1)} y2={86.125} stroke="black" strokeWidth="2"/>
                <line x1={300 + (27 * 2)} y1={76.125} x2={300 + (27 * 2)} y2={86.125} stroke="black" strokeWidth="2"/>
                <line x1={300 + (27 * 3)} y1={76.125} x2={300 + (27 * 3)} y2={86.125} stroke="black" strokeWidth="2"/>
                <line x1={300 + (27 * 4)} y1={76.125} x2={300 + (27 * 4)} y2={86.125} stroke="black" strokeWidth="2"/>
                <line x1={300 + (27 * 5)} y1={73.625} x2={300 + (27 * 5)} y2={88.625} stroke="black" strokeWidth="3"/>
                <line x1={300 + (27 * 6)} y1={76.125} x2={300 + (27 * 6)} y2={86.125} stroke="black" strokeWidth="2"/>
                <line x1={300 + (27 * 7)} y1={76.125} x2={300 + (27 * 7)} y2={86.125} stroke="black" strokeWidth="2"/>
                <line x1={300 + (27 * 8)} y1={76.125} x2={300 + (27 * 8)} y2={86.125} stroke="black" strokeWidth="2"/>
                <line x1={300 + (27 * 9)} y1={76.125} x2={300 + (27 * 9)} y2={86.125} stroke="black" strokeWidth="2"/>

                {/* Upper Bounds */}
                <line x1={570} y1={71.125} x2={570} y2={91.125} stroke="black" strokeWidth="4"/>
                <text x={570} y={91.125} alignmentBaseline="hanging" textAnchor="middle" fill="black" style={{fontSize:"1.25em", fontWeight:"bold"}}>255</text>

                {/* Green Value Marker */}
                <line x1={300 + 270 * (green/255)} y1={71.125} x2={300 + 270 * (green/255)} y2={91.125} stroke="green" strokeWidth="5"/> 
            </g>

            {/* Blue Selector */}
            <g transform="scale(0.5) translate(-135, 207.5)">
                <rect x={550} y={25} width={125} height={62.5} fill={`rgb(0,0,${blue})`} stroke="black" strokeWidth="2"/>
                <foreignObject x={550} y={32.5} width={125} height={55}>
                <input type="number" step="1" max={255} min={0} value={blue} onChange={e => {setBlue(e.target.value)}} style={{color: "black", width:"70%", margin:"0 auto", fontSize: "2.5em", display:"block", fontWeight: "bold"}}/>
                </foreignObject>
                <rect x={550} y={90} width={125} height={25} fill="white" stroke="black" strokeWidth="2"/>
                <text x={612.5} y={104.5} dominantBaseline="middle" textAnchor="middle" fill="black" style={{fontSize:"1.75em", fontWeight:"bold"}}>Blue</text>
            </g>

            {/* Blue Scale/Bar */}
            <g>
                <rect x={280} y={116.75} width={310} height={43.75} fill="white" stroke="black" strokeWidth="2"/>
                <line x1={300} y1={133.625} x2={570} y2={133.625} stroke="black" strokeWidth="4"/>
                
                {/* Lower Bounds */}
                <line x1={300} y1={123.625} x2={300} y2={143.625} stroke="black" strokeWidth="4"/>
                <text x={300} y={143.625} alignmentBaseline="hanging" textAnchor="middle" fill="black" style={{fontSize:"1.25em", fontWeight:"bold"}}>0</text>
                
                {/* Dividing Markers - divides space into 10 equal units */}
                <line x1={300 + (27 * 1)} y1={128.625} x2={300 + (27 * 1)} y2={138.625} stroke="black" strokeWidth="2"/>
                <line x1={300 + (27 * 2)} y1={128.625} x2={300 + (27 * 2)} y2={138.625} stroke="black" strokeWidth="2"/>
                <line x1={300 + (27 * 3)} y1={128.625} x2={300 + (27 * 3)} y2={138.625} stroke="black" strokeWidth="2"/>
                <line x1={300 + (27 * 4)} y1={128.625} x2={300 + (27 * 4)} y2={138.625} stroke="black" strokeWidth="2"/>
                <line x1={300 + (27 * 5)} y1={126.125} x2={300 + (27 * 5)} y2={140.125} stroke="black" strokeWidth="3"/>
                <line x1={300 + (27 * 6)} y1={128.625} x2={300 + (27 * 6)} y2={138.625} stroke="black" strokeWidth="2"/>
                <line x1={300 + (27 * 7)} y1={128.625} x2={300 + (27 * 7)} y2={138.625} stroke="black" strokeWidth="2"/>
                <line x1={300 + (27 * 8)} y1={128.625} x2={300 + (27 * 8)} y2={138.625} stroke="black" strokeWidth="2"/>
                <line x1={300 + (27 * 9)} y1={128.625} x2={300 + (27 * 9)} y2={138.625} stroke="black" strokeWidth="2"/>
                
                {/* Upper Bounds */}
                <line x1={570} y1={123.625} x2={570} y2={143.625} stroke="black" strokeWidth="4"/>
                <text x={570} y={143.625} alignmentBaseline="hanging" textAnchor="middle" fill="black" style={{fontSize:"1.25em", fontWeight:"bold"}}>255</text>

                {/* Red Value Marker */}
                <line x1={300 + 270 * (blue/255)} y1={123.625} x2={300 + 270 * (blue/255)} y2={143.625} stroke="blue" strokeWidth="5"/> 
            </g>
          </g>
        </g>
      </svg>   
     </>
    );
}

export default NeoPixel