import React, { useState, useEffect } from 'react';

//Todo: Add a settings gear icon, where a user can modify the min/max values for the data table
// Altering dMax/dMin doesn't modify how the lines are drawn, for some reason.

// 0 - Update graph to move the circle back a bit and place gear icon in the top right corner
//Code for smooth line taken from: https://medium.com/@francoisromain/smooth-a-svg-path-with-functional-programming-1b9876b8bf7e
function SmoothLineGraph({x, y, x_axis_offset=15, y_axis_offset=10, width, height, stroke_width="2", stroke_color="black", stroke_dasharray="none", data, data_max, data_min, download, display_markers=true, display_markers_in_background=false, background_color="white", reset_min_max, display_reset_icon=true, font_size="1.25em", text_offset_x=0, text_offset_y=0}) {
    //assume data is a one dimensional array of y-axis values.
    if(data_max == data_min){//If max/min are the same, force them to be at least one apart
        data_max = data_min + 1;
    }
    let reset_icon = <></>;
    let download_icon =  <></>;
    if(display_reset_icon)
    {
        reset_icon =  <>
                            <rect className={`graph_settings`} x={x+x_axis_offset + width - 25} y={y} width={25} height={50} fill="white" stroke="black" strokeWidth={2}/>
                            <svg className={`graph_settings`} transform="rotate(" preserveAspectRatio="xMinYMin" x={x+x_axis_offset + width - 23} y={y+15} width="24" height="24" fill="black" viewBox="0 0 16 16">
                                <g transform="rotate(-90,8,8)">
                                    <path fill-rule="evenodd" d="M8 3a5 5 0 1 1-4.546 2.914.5.5 0 0 0-.908-.417A6 6 0 1 0 8 2v1z"/>
                                    <path d="M8 4.466V.534a.25.25 0 0 0-.41-.192L5.23 2.308a.25.25 0 0 0 0 .384l2.36 1.966A.25.25 0 0 0 8 4.466z"/>
                                </g>
                            </svg>
                            <rect x={x+x_axis_offset + width - 25} y={y} width={25} height={50} fill="transparent" style={{cursor:"pointer"}} onMouseEnter={() =>{mHover(`graph_settings`, "fill", "white", "black")}} onMouseLeave={() =>{mHover(`graph_settings`, "fill", "white", "black")}} onClick={()=>{reset_min_max()}}/>
                        </>;
        download_icon =  <>
                            <rect className={`graph_download`} x={x+x_axis_offset + width - 25} y={y + 50} width={25} height={50} fill="white" stroke="black" strokeWidth={2}/>
                            <svg className={`graph_download`} viewBox="0 0 24 24" width="20" height="20" preserveAspectRatio="xMinYMin" x={x+x_axis_offset + width - 22.5} y={y+65}><path d="M4.97 11.03a.75.75 0 111.06-1.06L11 14.94V2.75a.75.75 0 011.5 0v12.19l4.97-4.97a.75.75 0 111.06 1.06l-6.25 6.25a.75.75 0 01-1.06 0l-6.25-6.25zm-.22 9.47a.75.75 0 000 1.5h14.5a.75.75 0 000-1.5H4.75z"></path></svg>
                            <rect x={x+x_axis_offset + width - 25} y={y + 50} width={25} height={50} fill="transparent" style={{cursor:"pointer"}} onMouseEnter={() =>{mHover(`graph_download`, "fill", "white", "black")}} onMouseLeave={() =>{mHover(`graph_download`, "fill", "white", "black")}} onClick={()=>{download()}}/>
                        </>;
    }

    let points = [];
    let x_start = x + x_axis_offset;//X-axis location of first data point
    let x_step = (width - x_axis_offset -30) / data.length;//Distance between each datapoint, evenly distributed across x-axis
    let y_range = height - (y_axis_offset * 2);//Range of y values, based on SVG Units
    let y_positive = y_range * ((data_max / (data_max + Math.abs(data_min))));//Length of Y-axis (in svg units) in positive direction
    let y_negative = y_range * (Math.abs(data_min) / (data_max + Math.abs(data_min)));//Length of Y-Axis (in svg units) in negative direction
    let axis_marker_position = y + y_positive + y_axis_offset;//Location of the y-axis (0 position)
    //Modify to handle if min & max are both positive/negative
    //Switch to absolute values?
    data.forEach(data_point => {//Map each datapoint from it's provided range (data_max, data_min) to y-coordinates on the SVG canvas
        let mapped_y = 0;
        if(data_point === undefined) data_point = 0;
        if(data_min <= 0)
        {
            if(data_point >= 0 )
            {
                mapped_y = axis_marker_position - (y_positive * ((data_point > data_max ? data_max : data_point) / data_max)); //inline conditional/ternary operation ensures values do not go beyond the expected max
            }
            else{
                mapped_y = axis_marker_position + (y_negative * ((data_min < 0) ? ((data_point < data_min ? data_min : data_point )/ data_min) : 0));//inline conditional/ternary operation ensures values do not go beyond the expected min
            }
        }
        else
        {//data_min is greater than 0, shift the range by data_min so that it goes from 0 - (data_max-data_min)
            mapped_y =  (y + y_range + y_axis_offset) - (y_range * ((data_point - data_min) > (data_max - data_min) ? 1 : (data_point - data_min) < 0 ? 0 :  (data_point - data_min) / (data_max - data_min))); //inline conditional/ternary operation ensures values do not go beyond the expected max
        }
        points.push([x_start, mapped_y])
        x_start += x_step;
    });

    //Remaining methods taken from code URL
    const line = (pointA, pointB) => {
        const lengthX = pointB[0] - pointA[0]
        const lengthY = pointB[1] - pointA[1]
        return {
            length: Math.sqrt(Math.pow(lengthX, 2) + Math.pow(lengthY, 2)),
            angle: Math.atan2(lengthY, lengthX)
        }
    }
    const controlPoint = (lineCalc, smooth) => (current, previous, next, reverse) => {
        const p = previous || current
        const n = next || current 
        const l = lineCalc(p, n)
        const angle = l.angle + (reverse ? Math.PI : 0)
        const length = l.length * smooth
        const x = current[0] + Math.cos(angle) * length
        const y = current[1] + Math.sin(angle) * length
        return [x, y]
    }

    const bezierCommand = (controlPointCalc) => (point, i, a) => {
        const [cpsX, cpsY] = controlPointCalc(a[i - 1], a[i - 2], point)
        const [cpeX, cpeY] = controlPointCalc(point, a[i - 1], a[i + 1], true)
        return `C ${cpsX},${cpsY} ${cpeX},${cpeY} ${point[0]},${point[1]}`
    }

    const svgPath = (points, command) => {
        const d = points.reduce((acc, point, i, a) => i === 0
            ? `M ${point[0]},${point[1]}`
            : `${acc} ${command(point, i, a)}`
        , '')
        return <path d={d} fill="none" stroke={stroke_color} strokeDasharray={stroke_dasharray} strokeWidth="3"/>
      }

    const controlPointCalc = controlPoint(line, 0.2);
    const bezierCommandCalc = bezierCommand(controlPointCalc); 

    let zero_line = <></>;
    if(data_min < 0)
    {//Todo, verify this works!
        zero_line =  <>
                        <text x={(display_markers_in_background ? x + 12.5 : x + 5) + text_offset_x} y={axis_marker_position + 7.5 + text_offset_y} textAnchor={display_markers_in_background ? "start" : "end"}  opacity={display_markers_in_background ? 0.5 : 1} fill="black" style={{fontSize:font_size, fontWeight: "bold"}}>{display_markers === true ? "0" : ""}</text>
                        <line x1={x+x_axis_offset} y1={axis_marker_position} x2={x + width + x_axis_offset} y2={axis_marker_position} stroke="grey" strokeDasharray="1 3" strokeWidth={stroke_width}/> 
                    </>;
    }

    function mHover(class_name, attribute, option_a, option_b){
        document.querySelectorAll("." + class_name).forEach(element =>{
                element.setAttribute(attribute, element.getAttribute(attribute) == option_a ? option_b : option_a);
            }
        );
    }
  
  return(
      <>
        <rect width={width} height={height} fill={background_color} x={x + x_axis_offset} y={y} stroke="black"></rect>
        
        {/* Max Line */}
        <text x={(display_markers_in_background ? x + 12.5 : x + 5) + text_offset_x} y={y + y_axis_offset +7.5 + text_offset_y} textAnchor={display_markers_in_background ? "start" : "end"} opacity={display_markers_in_background ? 0.5 : 1} fill="black" style={{fontSize:font_size, fontWeight: "bold"}}>{display_markers === true ? data_max.toFixed(2) : ""}</text>
        <line x1={x+x_axis_offset} y1={y + y_axis_offset} x2={x + width + x_axis_offset} y2={y + y_axis_offset} stroke="grey" strokeDasharray="1 3" strokeWidth={stroke_width}/>

        {zero_line}

        {/* Min Line */}
        <text x={(display_markers_in_background ? x + 12.5 : x + 5) + text_offset_x} y={y + height - y_axis_offset +7.5 + text_offset_y} textAnchor={display_markers_in_background ? "start" : "end"} opacity={display_markers_in_background ? 0.5 : 1} fill="black" style={{fontSize:font_size, fontWeight: "bold"}}>{display_markers === true ? data_min.toFixed(2) : ""}</text>
        <line x1={x+x_axis_offset} y1={y + height - y_axis_offset} x2={x + width + x_axis_offset} y2={y + height - y_axis_offset} stroke={"grey"} strokeDasharray="1 3" strokeWidth={stroke_width}/>
        
        {svgPath(points, bezierCommandCalc)}
        <circle cx={points[points.length - 1][0]} cy={points[points.length - 1][1]} r="6" fill="white" stroke="black" strokeWidth={stroke_width}/>
      
        {reset_icon}
        {download_icon}
       </>
    )
}

export default SmoothLineGraph
