import React, {Suspense} from 'react';
import {boards} from "../Boards/BoardList.js"
//import 'bootstrap';
/**
 * 
 */

function BoardSelector({selectBoard, closeModal}) {
    
    //Todo: Add in options to scroll through when board list is too large to fit on screen. 
    let board_buttons = [];
    const x_min = 390;
    const x_max = 840;
    const x_step = 225;
    const y_step = 100;
    let x = x_min;
    let y = 75;
    //Todo for text centering, switch to a monospace font and measure the width of a single character to determine spacing
    //Height - 18
    //Width - 14
    //g id={`${boards[board_index].folder}_group`}
    for(let board_index= 0; board_index < boards.length; board_index++)
    {
        board_buttons.push(<rect id={`${boards[board_index].folder}_rect`} x={x} y={y} width={200} height={75} fill="var(--bgColor1)" stroke="white" strokeWidth="3" /> );
        
        if(boards[board_index].name.length == 2)
        {
            board_buttons.push(<text xmlSpace="preserve" class={`${boards[board_index].folder}_text`} textLength={175} x={x + 12.5} y={y + 30} fill="white" font-family="monospace" style={{fontSize:"1.75em", fontWeight:"bold"}}>
                                {boards[board_index].name[0]}
                            </text>);
            board_buttons.push(<text xmlSpace="preserve" class={`${boards[board_index].folder}_text`} textLength={175} x={x + 12.5} y={y + 60} fill="white" font-family="monospace" style={{fontSize:"1.75em", fontWeight:"bold"}}>
                                {boards[board_index].name[1]}
                            </text>);
        }
        else{
            board_buttons.push(<text xmlSpace="preserve" class={`${boards[board_index].folder}_text`} textLength={175} x={x + 12.5} y={y + 45} fill="white" font-family="monospace" style={{fontSize:"1.75em", fontWeight:"bold"}}>
                                {boards[board_index].name[0]}
                            </text>);
        }
        
        board_buttons.push(<rect fill="transparent" onMouseEnter={()=>{select(boards[board_index].folder)}} onMouseLeave={()=>{deselect(boards[board_index].folder)}} style={{cursor:'pointer'}} onClick={() => {selectBoard(boards[board_index].folder); closeModal(false);}} x={x} y={y} width={200} height={75}>
                                <title>
                                    {`Click to select the ${boards[board_index].name}`}
                                </title>  
                            </rect>
                        );
        x += x_step;
        if(x > x_max)
        {
            x = x_min;
            y += y_step;
        }
    }

    function select(type){
        document.getElementById(type + "_rect").setAttribute("fill", "white");
        document.querySelectorAll(`.${type}_text`).forEach(element =>{
            element.setAttribute("fill", "var(--bgColor1)");
        });
      }
    
    function deselect(type){
      //Verify sensor isn't selected before removing styling
        document.getElementById(type + "_rect").setAttribute("fill", "var(--bgColor1)");
        document.querySelectorAll(`.${type}_text`).forEach(element =>{
            element.setAttribute("fill", "white");
        });
    }
   
    return (
        <>
            <svg 
                width={"100%"} 
                viewBox="0 0 1440 650"
                x={0}
                y={55}
                preserveAspectRatio="xMinYMin"
            >
                {/* Translucent Background */}
                <rect width={1440} height={800} fill="white" x={0} y={0} opacity={0.75}></rect>

                {/* Board Selector Area */}
                <rect width={810} height={310} fill="white" stroke="var(--bgColor1)" strokeWidth={5} x={315} y={45}></rect>
                <rect width={800} height={300} fill="var(--bgColor1)" x={320} y={50}></rect>
                

                {/* Boards displayed here */}
                {board_buttons}

                {/* Close Button */}
                <rect id="close_rect" x={615} y ={296.5} fill={"var(--bgColor1)"} stroke="white" strokeWidth={2.5} width={200} height={35} /> 
                <text class="close_text" x={680} y={323} fill="white" style={{fontSize:"1.75em", fontWeight:"bold"}}>
                    Close
                    <title>Close board selector menu</title>
                </text>
                <rect x={615} y ={296.5} fill="transparent" width={200} height={35} cursor="pointer" onMouseEnter={()=>{select("close")}} onMouseLeave={()=>{deselect("close")}} onClick={() => {{closeModal(false)}}} />


            </svg>
        </>
    );
}
  
export default BoardSelector