
import React, { useState, useEffect } from 'react';
import SmoothLineGraph from "../GraphingUtilities/SmoothLineGraph.js";

//Todo update highlighting (mHover) for buttons to also update font color to use --fontColorX
//Todo, accept a max value for pwm - micro:bit uses 1023, arduino uses 255
function Button({x, y, width, pinList, selectedPin, setSelectedPin, serialCom, deviceData, useVerticalLayout}) {
    //Digtal Specific
    const [digitalOutputState, setDigitalOutputState] = useState(0); 
    useEffect(() =>{//Add modifyier warning when pin changes
        if(selectedPin.input)
        {
            //serialCom("PIN_MODE", {mode: 0, pin: selectedPin.digital});
            serialCom("PIN_READ", {signalType: 0, pin: selectedPin.digital});
        }
        else
        {
            for(let i = 0; i < pinList.length; i++)
            {//Set to first pin with input
                if(pinList[i].input)
                {
                    setSelectedPin(pinList[i]);
                    break;
                }
            }
        }
      }, [selectedPin]);

    let pinListOptions = [];
    for(let i = 0; i < pinList.length; i++)
    {
        if(pinList[i].input){
            pinListOptions.push(<option value={`${i}`}>{pinList[i].name}</option>);
        }
    }
    let digital_display = <> </>;
    let digital_state = 0;
    if(deviceData.digital.hasOwnProperty(selectedPin.digital))
    {//This shouldn't be necessary, there should always be a digital value for every pin
        digital_state = deviceData.digital[selectedPin.digital][deviceData.digital[selectedPin.digital].length - 1];//Retrieve latest value
    }

    if(digital_state == 0)
    {//Low
        digital_display = <> 
                        <svg width={240} viewBox="110 7.5 240 57.5" fill="none" x={250} y={25} preserveAspectRatio="xMinYMin">
                            <text  x= {177.5} y={55}  fill="black" style={{fontWeight: "bold", fontSize: "4em"}}>0</text> 
                            <text  x= {150} y={100}  fill="black" style={{fontWeight: "bold", fontSize: "3.5em"}}>LOW</text>
                            <rect fill="black"  x={234} y={15} width={4} height={25} />
                            <rect fill="black"  x={223} y={40} width={25} height={3} />
                            <rect fill="black"  x={228} y={45} width={15} height={3} />
                            <rect fill="black"  x={233} y={50} width={5} height={3} />
                        </svg>
                    </>;   
    }
    else
    {//HIGH
        digital_display = <>
                    <svg width={240} viewBox="110 7.5 240 57.5" fill="none" x={250} y={25} preserveAspectRatio="xMinYMin">
                        <text  x= {175} y={55}  fill="red" style={{fontWeight: "bold", fontSize: "4em"}}>1</text> 
                        <text  x= {140} y={100}  fill="red" style={{fontWeight: "bold", fontSize: "3.5em"}}>HIGH</text>
                        <rect fill="red"  x={224} y={15.5} width={4} height={10.5} />
                        <rect fill="red"  x={213} y={26} width={25} height={4} />
                        <rect fill="red"  x={218} y={31.5} width={15} height={4} />
                        <rect fill="red"  x={213} y={37.5} width={25} height={4} />
                        <rect fill="red"  x={218} y={43.5} width={15} height={4} />
                        <rect fill="red"  x={224} y={45} width={4} height={10} />
                    </svg>
                </>;
    
    }

    function mHover(class_name, attribute, colorA, colorB){
        document.querySelectorAll("." + class_name).forEach(element =>{
                element.setAttribute(attribute, element.getAttribute(attribute) == colorA ? colorB : colorA);
            }
        );
    }
  
    function changePin()
    {
        var pinSelect = document.getElementById("pinSelecter_HardwarePin");
        var value = pinSelect.options[pinSelect.selectedIndex].value;
        pinSelect.selectedIndex = 0;
        if(value !== "")
        {
            setSelectedPin(pinList[value]);
            //serialCom("PIN_MODE", {mode: 0, pin: pinList[value].digital});
            serialCom("PIN_READ", {signalType: 0, pin: pinList[value].digital});
        }
    }

  return (
      <>
        <svg 
            width={width} 
            viewBox={`0 0 500 150`}
            x={x}
            y={y}
            preserveAspectRatio="xMinYMin"
        > 
        {/* Logo 
        {displayMode === "Basic" ? 
            <>
                <line x1={115} x2={115} y1={25} y2={125} stroke="white" strokeWidth="4" />
                <circle cx={50} cy={60} r={7.5} fill="white" />
                <text x={55} y={115} dominantBaseline="middle" textAnchor="middle" fill="white" style={{fontSize:"2.5em", fontWeight:"bolder"}}>Button</text>
            </> : <></>}*/}  
          <g transform ={useVerticalLayout ? "scale(0.85) translate(50 -5)": ""}>
            <g transform ={useVerticalLayout ? "translate(0 -20) scale(1.5)": "translate(-100 -10) scale(1.25)"}>
                {/* Display/Select Pin*/}
                <rect x={125} y={25} height={65} width={75} fill="white" stroke="black" strokeWidth="2" />
                <text x={162.5} y={62.5} dominantBaseline="middle" textAnchor="middle" fill="black" style={{fontSize:"3em", fontWeight:"bold"}}>{selectedPin.name}</text>
                <rect className="pin_select" x={125} y={90} height={35} width={75} fill="white" stroke="black" strokeWidth="2" />
                <foreignObject x={127.5} y={95} height={35} width={70.5}>
                    <select id="pinSelecter_HardwarePin" style={{width:"100%", fontSize:"1.15em", borderColor:"white"}} onChange={()=>changePin()}>
                        <option value="" selected>Select</option>
                        {pinListOptions}
                    </select>
                </foreignObject>
            </g>
            <g transform ={useVerticalLayout ? "translate(-250 145) scale(1.5)": "translate(-100 -10) scale(1.25)"}>
                {/* Digital State, Analog value, PWM slider*/}
                <rect  x={225} y={25} height={100} width={235} fill="white" stroke="black" strokeWidth="2" />
                {digital_display}
            </g>
          </g>
        </svg> 
     </>

    );
}

export default Button