
import React, { useState, useEffect } from 'react';
import SmoothLineGraph from "../GraphingUtilities/SmoothLineGraph.js";

//Todo update highlighting (mHover) for buttons to also update font color to use --fontColorX
//Todo, accept a max value for pwm - micro:bit uses 1023, arduino uses 255
function HardwarePin({x, y, width, pinList, selectedPin, setSelectedPin, serialCom, deviceData, modeText="PinMode", modes=["INPUT", "OUTPUT", "INPUT PUllUP"], useVerticalLayout}) {
    const [pinType, setPinType] = useState("DIGITAL");
    const [pinModeIndex, setPinModeIndex] = useState(0);
    const [allowPinChanges, setAllowPinChanges] = useState(true);//modified, originally false

    //Digtal Specific
    const [digitalOutputState, setDigitalOutputState] = useState(0);

    //Pwm specific
    const [pwmVal, setPwmVal] = useState(100);
    const [pwmColor, setPwmColor] = useState("black");
    const [pwmWeight, setPwmWeight] = useState("normal");

    useEffect(() =>{//Add modifyier warning when pin changes
        setAllowPinChanges(true);
        if(deviceData.pwm.hasOwnProperty(selectedPin.pwm))
        {
            setPwmVal(deviceData.pwm[selectedPin.pwm]);
        }
        serialCom("PIN_READ", {signalType: 0, pin: selectedPin.digital});
      }, [selectedPin]);

    //Pin covers prevent user from selecting unsupported signal types (PWM and/or ANALOG)
    let analog_cover = <rect x={225} y={60} height={30} width={125} fill="grey" opacity="0.7">
                            <title>Analog not supported</title>
                        </rect>;
    if(deviceData.analog.hasOwnProperty(selectedPin.analog))
    {//This shouldn't be necessary, there should always be a digital value for every pin
        analog_cover = <> </>;
    }
    let pwm_cover = <rect x={225} y={95} height={30} width={125} fill="grey" opacity="0.7">
                        <title>PWM not supported</title>
                    </rect>;
    if(deviceData.pwm.hasOwnProperty(selectedPin.pwm))
    {//This shouldn't be necessary, there should always be a digital value for every pin
        pwm_cover = <> </>;
    }

    //Handle Pin Selection dropdown
    //Todo create a foreign object, with select and the appropriate options
    // Will probably need to re-add pinlist because we will need the full description for the pin to add a selected pin
    
    //This assumes that all pins have the ability to send/receive a digital signal
    let pinListOptions = [];
    for(let i = 0; i < pinList.length; i++)
    {
        pinListOptions.push(<option value={`${i}`}>{pinList[i].name}</option>)
    }

    //Todo: Handle Input Pullup, must be seperated onto two lines
    let smallDisplay = <> </>;
    let largeDisplay = <> </>;
    let pin_cover = <rect x={225} y={25} height={100} width={125} fill="grey" stroke="black" strokeWidth="2"/>
    if(allowPinChanges)
    {
        pin_cover = <></>;
        switch(pinType)
        {
            case "DIGITAL"://Todo modify, have mode at the bottom and have arrows beside it (instead of next to the actual mode display)
                let modeState = <text x={437.5} y={95} dominantBaseline="middle" textAnchor="middle" fill="black" style={{fontSize:"2.25em", fontWeight:"bold"}}>{modes[pinModeIndex]}</text>;
                if(modes[pinModeIndex].includes(" "))
                {//Separate pinmode words onto separate lines
                    modeState = <>
                                    <text x={437.5} y={82.5} dominantBaseline="middle" textAnchor="middle" fill="black" style={{fontSize:"2.25em", fontWeight:"bold"}}>{modes[pinModeIndex].split(" ")[0]}</text>;
                                    <text x={437.5} y={107.5} dominantBaseline="middle" textAnchor="middle" fill="black" style={{fontSize:"2.25em", fontWeight:"bold"}}>{modes[pinModeIndex].split(" ")[1]}</text>;
                                </>;
                }
                smallDisplay = <>
                                <text x={437.5} y={45} dominantBaseline="middle" textAnchor="middle" fill="black" style={{fontSize:"1.4em", fontWeight:"bold"}}>{modeText}</text>
                                <line x1={375} x2={500} y1={60} y2={60} stroke="black" strokeWidth="2"/>
                                {/* Left Arrow */}
                                <rect className="left_arrow_pin_mode_background" x={375} y={25} height={35} width={25} fill="white" stroke="black" strokeWidth="2"/>
                                <line className="left_arrow_pin_mode" x1={382.5} x2={390} y1={42.5} y2={30} stroke="black" strokeWidth="4" strokeLinecap="round"/>
                                <line className="left_arrow_pin_mode" x1={382.5} x2={390} y1={42.5} y2={55} stroke="black" strokeWidth="4" strokeLinecap="round"/>
                                <rect x={375} y={25} height={35} width={25} fill="transparent" style={{cursor:"pointer"}} onMouseEnter={()=>{mHover("left_arrow_pin_mode", "stroke", "black", "white");mHover("left_arrow_pin_mode_background", "fill", "black", "white")}} onMouseLeave={()=>{mHover("left_arrow_pin_mode", "stroke", "black", "white");mHover("left_arrow_pin_mode_background", "fill", "black", "white")}} onClick={()=>{handlePinModeChange("decrease")}}/>
                                {/* Right Arrow */}
                                <rect className="right_arrow_pin_mode_background" x={475} y={25} height={35} width={25} fill="white" stroke="black" strokeWidth="2"/>
                                <line className="right_arrow_pin_mode" x1={492.5} x2={485} y1={42.5} y2={30} stroke="black" strokeWidth="4" strokeLinecap="round"/>
                                <line className="right_arrow_pin_mode" x1={492.5} x2={485} y1={42.5} y2={55} stroke="black" strokeWidth="4" strokeLinecap="round"/>
                                <rect x={475} y={25} height={35} width={25} fill="transparent" style={{cursor:"pointer"}} onMouseEnter={()=>{mHover("right_arrow_pin_mode", "stroke", "black", "white");mHover("right_arrow_pin_mode_background", "fill", "black", "white")}} onMouseLeave={()=>{mHover("right_arrow_pin_mode", "stroke", "black", "white");mHover("right_arrow_pin_mode_background", "fill", "black", "white")}} onClick={()=>{handlePinModeChange("increase");}}/>
                                {/*PinMode*/}
                                {modeState}
                            </>;
                
                //Determine which display to show based on current state [0] and mode [1]
                if(pinModeIndex == 0 || pinModeIndex == 2)
                {//Read current pin state
                    let digital_state = 0;
                    if(deviceData.digital.hasOwnProperty(selectedPin.digital))
                    {//This shouldn't be necessary, there should always be a digital value for every pin
                        digital_state = deviceData.digital[selectedPin.digital][deviceData.digital[selectedPin.digital].length - 1];//Retrieve latest value
                    }
                
                    if(digital_state == 0)
                    {//Low
                        largeDisplay = <> 
                                        <svg width={240} viewBox="110 7.5 240 57.5" fill="none" x={550} y={25} preserveAspectRatio="xMinYMin">
                                            <text  x= {177.5} y={55}  fill="black" style={{fontWeight: "bold", fontSize: "4em"}}>0</text> 
                                            <text  x= {150} y={100}  fill="black" style={{fontWeight: "bold", fontSize: "3.5em"}}>LOW</text>
                                            <rect fill="black"  x={234} y={15} width={4} height={25} />
                                            <rect fill="black"  x={223} y={40} width={25} height={3} />
                                            <rect fill="black"  x={228} y={45} width={15} height={3} />
                                            <rect fill="black"  x={233} y={50} width={5} height={3} />
                                        </svg>
                                    </>;   
                    }
                    else
                    {//HIGH
                        largeDisplay = <>
                                    <svg width={240} viewBox="110 7.5 240 57.5" fill="none" x={550} y={25} preserveAspectRatio="xMinYMin">
                                        <text  x= {175} y={55}  fill="red" style={{fontWeight: "bold", fontSize: "4em"}}>1</text> 
                                        <text  x= {140} y={100}  fill="red" style={{fontWeight: "bold", fontSize: "3.5em"}}>HIGH</text>
                                        <rect fill="red"  x={224} y={15.5} width={4} height={10.5} />
                                        <rect fill="red"  x={213} y={26} width={25} height={4} />
                                        <rect fill="red"  x={218} y={31.5} width={15} height={4} />
                                        <rect fill="red"  x={213} y={37.5} width={25} height={4} />
                                        <rect fill="red"  x={218} y={43.5} width={15} height={4} />
                                        <rect fill="red"  x={224} y={45} width={4} height={10} />
                                    </svg>
                                </>;
                    
                    }
                }
                else
                {//Handle digital output
                    if(digitalOutputState == 0)
                    {//Low
                        largeDisplay = <>
                                        <svg width={240} viewBox="110 7.5 240 57.5" fill="none" x={540} y={25} preserveAspectRatio="xMinYMin">
                                            <text  x= {157.5} y={55}  fill="black" style={{fontWeight: "bold", fontSize: "4em"}}>0</text> 
                                            <text  x= {130} y={100}  fill="black" style={{fontWeight: "bold", fontSize: "3.5em"}}>LOW</text>
                                            <rect fill="black"  x={214} y={15} width={4} height={25} />
                                            <rect fill="black"  x={203} y={40} width={25} height={3} />
                                            <rect fill="black"  x={208} y={45} width={15} height={3} />
                                            <rect fill="black"  x={213} y={50} width={5} height={3} />
                                            <rect fill="black"  x={267.5} y={20} rx={10} ry={10} width ={30} height={75}></rect>
                                            <rect fill="white"  x={270} y={65} rx={8} ry={8} width={25} height={25}></rect>
                                            <rect fill="transparent" x={267.5} y={20} rx={10} ry={10} width ={30} height={75} style={{cursor:'pointer'}} onClick={() => {serialCom("DIGITAL", {pin: selectedPin.digital, value: 1}); setDigitalOutputState(1);}}>
                                            <title>Set pin to HIGH</title>
                                            </rect>
                                        </svg>
                                    </>;
                    }
                    else
                    {//High Output
                        largeDisplay = <>
                                        <svg width={240} viewBox="110 7.5 240 57.5" fill="none" x={540} y={25} preserveAspectRatio="xMinYMin">
                                            <text  x= {155} y={55}  fill="red" style={{fontWeight: "bold", fontSize: "4em"}}>1</text> 
                                            <text  x= {120} y={100}  fill="red" style={{fontWeight: "bold", fontSize: "3.5em"}}>HIGH</text>
                                            <rect fill="red"  x={204} y={15.5} width={4} height={10.5} />
                                            <rect fill="red"  x={193} y={26} width={25} height={4} />
                                            <rect fill="red"  x={198} y={31.5} width={15} height={4} />
                                            <rect fill="red"  x={193} y={37.5} width={25} height={4} />
                                            <rect fill="red"  x={198} y={43.5} width={15} height={4} />
                                            <rect fill="red"  x={204} y={45} width={4} height={10} />
                                            <rect fill="black"  x={267.5} y={20} rx={10} ry={10} width ={30} height={75}></rect>
                                            <rect fill="white"  x={270} y={25} rx={8} ry={8} width={25} height={25}></rect>
                                            <rect fill="transparent"  x={267.5} y={20} rx={10} ry={10} width ={30} height={75} style={{cursor:'pointer'}} onClick={() => {serialCom("DIGITAL", {pin: selectedPin.digital, value: 0}); setDigitalOutputState(0);}}>
                                            <title>Set pin to LOW</title>
                                            </rect>
                                        </svg>
                                    </>;
                    }
                }
                break;
            case "ANALOG":
                let analog_val = deviceData.analog[selectedPin.analog][deviceData.analog[selectedPin.analog].length - 1];
                smallDisplay = <>
                                <text x={437.5} y={62.5} dominantBaseline="middle" textAnchor="middle" fill="black" style={{fontSize:"2.5em", fontWeight:"bold"}}>{analog_val}</text>
                                <line x1={375} x2={500} y1={90} y2={90} stroke="black" strokeWidth="2"/>
                                {/*<line x1={387.5} x2={487.5} y1={107.5} y2={107.5} stroke="black" strokeWidth="4"/>
                                <line x1={387.5} x2={387.5} y1={112.5} y2={102.5} stroke="black" strokeWidth="4"/>
                                <line x1={487.5} x2={487.5} y1={112.5} y2={102.5} stroke="black" strokeWidth="4"/>
                                <line x1={387.5 + (100/1023 * analog_val)} x2={387.5 + (100/1023 * analog_val)} y1={115} y2={100} stroke="red" strokeWidth="6" strokeLinecap="round"/>*/}
                                <text  x={437.5} y={110} dominantBaseline="middle" textAnchor="middle" fill="black" style={{fontSize:"1.75em", fontWeight:"bold"}}>Value</text>
                            </>;

                //Add graphing component here
                largeDisplay = <>
                            <SmoothLineGraph x={565} y={25.5} height={99} width={180} y_axis_offset={12.5} data={deviceData.analog[selectedPin.analog].slice(-25)} data_max={1023} data_min={0} display_reset_icon="false" font_size="1em" text_offset_x={7.5} text_offset_y={-2.5} reset_min_max={()=>{}}/>
                        </>;
                break;

            case "PWM":
                smallDisplay = <>
                                <foreignObject x={375} y={32.5} width={125} height={55}>
                                  <input type="number" step="1" max={255} min={0} value={pwmVal} onChange={e => {pwmChange(e.target.value)}} style={{color: pwmColor, width:"70%", margin:"0 auto", fontSize: "2.5em", display:"block", fontWeight: pwmWeight}}/>
                                </foreignObject>
                                <line x1={375} x2={500} y1={90} y2={90} stroke="black" strokeWidth="2"/>
                                <rect x={375} y={90} width={125} height={35} className="pwm_update_button" fill="white" stroke="black" strokeWidth="2"/>
                                <text className="pwm_update_button" x={437.5} y={110} dominantBaseline="middle" textAnchor="middle" fill="black" style={{fontSize:"1.75em", fontWeight:"bold"}}>Update</text>
                                <rect x={375} y={90} width={125} height={35} fill="transparent" style={{cursor:"pointer"}} onMouseEnter={()=>{mHover("pwm_update_button", "fill", "black", "white")}} onMouseLeave={()=>{mHover("pwm_update_button", "fill", "black", "white")}} onClick={()=> {handlePwmUpdate()}} />
                                </>;

                //Add Duty Cycle component here
                largeDisplay = <>
                            <line x1={525} x2={760} y1={90} y2={90} stroke="black" strokeWidth="2" />
                            <text x={647.5} y={110} dominantBaseline="middle" textAnchor="middle" fill="black" style={{fontSize:"1.75em", fontWeight:"bold"}}>Duty Cycle</text>
                            <line x1={642.5} x2={642.5} y1={90} y2={25} stroke="black" strokeWidth="2" />
                            <text x={580} y={80} dominantBaseline="middle" textAnchor="middle" fill="black" style={{fontSize:"1.75em", fontWeight:"bold"}}>ON</text>
                            <text x={585} y={50} dominantBaseline="middle" textAnchor="middle" fill="black" style={{fontSize:"2.5em", fontWeight:"bold"}}>{Math.floor(pwmVal / 255 * 100).toString() + "%"}</text>
                            <rect x={642.5} y={25} height={65} width={117.5} fill="black" />
                            <text x={700} y={80} dominantBaseline="middle" textAnchor="middle" fill="white" style={{fontSize:"1.75em", fontWeight:"bold"}}>OFF</text>
                            <text x={705} y={50} dominantBaseline="middle" textAnchor="middle" fill="white" style={{fontSize:"2.5em", fontWeight:"bold"}}>{(100 - Math.floor(pwmVal / 255 * 100)).toString() + "%"}</text>
                        </>;
                break;
        }
    }
    else{//This will display a "modify pin" button which prevents the system from updating a pin's state until the user wishes too.
        largeDisplay =<>
                         <rect x={375} y={25} height={100} width={385} fill="grey" stroke="black" strokeWidth="2" />
                         <rect className="enable_pin_modifications" x={400} y={50} height={50} width={335} fill="white" stroke="black" strokeWidth="2" />
                         <text className="enable_pin_modifications" x={567.5} y={80} dominantBaseline="middle" textAnchor="middle" fill="black" style={{fontSize:"3em", fontWeight:"bold"}}>Modify Pin</text>
                         <rect x={400} y={50} height={50} width={335} fill="transparent" style={{cursor:"pointer"}} onMouseEnter={()=>{mHover("enable_pin_modifications", "fill", "black", "white")}} onMouseLeave={()=>{mHover("enable_pin_modifications", "fill", "black", "white")}} onClick={()=>{setAllowPinChanges(true); serialCom("PIN_READ", {signalType: 0, pin: selectedPin.digital})}}/>
                      </>;
    }

    function mHover(class_name, attribute, colorA, colorB){
        document.querySelectorAll("." + class_name).forEach(element =>{
                element.setAttribute(attribute, element.getAttribute(attribute) == colorA ? colorB : colorA);
            }
        );
    }

    function handlePwmUpdate(){
        if(pwmVal <= 255 && pwmVal >=0)
        {//Ensure Integers (1) do not contain decimal points, must be whole numbers
          serialCom("PWM", {pin: selectedPin.pwm, value: pwmVal});
          setPwmColor("black");
          setPwmWeight("normal");
        }
        else{
          alert(`Warning: The pwm value must be between [0-255]`);
        }
    }
  
    function pwmChange(newValue)
    {
      setPwmColor("red");
      setPwmWeight("bold");
      setPwmVal(newValue);
    }

    function handlePinModeChange(direction)
    {
        let tempIndex = pinModeIndex;
        if(direction ==="increase")
        {
            tempIndex = tempIndex + 1 >= modes.length ? 0 : tempIndex + 1;
        }
        else{
            tempIndex = tempIndex - 1 < 0 ? modes.length -1 : tempIndex - 1;
        }
        switch(modes[tempIndex])
        {//TODO for arduino compatibitly modify this, the controller to utilize a PinMode, and CC for micro:bit to have a PinMode option that can handle pinmode changes
            case "INPUT":
                //serialCom("DIGITAL", {pin: selectedPin.digital, value: 0});
                //serialCom("PIN_MODE", {mode: 0, pin: selectedPin.digital});
                serialCom("PIN_READ", {signalType: 0, pin: selectedPin.digital});
            break;

            case "OUTPUT":
                //serialCom("PIN_MODE", {mode: 1, pin: selectedPin.digital});
                serialCom("DIGITAL", {pin: selectedPin.digital, value: digitalOutputState});//Set to previous digital output
            break;

            case "INPUT PULLUP"://Update to support on arduino devices
                //serialCom("PIN_MODE", {mode: 2, pin: selectedPin.digital});
                serialCom("PIN_READ", {signalType: 2, pin: selectedPin.digital});
            break;
        }
        setPinModeIndex(tempIndex);
    }

    function changePin()
    {
        var pinSelect = document.getElementById("pinSelecter_HardwarePin");
        var value = pinSelect.options[pinSelect.selectedIndex].value;
        pinSelect.selectedIndex = 0;
        if(value !== "")
        {
            setSelectedPin(pinList[value]);
            setPinType("DIGITAL");
            serialCom("PIN_READ", {signalType: 0, pin: pinList[value].digital});
        }
    }

  return (
      <>
        <svg 
            width={width} 
            viewBox={`0 0 500 150`}
            x={x}
            y={y}
            preserveAspectRatio="xMinYMin"
        > 
        {/* Logo 
        {displayMode === "Basic" ? <> 
            <line x1={100} x2={100} y1={25} y2={125} stroke="white" strokeWidth="4" />
            <circle cx={50} cy={60} r={25} fill="transparent" stroke="white" strokeWidth="4"  />
            <circle cx={50} cy={60} r={15} fill="transparent" stroke="white" strokeWidth="4" />
            <circle cx={50} cy={60} r={7.5} fill="white" />
            <text x={50} y={115} dominantBaseline="middle" textAnchor="middle" fill="white" style={{fontSize:"2.5em", fontWeight:"bolder"}}>Pins</text>
        </> : <></>}*/} 
        <g transform = "translate(-115, -20)">
            {/* Display/Select Pin*/}
            <rect x={125} y={25} height={65} width={75} fill="white" stroke="black" strokeWidth="2" />
            <text x={162.5} y={62.5} dominantBaseline="middle" textAnchor="middle" fill="black" style={{fontSize:"3em", fontWeight:"bold"}}>{selectedPin.name}</text>
            <rect className="pin_select" x={125} y={90} height={35} width={75} fill="white" stroke="black" strokeWidth="2" />
            <foreignObject x={127.5} y={95} height={35} width={70.5}>
                <select id="pinSelecter_HardwarePin" style={{width:"100%", fontSize:"1.15em", borderColor:"white"}} onChange={()=>changePin()}>
                    <option value="" selected>Select</option>
                    {pinListOptions}
                </select>
            </foreignObject>
            <g transform="scale(0.7) translate(75, 10)">
                {/* Digital Pin*/}
                <rect className="digital_select" x={225} y={25} height={30} width={125} fill={pinType === "DIGITAL" ? "var(--bgColor3)": "white"} stroke="black" strokeWidth="2" />
                <text x={287.5} y={42.5} dominantBaseline="middle" textAnchor="middle" fill="black" style={{fontSize:"1.75em", fontWeight:"bold"}}>Digital</text>
                <rect x={225} y={25} height={30} width={125} fill="transparent" style={{cursor:pinType === "DIGITAL" ? "default" :"pointer"}} onMouseEnter={()=>{if(pinType !== "DIGITAL"){mHover("digital_select", "fill", "var(--bgColor3)", "white")}}} onMouseLeave={()=>{if(pinType !== "DIGITAL"){mHover("digital_select", "fill", "var(--bgColor3)", "white")}}} onClick={()=>{setPinType("DIGITAL"); serialCom("PIN_READ", {signalType: 0, pin: selectedPin.digital})}}/>
                
                {/* Analog Pin*/}
                <rect className="analog_select" x={225} y={60} height={30} width={125} fill={pinType === "ANALOG" ? "var(--bgColor3)": "white"} stroke="black" strokeWidth="2" />
                <text x={287.5} y={77.5} dominantBaseline="middle" textAnchor="middle" fill="black" style={{fontSize:"1.75em", fontWeight:"bold"}}>Analog</text>
                <rect x={225} y={60} height={30} width={125} fill="transparent" style={{cursor:pinType === "ANALOG" ? "default" :"pointer"}} onMouseEnter={()=>{if(pinType !== "ANALOG"){mHover("analog_select", "fill", "var(--bgColor3)", "white")}}} onMouseLeave={()=>{if(pinType !== "ANALOG"){mHover("analog_select", "fill", "var(--bgColor3)", "white")}}} onClick={()=>{setPinType("ANALOG"); serialCom("PIN_READ", {signalType: 1, pin: selectedPin.analog})}}/>
                {analog_cover}

                {/* PWM Pin*/}
                <rect className="pwm_select" x={225} y={95} height={30} width={125} fill={pinType === "PWM" ? "var(--bgColor3)": "white"}stroke="black" strokeWidth="2" />
                <text x={287.5} y={112.5} dominantBaseline="middle" textAnchor="middle" fill="black" style={{fontSize:"1.75em", fontWeight:"bold"}}>PWM</text>
                <rect x={225} y={95} height={30} width={125} fill="transparent" style={{cursor:pinType === "PWM" ? "default" : "pointer"}} onMouseEnter={()=>{if(pinType !== "PWM"){mHover("pwm_select", "fill", "var(--bgColor3)", "white")}}} onMouseLeave={()=>{if(pinType !== "PWM"){mHover("pwm_select", "fill", "var(--bgColor3)", "white")}}} onClick={()=>{setPinType("PWM")}}/>
                {pwm_cover}

                {/* Ensure user cannot modify pins if they haven't pressed "Modify Pins" yet*/}
                {pin_cover}
            </g>

            {/* Value or PinMode*/}
            <g transform="scale(0.7) translate(-75, 117.5)">
                <rect  x={375} y={25} height={100} width={125} fill="white" stroke="black" strokeWidth="2" />
                {smallDisplay}
            </g>
            {/* Digital State, Analog value, PWM slider*/}
            <g transform=" scale(1.25) translate(-275, 5)">
                <rect  x={525} y={25} height={100} width={235} fill="white" stroke="black" strokeWidth="2" />
                {largeDisplay}
            </g>
            </g>
        </svg> 
     </>

    );
}

export default HardwarePin