
import React, { useState, useEffect } from 'react';
import SyntaxHighlighter from 'react-syntax-highlighter';
import { vs } from 'react-syntax-highlighter/dist/esm/styles/hljs';


/**
 *  Todo:
 *   make type selection with code upload, this might remove some of the issues with re-rendering and changing the state here
 */
function Code_Display_Text({breakpoint_id, code_package, serialCom, x, y, width, height}) {
    const [breakpointList, setBreakpointList] = useState([]);
    const [breakpointList_Skip, setBreakpointList_Skip] = useState([]);

    useEffect(() =>{
        if(document.getElementById("code_text_container") !== null)
            prepareBreakpoints(); 
        if(breakpointList.includes(breakpoint_id.replaceAll(" ", "_")))
        {
            setBreakpoint(breakpoint_id.replaceAll(" ", "_"));
        }
      }, [code_package.code, code_package.language]);

    useEffect(() => {
        resetBreakpoint();
        if(breakpointList_Skip.includes(breakpoint_id.replaceAll(" ", "_")))
        {
            serialCom("STEP");//Skip this breakpoint
        }
        else
        {
            if(breakpoint_id !== "" && breakpointList.includes(breakpoint_id.replaceAll(" ", "_")))
            {
                setBreakpoint(breakpoint_id.replaceAll(" ", "_"));
            }
        }
    }, [breakpoint_id]);

    function setBreakpoint(name)
    {
        let breakpoint = document.getElementById("breakpoint_" + name);
        breakpoint.style.backgroundColor =  "black";
        breakpoint.style.color = "white";
        breakpoint.classList.add("active_breakpoint_text");
        let children = document.getElementById("breakpoint_" + name).children;
        for(let i = 0; i < children.length; i++)
        {
            if(children[i].style.color !== "")
            {//Skip span tags that have default colors, those will be modified by the wrapper's style
                children[i].style.color =  RGB_Log_Shade(0.4, children[i].style.color);
            }
            
        }
    }
    function resetBreakpoint()
    {
        if(document.getElementsByClassName("active_breakpoint_text").length !== 0)
        {//Reset previous breakpoints attributes
            let old_breakpoint = document.getElementsByClassName("active_breakpoint_text")[0];
            old_breakpoint.style.backgroundColor="lightgrey";
            old_breakpoint.style.color = "black";   
            let children = old_breakpoint.children;
            for(let i = 0; i < children.length; i++)
            {
                if(children[i].style.color !== "")
                {//Skip span tags that have default colors, those will be modified by the wrapper's style
                    children[i].style.color = children[i].dataset.color;
                }
                
            }
            old_breakpoint.classList.remove("active_breakpoint_text");
        }
    }

    function prepareBreakpoints()
    {
        let all_linenumbers = document.getElementsByClassName('linenumber');
        for(let i = 0; i < all_linenumbers.length; i++)
        {//Based on the number of characters, set the amount of padding 
         //so that all of the breakpoint boxes are equal in size
            all_linenumbers[i].style.paddingLeft = "25px";
            if(all_linenumbers[i].innerHTML.length === 1)
            {
                all_linenumbers[i].style.paddingRight = "2em";
            }
            if(all_linenumbers[i].innerHTML.length === 2)
            {
                all_linenumbers[i].style.paddingRight = "1.5em";
            }
        }
        let breakpoint_names =[];
        let container = document.getElementById("code_text_container").children[0];
        let breakpoint_index = container.innerHTML.indexOf("CircuitCheck.breakpoint(</span><span");//Javascript
        if(code_package.language === "arduino" || code_package.language === "python")
        {//This indexof has been hardcoded for the light color scheme (will need to update to use search for other color schemes)
            breakpoint_index = container.innerHTML.indexOf('CircuitCheck.</span><span style="color: rgb(0, 0, 255);">breakpoint</span><span>(</span><span');
        }
        while(breakpoint_index !== -1)
        {//Check for breakpoints
            switch(code_package.language)
            {
                case "javascript":
                    container.innerHTML = container.innerHTML.substring(0, breakpoint_index + ("CircuitCheck.breakpoint(</span><span").length) + ' id="breakpoint_marker" ' + container.innerHTML.substring(breakpoint_index + ("CircuitCheck.breakpoint(</span><span").length);
                    break;

                case "arduino":
                case "python":
                    container.innerHTML = container.innerHTML.substring(0, breakpoint_index + ('CircuitCheck.</span><span style="color: rgb(0, 0, 255);">breakpoint</span><span>(</span><span').length) + ' id="breakpoint_marker" ' + container.innerHTML.substring(breakpoint_index + ('CircuitCheck.</span><span style="color: rgb(0, 0, 255);">breakpoint</span><span>(</span><span').length);
                    break;
            }
            
            let breakpoint_name = document.getElementById("breakpoint_marker").innerHTML.replaceAll(" ", "_").replaceAll('"', ''); 
            //Ensure that all breakpoints are unique
            if(!breakpoint_names.includes(breakpoint_name))
            {
                breakpoint_names.push(breakpoint_name);

                //Modify the linenumber span to be a clickable element (will toggle the breakpoint on/off)
                let linenumber = document.getElementById("breakpoint_marker").previousSibling.previousSibling;
                if(code_package.language === "python" || code_package.language==="arduino")
                linenumber = document.getElementById("breakpoint_marker").previousSibling.previousSibling.previousSibling.previousSibling;
                linenumber.setAttribute("id", "breakpoint_selector_" + breakpoint_name);
                linenumber.style.backgroundColor = "#dc143c";
                linenumber.style.color = "white";
                linenumber.style.fontWeight =  "bold";
                linenumber.style.cursor = "pointer";
                linenumber.style.border= "1.5px solid black";

                //Create a span to wrap the entire line of code
                let wrapper = document.createElement('span');
                wrapper.setAttribute("id", "breakpoint_" + breakpoint_name);
                wrapper.style.backgroundColor = "lightgrey"
                //Place wrapper before the breakpoints text
                switch(code_package.language)
                {
                    case "javascript":
                        document.getElementById("breakpoint_marker").previousSibling.before(wrapper);
                    break;

                    case "arduino":
                    case "python":
                        document.getElementById("breakpoint_marker").previousSibling.previousSibling.previousSibling.before(wrapper);
                    break;

                }
                
                //Depending on the language used, there are a different number of children spans to
                //nest into the wrapper span. Keep adding until the next linenumber has been reached.
                let sibling = wrapper.nextSibling;
                do
                {
                    sibling.setAttribute("data-color", sibling.style.color);
                    wrapper.appendChild(sibling);
                    sibling = wrapper.nextSibling;
                    if(sibling === null) break;
                }while(!sibling.classList.contains("linenumber"));
            }
            document.getElementById("breakpoint_marker").removeAttribute("id");
            switch(code_package.language)
            {
                case "javascript":
                    breakpoint_index = container.innerHTML.indexOf("CircuitCheck.breakpoint(</span><span", breakpoint_index + 230);//224 should be the number of characters the index has moved by after adding the wrapper
                break;
                
                case "arduino":
                case "python":
                    breakpoint_index = container.innerHTML.indexOf('CircuitCheck.</span><span style="color: rgb(0, 0, 255);">breakpoint</span><span>(</span><span', breakpoint_index + 230);
                break;
            }

        }

        for(let i = 0; i < breakpoint_names.length; i++)
        {//Add event listeners to all of the breakpoints
            document.getElementById("breakpoint_selector_" + breakpoint_names[i]).addEventListener("click", () => toggleBreakpoint(breakpoint_names[i]));
        }
        setBreakpointList([...breakpoint_names]);
    }
    function toggleBreakpoint(breakpoint_name)
    {
        setBreakpointList_Skip(breakpointList_Skip =>
            {
                let linenumber = document.getElementById("breakpoint_selector_" + breakpoint_name);
                if(breakpointList_Skip.includes(breakpoint_name))
                {
                    linenumber.style.backgroundColor = "#dc143c";
                    linenumber.style.color = "white";
                    linenumber.style.fontWeight =  "bold";
                    breakpointList_Skip.splice(breakpointList_Skip.indexOf(breakpoint_name), 1);
                    return [...breakpointList_Skip];
                }
                else
                {
                    linenumber.style.backgroundColor = "white";
                    linenumber.style.color = "black";
                    linenumber.style.fontWeight =  "normal";
                    return [...breakpointList_Skip.concat(breakpoint_name)];
                }
            }
        );
    }


    //function for brightening colors, taken from : https://stackoverflow.com/questions/5560248/programmatically-lighten-or-darken-a-hex-color-or-rgb-and-blend-colors
    const RGB_Log_Shade=(p,c)=>{
        var i=parseInt,r=Math.round,[a,b,c,d]=c.split(","),P=p<0,t=P?0:p*255**2,P=P?1+p:1-p;
        return"rgb"+(d?"a(":"(")+r((P*i(a[3]=="a"?a.slice(5):a.slice(4))**2+t)**0.5)+","+r((P*i(b)**2+t)**0.5)+","+r((P*i(c)**2+t)**0.5)+(d?","+d:")");
    }
  return (
      <>    
        <svg 
            width={width} 
            viewBox={`0 0 ${width} ${height}`}
            x={x}
            y={y}
            preserveAspectRatio="xMinYMin"
            id="svg_code_container"
        >
            <foreignObject x={0} y={40} width="100%" height={height - 40} style={{overflow: "auto", borderTop: "2px solid black"}}>
                <SyntaxHighlighter language={code_package.language} style={vs} showLineNumbers={true} id="code_text_container" customStyle={{overflow: "visible" }}>
                    {code_package.code}
                </SyntaxHighlighter>
            </foreignObject>

            {/* Controls for selecting programming language & increasing/decreasing font */}
            <rect x={width-40} y={0} width={35} height={35} fill="white" stroke="black" strokeWidth="2" rx={5} ry={5}/>
            <text x={width-22.5} y={20} dominantBaseline="middle" textAnchor="middle" fill="black" style={{fontSize:"1.5em", fontWeight:"bolder"}}>A-</text>
            <rect x={width-40} y={0} width={35} height={35} fill="transparent" stroke="transparent" strokeWidth="2" rx={5} ry={5} style={{cursor:"pointer"}} onClick={() => {let container = document.getElementById("code_text_container"); if(container !== null){container.style.fontSize = container.style.fontSize === "" ? "0.9em" :`${parseFloat(container.style.fontSize.replace("em", "")) - 0.1}em`}}}>
                <title>Decrease font</title>    
            </rect>

            <rect x={width-80} y={0} width={35} height={35} fill="white" stroke="black" strokeWidth="2" rx={5} ry={5}/>
            <text x={width-62.5} y={20} dominantBaseline="middle" textAnchor="middle" fill="black" style={{fontSize:"1.5em", fontWeight:"bolder"}}>A+</text>
            <rect x={width-80} y={0} width={35} height={35} fill="transparent" stroke="transparent" strokeWidth="2" rx={5} ry={5} style={{cursor:"pointer"}} onClick={() => { let container = document.getElementById("code_text_container"); if(container !== null){container.style.fontSize = container.style.fontSize === "" ? "1.1em" :  `${parseFloat(container.style.fontSize.replace("em", "")) + 0.1}em`}}}>
                <title>Increase font</title>    
            </rect>
            
        </svg>
     </>

    );
}

export default Code_Display_Text