import React, {useState, useEffect} from "react";
import Variable_Field from "./Variable_Field.js";


function Variables_Display({ serialCom, x, y, width, height, variableData, useVerticalLayout}) {

  
  let var_names = [];
  if(variableData !== undefined && Object.keys(variableData).length !== 0)
  {
      var_names = Object.keys(variableData).sort();
  }
  /*
  let [var_names, setVarNames] = useState([]);//Sort the variable names alphabeticall

  useEffect(() =>{//Still no clue on how to properly set this up and ensure the value is correct!
    if(variableData !== undefined && Object.keys(variableData).length !== 0){//Ensure variable data isn't empty
        setVarNames(Object.keys(variableData).sort());//Update if prop changes 
    }
  },[variableData]);*/
  let table = [];
  for(let i = 0; i < var_names.length; i++)
  {
      let b_color = i % 2 === 0 ? "#E8E8E8" : "white";
      table.push(<>
          <tr style={{height: "30px", fontSize:"1.75em", backgroundColor: b_color}}>
            <td style={{width:"40%", padding:"5px", fontWeight:"bold"}}>{var_names[(i)]}</td>
            {/* Add a pop-up/warning message to handle variable updates <td style={{width:"20%", fontWeight:"bold"}}>{variableData[var_names[(i)]].type == 1 ? "Integer" : variableData[var_names[(i)]].type == 2 ? "Decimal" : variableData[var_names[(i)]].type == 3 ? "String" : "Boolean"}</td> */}
            <td style={{width:"60%", padding:"5px"}}><Variable_Field starting_value={variableData[var_names[(i)]].value} name={var_names[(i)]} type={variableData[var_names[(i)]].type} update={serialCom}/></td>
          </tr>
      </>);
  }

  /*The display will only show, at most, 6 variables at a time    :*/
  return (
    <>
      <svg
        viewBox={`0 0 ${width} ${height}`}
        width={width}
        x={x}
        y={y}
        preserveAspectRatio="xMinYMin"
        id="svg_variables"
        >
          {/* Variables Table */}
          {table.length !== 0 ? 
            <>
              <foreignObject  width={width} height={height} x={0} y={0} style={{overflowY: "auto", overflowX:"auto"}}> 
                <table width="100%" style={{margin:"0 auto", borderCollapse:"collapse"}} >
                  {table}
                </table>
              </foreignObject>
            </> :
            <>
              <text x={width * 0.5} y={height * 0.5}  fill="black" dominantBaseline="middle" textAnchor="middle" style={{fontSize:"3em", fontWeight:"bolder"}}>
                No Variables
              </text>
            </>}

        </svg>
      </>  
    )
}

export default Variables_Display