
import React, { useState, useEffect } from 'react';
import HardwarePin from "../../PinControls/Pins/HardwarePin.js";
import Led from "../../PinControls/Actuators/Led.js";
import Button from "../../PinControls/Sensors/Button.js";
import LedArray_Selector from "../../PinControls/Actuators/LedArray_Selector.js";
import Gesture from "../../PinControls/Sensors/Gesture.js"
import Temp from "../../PinControls/Sensors/Temp.js";
import Neopixel from "../../PinControls/Actuators/NeoPixel.js";
import Speaker from "../../PinControls/Actuators/Speaker.js";
import GenericSensor from "../../PinControls/Generics/GenericSensor.js";
import Capacitive from "../../PinControls/Sensors/Capacitive.js";
import Multi_SmoothLineGraph from "../../PinControls/GraphingUtilities/Multi_SmoothLineGraph.js"


function Sensor_Display({pinList, selectedSensor, selectedPin, setSelectedPin, x, y, width, height, serialCom, deviceData, modeText="PinMode", modes=["INPUT", "OUTPUT", "INPUT PULLUP"], useVerticalLayout}) {

    let display = <> </>;
    let logo = <> </>;
    let data;
    let reading;
    try{
        if(selectedSensor.startsWith("E_"))
        {
            selectedSensor = selectedSensor.substr(2);
        }
        switch(selectedSensor){

            case "Pins" :
                //Update to use a PIN component
                display =<><HardwarePin x={x} y={y} download={prepDownload} selectedPin={selectedPin} setSelectedPin={setSelectedPin} width={width} height={height} serialCom={serialCom} deviceData={deviceData} modeText={modeText} modes={modes} pinList={pinList} useVerticalLayout={useVerticalLayout}/></>; 
                break;

            case "LED" :
                //Update to use a PIN component
                display =<><Led x={x} y={y} download={prepDownload} selectedPin={selectedPin} setSelectedPin={setSelectedPin} width={width} height={height} serialCom={serialCom} deviceData={deviceData} pinList={pinList} useVerticalLayout={useVerticalLayout}/></>; 
                break;

            case "Button" :
                //Update to use a PIN component
                display =<><Button x={x} y={y} download={prepDownload} selectedPin={selectedPin} setSelectedPin={setSelectedPin} width={width} height={height} serialCom={serialCom} deviceData={deviceData} pinList={pinList} useVerticalLayout={useVerticalLayout}/></>; 
                break;

            case "Motion":
                data = deviceData.motion.slice(-25);
                reading = {X: [], Y: [], Z:[]};
                if('Strength' in data)
                {
                    reading.Strength = [];
                    for(let i = 0; i < data.length; i++)
                    {
                        reading.X.push(data[i].X);
                        reading.Y.push(data[i].Y);
                        reading.Z.push(data[i].Z);
                        reading.Strength.push(data[i].Strength);
                    }
                }
                else{
                    for(let i = 0; i < data.length; i++)
                    {
                        reading.X.push(data[i].X);
                        reading.Y.push(data[i].Y);
                        reading.Z.push(data[i].Z);
                    }
                }
                
                logo =  <svg viewBox="0 0 480 480" x={7.5} y={22.5} width={85} preserveAspectRatio="xMinYMin">
                        <defs>
                            <clipPath id="clip0">
                                <path d="M0 0 480 0 480 480 0 480Z" fill-rule="evenodd" clip-rule="evenodd"/>
                            </clipPath>
                        </defs>
                        <g clip-path="url(#clip0)">
                            <path d="M216.5 240.5C216.5 227.245 227.245 216.5 240.5 216.5 253.755 216.5 264.5 227.245 264.5 240.5 264.5 253.755 253.755 264.5 240.5 264.5 227.245 264.5 216.5 253.755 216.5 240.5Z" stroke="#FFFFFF" stroke-miterlimit="8" fill="#FFFFFF" fill-rule="evenodd"/>
                            <path d="M10-8.10084e-06 10.0001 79.6-9.99994 79.6-10 8.10084e-06ZM30.0001 69.6 0.000104987 129.6-29.9999 69.6Z" fill="#FFFFFF" transform="matrix(1 0 0 -1 240 167.6)"/>
                            <path d="M10-8.10084e-06 10.0001 79.6-9.99994 79.6-10 8.10084e-06ZM30.0001 69.6 0.000104987 129.6-29.9999 69.6Z" fill="#FFFFFF" transform="matrix(-0.707107 0.707107 0.707107 0.707107 290.912 290.912)"/>
                            <path d="M10-8.10084e-06 10.0001 79.6-9.99994 79.6-10 8.10084e-06ZM30.0001 69.6 0.000104987 129.6-29.9999 69.6Z" fill="#FFFFFF" transform="matrix(-0.707107 -0.707107 -0.707107 0.707107 189.625 290.376)"/>
                        </g>
                    </svg>;
                display =<><Multi_SmoothLineGraph download={prepDownload} logo={logo} name="Motion" useVerticalLayout={useVerticalLayout} x={x} y={y} width={width} height={height} reading={reading} /></>; //Modified for CP Express
                break;

            case "Magnet":
                data = deviceData.magnet.slice(-25);
                reading = {X: [], Y: [], Z:[], Strength:[]};
                for(let i = 0; i < data.length; i++)
                {
                    reading.X.push(data[i].X);
                    reading.Y.push(data[i].Y);
                    reading.Z.push(data[i].Z);
                    reading.Strength.push(data[i].Strength);
                }
                logo =  <svg viewBox="0 0 50 75" x={15} y={22.5} width={65} preserveAspectRatio="xMinYMin">
                            <text x={27.5} y={35} textAnchor="middle" alignmentBaseline="middle" dominantBaseline="middle" fill="white" style={{fontSize: "4.5em", fontWeight: "bolder"}}>U</text>
                            <line x1={0} x2={50} y1={13.5} y2={13.5} stroke="var(--bgColor1)" strokeWidth={4}/>
                        </svg>;
                display =<><Multi_SmoothLineGraph download={prepDownload} logo={logo} name="Magnet" useVerticalLayout={useVerticalLayout} x={x} y={y} width={width} height={height} reading={reading}  max={1024} min={-1024}/></>; 
                break;

            case "Compass":
                logo =<svg viewBox="0 0 50 75" x={20} y={22.5} width={55} preserveAspectRatio="xMinYMin">
                        <line x1={25} x2={25} y1={5} y2={30} stroke="white" strokeWidth={6} />
                        <line x1={25} x2={35} y1={5} y2={15} stroke="white" strokeWidth={6} strokeLinecap="square"/>
                        <line x1={25} x2={15} y1={5} y2={15} stroke="white" strokeWidth={6} strokeLinecap="square"/>
                        <text x={25} y={62.5} textAnchor="middle" fill="white" style={{fontSize: "2.5em", fontWeight: "bolder"}}>N</text>
                        </svg>;
                
                display=  <GenericSensor download={prepDownload} useVerticalLayout={useVerticalLayout} sensor_name="Compass" font_size="1.25em" x={x} y={y} width={width} height={height} logo={logo} serialCom={serialCom} reading={deviceData.compass} max={359} min={0} />;
                break;

            case "Rotation":
                reading = {Roll: [], Pitch: []};
                deviceData.rotation.slice(-25).forEach(data_point =>{
                    reading.Roll.push(data_point.Roll);
                    reading.Pitch.push(data_point.Pitch);
                });
                logo =  <svg viewBox="0 0 50 50" x={7.5} y={20} width={85} preserveAspectRatio="xMinYMin">
                            <circle cx={25} cy={25} r={15} fill="var(--bgColor1)" stroke="white" strokeWidth={4}/>
                            <rect x={25} y={30} width={20} height={20} fill="var(--bgColor1)"/>
                            <line x1={40} x2={47.5} y1={32.5} y2={25} stroke="white" strokeWidth={4} strokeLinecap="square"/>
                            <line x1={40} x2={32.5} y1={32.5} y2={25} stroke="white" strokeWidth={4} strokeLinecap="square"/>
                        </svg>;
                display =<><Multi_SmoothLineGraph download={prepDownload} logo={logo} name="Rotation" useVerticalLayout={useVerticalLayout} x={x} y={y} width={width} height={height} reading={reading} max={180} min={-180}/></>; 
                break;

            case "Light":
                logo =<> 
                        <path d="M24,25 C24,60 84,60 84,25" fill="var(--bgColor1)" stroke="white" strokeWidth={6} />
                        <line x1={54} x2={54} y1={65} y2={85} stroke="white" strokeWidth={6} strokeLinecap="round" />
                        <line x1={74} x2={84} y1={60} y2={75} stroke="white" strokeWidth={6} strokeLinecap="round" />
                        <line x1={34} x2={24} y1={60} y2={75} stroke="white" strokeWidth={6} strokeLinecap="round" />
                    </>;
                
                display=  <GenericSensor download={prepDownload} useVerticalLayout={useVerticalLayout} sensor_name="Light" x={x} y={y} width={width} height={height} logo={logo} serialCom={serialCom} reading={deviceData.light} max={255} min={0} />;
                break;

            case "Temp":
                display =<><Temp x={x} y={y} download={prepPackagedData} width={width} useVerticalLayout={useVerticalLayout} height={height} serialCom={serialCom} deviceData={deviceData}/></>; 
                break;

            case "Gesture":
                display= <Gesture x={x} y={y} download={prepDownload} useVerticalLayout={useVerticalLayout} width={width} height={height} gesture={deviceData.gesture[deviceData.gesture.length - 1]} />
                break;

            case "Led Matrix"://Todo handle led_settings being passed in
                display= <LedArray_Selector useVerticalLayout={useVerticalLayout} x={x} y={y}  width={width} serialCom={serialCom} led_settings={deviceData.led_array}/>;
                break;

            case "Microphone":
                logo =<svg viewBox="0 0 50 50" x={25} y={30 } width={55} preserveAspectRatio="xMinYMin">
                        <rect x={5} y={5} rx={5} ry={5} width={40} height={40} fill="transparent" stroke="white" strokeWidth={4} />
                        </svg>;
                display=  <GenericSensor download={prepDownload} useVerticalLayout={useVerticalLayout} sensor_name={selectedSensor} font_size="1.25em" x={x} y={y} width={width} height={height} logo={logo} serialCom={serialCom} reading={deviceData.microphone} max={1023} min={-1023} />;
                break;

            case "Neopixel":
                display = <Neopixel x={x} y={y} width={width} useVerticalLayout={useVerticalLayout} height={height} serialCom={serialCom} deviceData={deviceData} numPixels={10}/>;
                break;

            case "Speaker":
                display = <Speaker x={x} y={y} width={width} useVerticalLayout={useVerticalLayout} height={height} serialCom={serialCom} />;
                break;
            
            case "Capacitive":
                display =<><Capacitive x={x} y={y} download={prepDownload} selectedPin={selectedPin} setSelectedPin={setSelectedPin} width={width} height={height} serialCom={serialCom} deviceData={deviceData} pinList={pinList} font_size="1.25em"  useVerticalLayout={useVerticalLayout}/></>; 
                break;

            default:
                logo =<svg viewBox="0 0 50 50" x={25} y={30 } width={55} preserveAspectRatio="xMinYMin">
                        <rect x={5} y={5} rx={5} ry={5} width={40} height={40} fill="transparent" stroke="white" strokeWidth={4} />
                      </svg>;
                display=  <GenericSensor download={prepDownload} useVerticalLayout={useVerticalLayout} sensor_name={selectedSensor} font_size="1.25em" x={x} y={y} width={width} height={height} logo={logo} serialCom={serialCom} reading={deviceData.external_sensor_values[selectedSensor]} max={1023} min={-1023} />;
                break;    
        }
    }
    catch(e){
        console.log("Error in displaying sensor data: ");
        console.log(e);
    }

    //Convert a sensor's data from deviceData into csv format and download to the user's computer
    function prepDownload(sensor_name, dimension="") {
        let csvString= "No Data/Error";
        let data = deviceData[sensor_name.toLowerCase()];
        
        if(deviceData.external_sensor_values.hasOwnProperty(sensor_name))
        {//Check if the data belongs to an external sensor
            data = deviceData.external_sensor_values[sensor_name]
        }
        if(dimension !== "")
        {//Check if a specific dimension has been chosen
            data = data[dimension];
        }

        if(data.length > 0)
        {
            if(Object.keys(data[0]).length > 1)
            {
                csvString = "ID";
                let dim_names = Object.keys(data[0]);
                for(let i = 0; i < dim_names.length; i++)
                {//Create a header row for the dimensions of the data point
                    csvString += "," + dim_names[i];
                }
                csvString += "\n";
                for(let data_index = 0; data_index < data.length; data_index++)
                {//Add in all of the data for each
                    csvString += String(data_index);
                    for(let dim_index = 0; dim_index < dim_names.length; dim_index++)
                    {//Create a header row for the dimensions of the data point
                        csvString += "," + data[data_index][dim_names[dim_index]];
                    } 
                    csvString += "\n";
                } 
            }
            else
            {
                csvString = "ID,Data Point\n";
                for(let i = 0; i < data.length; i++)
                {
                    csvString += String(i) + "," + String(data[i]) +"\n";
                } 
            }
        }
        downloadCSV(sensor_name + (dimension !== "" ? "_" + dimension : "") ,csvString);
      }

      function prepPackagedData(file_name, data) {
        let csvString= "No Data/Error";
        if(data.length > 0)
        {
            if(Object.keys(data[0]).length > 1)
            {
                csvString = "ID";
                let dim_names = Object.keys(data[0]);
                for(let i = 0; i < dim_names.length; i++)
                {//Create a header row for the dimensions of the data point
                    csvString += "," + dim_names[i];
                }
                csvString += "\n";
                for(let data_index = 0; data_index < data.length; data_index++)
                {//Add in all of the data for each
                    csvString += String(data_index);
                    for(let dim_index = 0; dim_index < dim_names.length; dim_index++)
                    {//Create a header row for the dimensions of the data point
                        csvString += "," + data[data_index][dim_names[dim_index]];
                    } 
                    csvString += "\n";
                } 
            }
            else
            {
                csvString = "ID,Data Point\n";
                for(let i = 0; i < data.length; i++)
                {
                    csvString += String(i) + "," + String(data[i]) +"\n";
                } 
            }
        }
        downloadCSV(file_name, csvString);
      }

      function downloadCSV(file_name, csvString)
      {
        var blob = new Blob([csvString]);
        if (window.navigator.msSaveOrOpenBlob){
          window.navigator.msSaveBlob(blob, file_name+".csv");
        }
        else {
          var hidden_anchor = window.document.createElement("a");
          hidden_anchor.href = window.URL.createObjectURL(blob, {
            type: "text/plain"
          });
          hidden_anchor.download = file_name +".csv";
          document.body.appendChild(hidden_anchor);
          hidden_anchor.click();
          document.body.removeChild(hidden_anchor);
        }
      }

  return (
      <>    
        {display}
     </>

    );
}

export default Sensor_Display