import React, {useState} from "react";

//Todo determine functionality for switching between the breakpoint types (step, timed_interval, and runner)
//Idea: all handled here, create a step method which is either called on a button press (stepper), done by interval, or whenever the breakpoint_id has been updated
function Breakpoint_Controls({breakpoint_id="Breakpoint ID", serialCom, x, y, height, width, isWideLayout=true, useVerticalLayout=false}) {
    let [interval_id, setIntervalId] = useState(null); 
    let [chosen_stepper, setChosenStepper] = useState("STEPPER");
    let breakpoint_text = breakpoint_id;

    if(breakpoint_id.length > 25)
    {
        breakpoint_text =  breakpoint_id.substring(0, 25) + "..."; 
    }

    let mask = <> </>;
    if(chosen_stepper === "TIMER")
    {
       mask = <> 
                <rect x={335.5} y={5} width={52} height={40} fill="white" opacity={0.75}>
                    <title>Step</title>
                </rect>
                <rect x={442.1} y={5} width={52} height={40} fill="white" opacity={0.75}>
                    <title>Run (Turn Breakpoints Off)</title>
                </rect>
              </>;
    }
    else if(chosen_stepper === "RUNNER")
    {
        mask = <> 
                <rect x={335.5} y={5} width={52} height={40}fill="white" opacity={0.75}>
                    <title>Step</title>
                </rect>
                <rect x={388.8} y={5} width={52} height={40} fill="white" opacity={0.75}>
                    <title>Timed Intervals</title>
                </rect>
            </>;
    }

    //Stylize controls on hover
    function mHover(rect_id, svg_class){
        document.getElementById(rect_id).setAttribute("fill", document.getElementById(rect_id).getAttribute("fill") == "white" ? "black" : "white");
        document.querySelectorAll("." + svg_class).forEach(element =>{
                element.setAttribute("stroke", element.getAttribute("stroke") == "white" ? "black" : element.getAttribute("stroke") == "transparent" ? "transparent" : "white");
                element.setAttribute("fill", element.getAttribute("fill") == "white" ? "black" : "white");
            }
        );
    }

    function stepper(){
        setIntervalId(interval_id =>{
            if(interval_id !== null)
            {
                clearInterval(interval_id);
            }
            let id = setInterval(() => serialCom("STEP"), 2000);
            return id;
        });
    }

      return (
        <>
          <svg
            viewBox={`0 0 500 50`}
            height={height}
            width={width}
            x={x}
            y={y}
            preserveAspectRatio="xMinYMin"
            >
                {/* Background */}
                <rect x={5} y={5} rx="5" ry="5" width={490} height={40} fill="white" />
                
                {/* Stepper */}
                <rect id="rect_step" x={335.5} y={6}  width={52} height={40} fill="white" />
                <svg viewBox="0 0 24 24" width="35" height="35" x={344} y={4} ><path class="svg_step" transform='rotate(180) translate(-24, -24)' strokeWidth="1" stroke="black" d="M10.53 5.03a.75.75 0 10-1.06-1.06l-6.25 6.25a.75.75 0 000 1.06l6.25 6.25a.75.75 0 101.06-1.06L5.56 11.5H17a3.248 3.248 0 013.25 3.248v4.502a.75.75 0 001.5 0v-4.502A4.748 4.748 0 0017 10H5.56l4.97-4.97z"></path></svg>
                <rect id="selector_step" x={335.5} y={38}  width={52} height={7} fill={chosen_stepper === "STEPPER" ? "var(--bgColor2)" : "white"} />

                {/* Slow-mo /Timer based*/}
                <rect id="rect_interval" x={388.8} y={6}  width={52} height={40} fill="white" />
                <svg viewBox="0 0 24 24" width="25" height="25" x={401.3} y={9.5} ><path class="svg_interval" fill-rule="evenodd" strokeWidth="1" stroke="black" d="M10.25 0a.75.75 0 000 1.5h1v1.278a9.955 9.955 0 00-5.635 2.276L4.28 3.72a.75.75 0 00-1.06 1.06l1.315 1.316A9.962 9.962 0 002 12.75c0 5.523 4.477 10 10 10s10-4.477 10-10a9.962 9.962 0 00-2.535-6.654L20.78 4.78a.75.75 0 00-1.06-1.06l-1.334 1.334a9.955 9.955 0 00-5.636-2.276V1.5h1a.75.75 0 000-1.5h-3.5zM12 21.25a8.5 8.5 0 100-17 8.5 8.5 0 000 17zm4.03-12.53a.75.75 0 010 1.06l-2.381 2.382a1.75 1.75 0 11-1.06-1.06l2.38-2.382a.75.75 0 011.061 0z"></path></svg>
                <rect id="selector_interval" x={388.8} y={38} width={52} height={7} fill={chosen_stepper === "TIMER" ? "var(--bgColor2)" : "white"} />
                
                {/* Run */}    
                <rect id="rect_run" x={442.5} y={6}  width={52} height={38} fill="white"/>
                <circle className="svg_run" r={11} cx={468} cy={21} fill="white" stroke="black" strokeWidth={3} />
                <line className="svg_run" x1={457} x2={479} y1={21} y2={21} stroke="black" strokeWidth={3} />
                <rect id="selector_run" x={442.1} y={38}  width={52} height={7} fill={chosen_stepper === "RUNNER" ? "var(--bgColor2)" : "white"}  />
                
                {/* Line dividers*/}
                <line x1={335} x2={495} y1={38} y2={38} stroke="black" strokeWidth="2"/> 
                <line x1={335} x2={335} y1={5} y2={45} stroke="black" strokeWidth="2"/> 
                <line x1={388.3} x2={388.3} y1={5} y2={45} stroke="black" strokeWidth="2"/>                
                <line x1={441.6} x2={441.6} y1={5} y2={45} stroke="black" strokeWidth="2"/> 

                {/* Transparent Overlays */}
                <rect x={5} y={5} rx="5" ry="5" width={490} height={40} fill="transparent" stroke="black" strokeWidth="2" />
                <rect x={335.5} y={5} width={52} height={40} fill="transparent" style={{cursor:"pointer"}} onMouseEnter={() => mHover("rect_step", "svg_step")} onMouseLeave={() => mHover("rect_step", "svg_step")} onClick={()=> {setIntervalId(interval_id => { if(interval_id !== null)clearInterval(interval_id); return null});serialCom("STEP")}}>
                    <title>Step</title>
                </rect>
                <rect x={388.8} y={5} width={52} height={40} fill="transparent" style={{cursor:"pointer"}} onMouseEnter={() => mHover("rect_interval", "svg_interval")} onMouseLeave={() => mHover("rect_interval", "svg_interval")} onClick={()=>{setChosenStepper(chosen_stepper => {if(chosen_stepper === "STEPPER"){stepper(); return "TIMER";}else{setIntervalId(interval_id => { if(interval_id !== null)clearInterval(interval_id); return null}); return "STEPPER"}});}}>
                    <title>Timed Intervals</title>
                </rect>
                <rect x={442.1} y={5} width={52} height={40} fill="transparent" style={{cursor:"pointer"}} onMouseEnter={() => mHover("rect_run", "svg_run")} onMouseLeave={() => mHover("rect_run", "svg_run")} onClick={()=> {setIntervalId(interval_id => { if(interval_id !== null)clearInterval(interval_id); return null}); setChosenStepper(chosen_stepper => {return chosen_stepper === "STEPPER" ? "RUNNER" : "STEPPER"}); serialCom("STEP_NO_STOP")}}>
                    <title>Run (Turn Breakpoints Off)</title>
                </rect>
                {mask}

                {/* Breakpoint text */}
                <text x={15} y={27.5} dominantBaseline="middle" fill="black" style={{fontWeight:"bolder", fontSize:"1.5em"}}>
                    {breakpoint_text}
                    <title>{breakpoint_id}</title>
                </text>
            </svg>
         </>
            
    )
}

export default Breakpoint_Controls