
import React, { useState, useEffect } from 'react';
import SmoothLineGraph from "../GraphingUtilities/SmoothLineGraph.js";

function Temp({x, y, width, deviceData, download, useVerticalLayout, sourceType="C", conversionMethod=(temp, type)=>{return type=="F" ? Math.round(((temp * 9/5) + 32) * 100)/100 : temp + 273.15;}}) {
    const [degrees, setDegrees] = useState(sourceType);
    const [maxTemp, setMaxTemp] = useState(null);
    const [minTemp, setMinTemp] = useState(null);

    //Determine what the other degree units are
    let tempData =[]; 
    let temp_graph = <></>;
    if(deviceData.temp.length > 0)
    {
        if(maxTemp === null){
            setMaxTemp(deviceData.temp[0])
        }
        else if(deviceData.temp[deviceData.temp.length - 1] > maxTemp)
        {
            setMaxTemp(deviceData.temp[deviceData.temp.length - 1]);
        }
        if(minTemp === null){
            setMinTemp(deviceData.temp[0])
        }
        else if(deviceData.temp[deviceData.temp.length - 1] < minTemp)
        {
            setMinTemp(deviceData.temp[deviceData.temp.length - 1]);
        }
        let preConversionData = deviceData.temp.slice(-25);
        if(degrees === sourceType)
        {
            tempData = preConversionData;
        }
        else
        {
            for(let i = 0; i < preConversionData.length; i++)
            {
                tempData.push(conversionMethod(preConversionData[i], degrees));//Convert each data point into the appropriate system (C or F)
                if(maxTemp === null){
                    if(preConversionData[i] > maxTemp)
                    {
                        setMaxTemp(preConversionData[i]);
                    }
                }
                if(minTemp === null){
                    if(preConversionData[i] < minTemp)
                    {
                        setMinTemp(preConversionData[i]);
                    }
                }
            }
        }

        temp_graph = <SmoothLineGraph x={300} y={25.5} height={99} download={()=>prepDownload()}  width={275} x_axis_offset={10} y_axis_offset={12.5} data={tempData} data_max={ degrees === sourceType ? maxTemp : conversionMethod(maxTemp, degrees)} data_min={minTemp === maxTemp ? 0 : degrees === sourceType ? minTemp : conversionMethod(minTemp, degrees)} reset_min_max={reset_min_max} />
    }

    function prepDownload(){
        let converted_data = [];
        deviceData.temp.forEach(temperature => 
                                    {   
                                        converted_data.push(conversionMethod(temperature, degrees));
                                    }
                                );
        download("Temperature_" + degrees, converted_data);
    }

    function mHover(class_name, attribute, colorA, colorB){
        document.querySelectorAll("." + class_name).forEach(element =>{
                element.setAttribute(attribute, element.getAttribute(attribute) == colorA ? colorB : colorA);
            }
        );
    }

    function reset_min_max(){
        setMaxTemp(deviceData.temp.length > 0 ? Math.max(...deviceData.temp.slice(-25)) : 0);
        setMinTemp(deviceData.temp.length > 0 ? Math.min(...deviceData.temp.slice(-25)) : 0);
    }

  return (
      <>
        <svg 
            width={width} 
            viewBox={"0 0 500 150"}
            x={x}
            y={y}
            preserveAspectRatio="xMinYMin"
        >   
        
        {/* Logo   
        <line x1={100} x2={100} y1={25} y2={125} stroke="white" strokeWidth="4" />
        <circle cx={50} cy={75} r={15} fill="white" stroke="white" strokeWidth={4}/>
        <rect x={42.5} y={25} width={15} height={50} rx={5} ry={5} fill="white" stroke="white" strokeWidth={4}/>
        <circle cx={50} cy={75} r={9} fill="var(--bgColor1)" />fontSize: deviceData.temp.length > 0 ? tempData[tempData.length-1].toString().length > 5 ? "1.5em" : tempData[tempData.length-1].toString().length > 3 ? "1.75em" : "2.5em": "2.5em"
        <text x={50} y={110} dominantBaseline="middle" textAnchor="middle" fill="white" style={{fontSize:"2.25em", fontWeight:"bolder"}}>Temp</text>
        */}
        <g transform ={useVerticalLayout ? "scale(0.9) translate(30 -15)": ""}>
            <g transform ={useVerticalLayout ? "scale(1.5)": "translate(-100)"}>
                {/* Current Temp*/}
                <rect x={125} y={25} height={65} width={100} fill="white" stroke="black" strokeWidth="2" />
                <text x={175} y={62.5} dominantBaseline="middle" textAnchor="middle" fill="black" style={{fontSize: `${(tempData.length > 0 && tempData[tempData.length-1] !== undefined) ? (8 / tempData[tempData.length-1].toFixed(2).length).toFixed(2) : "2" }em`, fontWeight:"bold"}}>{deviceData.temp.length > 0 ? tempData[tempData.length-1].toFixed(2) + degrees + "" : "NA"}</text>
                
                {/* Choose Celsius*/}
                <rect className="select_degree_1" x={125} y={90} height={35} width={33.3} fill={degrees==="C" ? "black": "white"} stroke="black" strokeWidth="2" />
                <text className="select_degree_1" x={141.65} y={110} dominantBaseline="middle" textAnchor="middle" fill={degrees==="C" ? "white": "black"}  style={{fontSize:"1.25em", fontWeight:"bold"}}>C</text>
                <rect x={125} y={90} height={35} width={33.3} fill="transparent" style={{cursor: degrees === "C" ? "default" : "pointer"}} onMouseEnter={()=>{if(degrees !== "C")mHover("select_degree_1", "fill","black", "white")}} onMouseLeave={()=>{if(degrees !== "C")mHover("select_degree_1", "fill","black", "white")}} onClick={()=>{setDegrees("C")}}>
                    <title> Select for Celsius</title>    
                </rect>

                {/* Choose Fahrenheit */} 
                <rect className="select_degree_2" x={158.3} y={90} height={35} width={33.3} fill={degrees==="F" ? "black": "white"}  stroke="black" strokeWidth="2" />
                <text className="select_degree_2" x={174.95} y={110} dominantBaseline="middle" textAnchor="middle" fill={degrees==="F" ? "white": "black"}  style={{fontSize:"1.25em", fontWeight:"bold"}}>F</text>
                <rect x={158.3} y={90} height={35} width={33.3} fill="transparent" style={{cursor:degrees === "F" ? "default" : "pointer"}} onMouseEnter={()=>{if(degrees !== "F")mHover("select_degree_2", "fill","black", "white")}} onMouseLeave={()=>{if(degrees !== "F")mHover("select_degree_2", "fill","black", "white")}} onClick={()=>{setDegrees("F")}}>
                    <title> Select for Fahrenheit</title>    
                </rect>  

                {/* Choose Kelvin */} 
                <rect className="select_degree_3" x={191.6} y={90} height={35} width={33.3} fill={degrees==="K" ? "black": "white"}  stroke="black" strokeWidth="2" />
                <text className="select_degree_3" x={208.25} y={110} dominantBaseline="middle" textAnchor="middle" fill={degrees==="K" ? "white": "black"}  style={{fontSize:"1.25em", fontWeight:"bold"}}>K</text>
                <rect x={191.6} y={90} height={35} width={33.3} fill="transparent" style={{cursor:degrees === "K" ? "default": "pointer"}} onMouseEnter={()=>{if(degrees !== "K")mHover("select_degree_3", "fill","black", "white")}} onMouseLeave={()=>{if(degrees !== "K")mHover("select_degree_3", "fill","black", "white")}} onClick={()=>{setDegrees("K")}}>
                    <title> Select for Kelvin</title>    
                </rect>  
            </g>
            <g transform ={useVerticalLayout ? "translate(-250 165) scale(1.25)": "translate(-100)"}>
                {/* Data Graph */}
                <rect  x={230} y={25} height={100} width={355}  fill="white" stroke="black" strokeWidth="2" />
                {temp_graph}
            </g>
          </g>
        </svg> 
     </>

    );
}

export default Temp