
import React, {useState, useEffect} from "react";
import SmoothLineGraph from "./SmoothLineGraph.js"
////Max of 4 data points!
//Todo create symbols for split/combine
function Multi_SmoothLineGraph({name, logo, x, y, width, height, reading, max, min, useVerticalLayout, download}) {

  const [dataToDisplay, setDataToDisplay] = useState([Object.keys(reading)[0]]);//Start with only 1
  const [combineGraphs, setCombineGraphs] = useState(false);
  const [maxVal, setMaxVal] = useState(null);
  const [minVal, setMinVal] = useState(null);

  useEffect(()=>{
    resetMaxMin();
    setDataToDisplay([Object.keys(reading)[0]]);
  }, [Object.keys(reading)[0], name]);
  setMaxMin();
  
  let dataGroupings = Object.keys(reading);
  let color_pallete = ["#003f5c", "#ef5675", "#7a5195", "#ffa600"]
  let dash_selection = ["none", "8 4 2 4", "4", "16 4"]
  let colors = {};
  let dashes = {};
  for(let i = 0; i < dataGroupings.length; i++)
  {
    colors[dataGroupings[i]] = color_pallete[i];
    dashes[dataGroupings[i]] = dash_selection[i];
  }
  
  let selectors = <></>;
  switch(dataGroupings.length)
  {
    case 2:
      selectors =<>
                  <rect className={`${dataGroupings[0]}_selector`} x={115} y={25} height={25} width={100} fill={"white"}stroke="black" strokeWidth="2" />
                  <text className={`${dataGroupings[0]}_selector`} x={165} y={40} dominantBaseline="middle" textAnchor="middle" fill="black" style={{fontSize:"1.3em", fontWeight:"bold"}}>{dataGroupings[0]}</text>
                  <rect x={115} y={25} height={25} width={100} fill="transparent" stroke="black" strokeWidth="2" style={{cursor:"pointer"}} onMouseLeave={()=>mHover(`${dataGroupings[0]}_selector`, "fill", "black", "white")} onMouseEnter={()=>mHover(`${dataGroupings[0]}_selector`,"fill", "black", "white")} onClick={()=>{updateSelector(dataGroupings[0])}}>
                      <title>Show {`${dataGroupings[0]}`}</title>
                  </rect>
                  
                  <rect className={`${dataGroupings[1]}_selector`} x={115} y={50} height={25} width={100} fill={"white"}stroke="black" strokeWidth="2" />
                  <text className={`${dataGroupings[1]}_selector`} x={165} y={65} dominantBaseline="middle" textAnchor="middle" fill="black" style={{fontSize:"1.3em", fontWeight:"bold"}}>{dataGroupings[1]}</text>
                  <rect x={115} y={50} height={25} width={100} fill="transparent" stroke="black" strokeWidth="2" style={{cursor:"pointer"}} onMouseLeave={()=>mHover(`${dataGroupings[1]}_selector`, "fill", "black", "white")} onMouseEnter={()=>mHover(`${dataGroupings[1]}_selector`,"fill", "black", "white")} onClick={()=>{updateSelector(dataGroupings[1])}}>
                      <title>Show {`${dataGroupings[1]}`}</title>
                  </rect>
                </>;
      break;

    case 3:
      selectors =<>
                  <rect className={`${dataGroupings[0]}_selector`} x={115} y={25} height={25} width={50} fill={"white"}stroke="black" strokeWidth="2" />
                  <text className={`${dataGroupings[0]}_selector`} x={140} y={40} dominantBaseline="middle" textAnchor="middle" fill="black" style={{fontSize:"1.3em", fontWeight:"bold"}}>{dataGroupings[0][0]}</text>
                  <rect x={115} y={25} height={25} width={50} fill="transparent" stroke="black" strokeWidth="2" style={{cursor:"pointer"}} onMouseLeave={()=>mHover(`${dataGroupings[0]}_selector`, "fill", "black", "white")} onMouseEnter={()=>mHover(`${dataGroupings[0]}_selector`,"fill", "black", "white")} onClick={()=>{updateSelector(dataGroupings[0])}}>
                      <title>Show {`${dataGroupings[0]}`}</title>
                  </rect>
                  <rect className={`${dataGroupings[1]}_selector`} x={165} y={25} height={25} width={50} fill={"white"}stroke="black" strokeWidth="2" />
                  <text className={`${dataGroupings[1]}_selector`} x={190} y={40} dominantBaseline="middle" textAnchor="middle" fill="black" style={{fontSize:"1.3em", fontWeight:"bold"}}>{dataGroupings[1][0]}</text>
                  <rect x={165} y={25} height={25} width={50} fill="transparent" stroke="black" strokeWidth="2" style={{cursor:"pointer"}} onMouseLeave={()=>mHover(`${dataGroupings[1]}_selector`, "fill", "black", "white")} onMouseEnter={()=>mHover(`${dataGroupings[1]}_selector`,"fill", "black", "white")} onClick={()=>{updateSelector(dataGroupings[1])}}>
                      <title>Show {`${dataGroupings[1]}`}</title>
                  </rect>
                  <rect className={`${dataGroupings[2]}_selector`} x={140} y={50} height={25} width={50} fill={"white"}stroke="black" strokeWidth="2" />
                  <text className={`${dataGroupings[2]}_selector`} x={165} y={65} dominantBaseline="middle" textAnchor="middle" fill="black" style={{fontSize:"1.3em", fontWeight:"bold"}}>{dataGroupings[2][0]}</text>
                  <rect x={140} y={50} height={25} width={50} fill="transparent" stroke="black" strokeWidth="2" style={{cursor:"pointer"}} onMouseLeave={()=>mHover(`${dataGroupings[2]}_selector`, "fill", "black", "white")} onMouseEnter={()=>mHover(`${dataGroupings[2]}_selector`,"fill", "black", "white")} onClick={()=>{updateSelector(dataGroupings[2])}}>
                      <title>Show {`${dataGroupings[2]}`}</title>
                  </rect>
                </>;
      break;

    case 4:
      selectors =<>
                  <rect className={`${dataGroupings[0]}_selector`} x={115} y={25} height={25} width={50} fill={"white"}stroke="black" strokeWidth="2" />
                  <text className={`${dataGroupings[0]}_selector`} x={140} y={40} dominantBaseline="middle" textAnchor="middle" fill="black" style={{fontSize:"1.3em", fontWeight:"bold"}}>{dataGroupings[0][0]}</text>
                  <rect x={115} y={25} height={25} width={50} fill="transparent" stroke="black" strokeWidth="2" style={{cursor:"pointer"}} onMouseLeave={()=>mHover(`${dataGroupings[0]}_selector`, "fill", "black", "white")} onMouseEnter={()=>mHover(`${dataGroupings[0]}_selector`,"fill", "black", "white")} onClick={()=>{updateSelector(dataGroupings[0])}}>
                      <title>Show {`${dataGroupings[0]}`}</title>
                  </rect>
                  <rect className={`${dataGroupings[1]}_selector`} x={165} y={25} height={25} width={50} fill={"white"}stroke="black" strokeWidth="2" />
                  <text className={`${dataGroupings[1]}_selector`} x={190} y={40} dominantBaseline="middle" textAnchor="middle" fill="black" style={{fontSize:"1.3em", fontWeight:"bold"}}>{dataGroupings[1][0]}</text>
                  <rect x={165} y={25} height={25} width={50} fill="transparent" stroke="black" strokeWidth="2" style={{cursor:"pointer"}} onMouseLeave={()=>mHover(`${dataGroupings[1]}_selector`, "fill", "black", "white")} onMouseEnter={()=>mHover(`${dataGroupings[1]}_selector`,"fill", "black", "white")} onClick={()=>{updateSelector(dataGroupings[1])}}>
                      <title>Show {`${dataGroupings[1]}`}</title>
                  </rect>
                  <rect className={`${dataGroupings[2]}_selector`} x={115} y={50} height={25} width={50} fill={"white"}stroke="black" strokeWidth="2" />
                  <text className={`${dataGroupings[2]}_selector`} x={140} y={65} dominantBaseline="middle" textAnchor="middle" fill="black" style={{fontSize:"1.3em", fontWeight:"bold"}}>{dataGroupings[2][0]}</text>
                  <rect x={115} y={50} height={25} width={50} fill="transparent" stroke="black" strokeWidth="2" style={{cursor:"pointer"}} onMouseLeave={()=>mHover(`${dataGroupings[2]}_selector`, "fill", "black", "white")} onMouseEnter={()=>mHover(`${dataGroupings[2]}_selector`,"fill", "black", "white")} onClick={()=>{updateSelector(dataGroupings[2])}}>
                      <title>Show {`${dataGroupings[2]}`}</title>
                  </rect>
                  <rect className={`${dataGroupings[3]}_selector`} x={165} y={50} height={25} width={50} fill={"white"}stroke="black" strokeWidth="2" />
                  <text className={`${dataGroupings[3]}_selector`} x={190} y={65} dominantBaseline="middle" textAnchor="middle" fill="black" style={{fontSize:"1.3em", fontWeight:"bold"}}>{dataGroupings[3][0]}</text>
                  <rect x={165} y={50} height={25} width={50} fill="transparent" stroke="black" strokeWidth="2" style={{cursor:"pointer"}} onMouseLeave={()=>mHover(`${dataGroupings[3]}_selector`, "fill", "black", "white")} onMouseEnter={()=>mHover(`${dataGroupings[3]}_selector`,"fill", "black", "white")} onClick={()=>{updateSelector(dataGroupings[3])}}>
                      <title>Show {`${dataGroupings[3]}`}</title>
                  </rect>
                </>;
      break;
  }
  
  let graphs = [];
  if(maxVal !== null && minVal !== null)
  {    
    let sortedKeys = Object.keys(reading).sort();
    let default_graph_length = 300;
    let graph_width = (default_graph_length - (10 * (sortedKeys.length - 1))) / sortedKeys.length;
    
    if(Object.keys(reading).includes(dataToDisplay[0]))
    {//Verify the dataToDisplay has been updated correctly after the sensor data type has changed
      graph_width = (default_graph_length - (10 * (dataToDisplay.length - 1))) / dataToDisplay.length;
      graphs.push(
        <>
          <rect x={225} y={25} height={75} width={combineGraphs ? default_graph_length : graph_width + 70} fill="white" stroke="black" strokeWidth="2" /> 
          <SmoothLineGraph x={285} y={25.5} height={74} width={combineGraphs ? default_graph_length : graph_width}  x_axis_offset={10} y_axis_offset={12.5} data={reading[dataToDisplay[0]].length !== 0 ? reading[dataToDisplay[0]] : [0]} data_max={maxVal} data_min={minVal} display_markers={true} stroke_color={colors[dataToDisplay[0]]} stroke_dasharray={dashes[dataToDisplay[0]]} display_reset_icon={false}/>
          <rect x={295} y={105} height={20} width={graph_width} fill="white" stroke="black" strokeWidth="2" />
          <line x1={294 + (graph_width * 0.75)} y1={115} x2={294 + graph_width} y2={115} strokeWidth={3.5} stroke={colors[dataToDisplay[0]]} strokeDasharray={dashes[dataToDisplay[0]]} />
          <text x={305} y={116.75} dominantBaseline="middle" textAnchor="start" fill="black" stroke="white" paintOrder="stroke" style={{fontSize:"1.3em", fontWeight:"bold"}}>{`${dataToDisplay.length < 3 ? dataToDisplay[0] : dataToDisplay[0][0]} : ${reading[dataToDisplay[0]].length !== 0 ? reading[dataToDisplay[0]][reading[dataToDisplay[0]].length - 1] : "NA"}`}</text>
        </>
      );
      for(let i = 1; i < dataToDisplay.length; i++)
      {
        graphs.push(
          <>
            {/*Instead of to the side all other graphs will have markers in the background - saving code just in case displayRanges[dataToDisplay[i]] && !combineGraphs ? <rect x={295 + ((10 + graph_width) * i)} y={25} height={75} width={graph_width} fill="white" stroke="black" strokeWidth="2" />: <> </>*/}
            {/* Old values: x={combineGraphs ? 285 : 295 + ((10 + graph_width) * i) + (displayRanges[dataToDisplay[i]] ? 70 : 0)} width={combineGraphs ? default_graph_length : displayRanges[dataToDisplay[i]] ? graph_width - 80 : graph_width} x_axis_offset={combineGraphs || displayRanges[dataToDisplay[i]] ? 10 : 0} */}
            <SmoothLineGraph x={combineGraphs ? 285 : 295 + ((10 + graph_width) * i)} y={25.5} height={74} width={combineGraphs ? default_graph_length : graph_width}  x_axis_offset={combineGraphs ? 10 : 0} y_axis_offset={12.5} data={reading[dataToDisplay[i]].length !== 0 ? reading[dataToDisplay[i]] : [0]} data_max={maxVal} data_min={minVal} display_markers={false} display_markers_in_background={false} stroke_color={colors[dataToDisplay[i]]} stroke_dasharray={dashes[dataToDisplay[i]]} background_color={combineGraphs ? "transparent" : "white"} display_reset_icon={false}/>
            <rect x={295 + ((10 + graph_width) * i)} y={105} height={20} width={graph_width} fill="white" stroke="black" strokeWidth="2" />
            <line x1={294 + ((10 + graph_width) * i) + (graph_width * 0.75)} y1={115} x2={294 + ((10 + graph_width) * i) + graph_width} y2={115} strokeWidth={3.5} stroke={colors[dataToDisplay[i]]} strokeDasharray={dashes[dataToDisplay[i]]} />
            <text x={305 + ((10 + graph_width) * i)} y={116.75} dominantBaseline="middle" textAnchor="start" stroke="white" fill="black" paintOrder="stroke" style={{fontSize:"1.3em", fontWeight:"bold"}}>{`${dataToDisplay.length < 3 ? dataToDisplay[i] : dataToDisplay[i][0]} : ${reading[dataToDisplay[i]].length !== 0 ? reading[dataToDisplay[i]][reading[dataToDisplay[i]].length - 1] : "NA"}`}</text>
          </>
        );
      }  
    }
  }

  /* This is fairly inefficient. TODO: Look up better way to set the max/min values */
function setMaxMin(){
  let keys = Object.keys(reading);
  for (let i = 0; i < keys.length; i++) {
    if(reading[keys[i]][reading[keys[i]].length-1] > maxVal){
      setMaxVal(reading[keys[i]][reading[keys[i]].length-1]);
    }
    else if(reading[keys[i]][reading[keys[i]].length-1] < minVal){
      setMinVal(reading[keys[i]][reading[keys[i]].length-1]);
    }
  }
}  

function resetMaxMin(){
  let keys = Object.keys(reading);
  let max = Math.max(...reading[keys[0]]);
  let min = Math.min(...reading[keys[0]]);
  for (let i = 1; i < keys.length; i++) {
    let tempMax = Math.max(...reading[keys[i]]);
    let tempMin = Math.min(...reading[keys[i]]);
    if(tempMax > max){
      max = tempMax;
    }
    if(tempMin < min){
      min = tempMin;
    }
  }
  setMaxVal(max);
  setMinVal(min);
}

function mHover(class_name, attribute, colorA, colorB){
  document.querySelectorAll("." + class_name).forEach(element =>{
          element.setAttribute(attribute, element.getAttribute(attribute) == colorA ? colorB : colorA);
      }
  );
}

function updateSelector(grouping_name){
  let copy = dataToDisplay;
  if(dataToDisplay.includes(grouping_name))
  {
    copy.splice(dataToDisplay.indexOf(grouping_name), 1);
    setDataToDisplay([...copy]);
  }
  else{
    copy.push(grouping_name);
    copy.sort()
    setDataToDisplay([...copy]);
  }
}

  return (
      <>
        <svg 
            width={width} 
            height={height}
            viewBox={"0 0 500 150"}
            x={x}
            y={y}
            preserveAspectRatio="xMinYMin"
        >   
        
          {/* Logo 
            {logo}
            <line x1={100} x2={100} y1={25} y2={125} stroke="white" strokeWidth="4" />
            <text x={50} y={110} dominantBaseline="middle" textAnchor="middle" fill="white" style={{fontSize:"1.5em", fontWeight:"bolder"}}>{name}</text>
          */}
          <g transform ={useVerticalLayout ? "scale(1.5)": "translate(-100)"}>
            {/* Data Grouping Selectors */}
            {selectors}

            {/* Split Graphs Button */}
            <rect className={`seperate_graphs`} x={115} y={75} height={25} width={100} fill={"white"}stroke="black" strokeWidth="2" />
            <text className={`seperate_graphs`} x={165} y={90} dominantBaseline="middle" textAnchor="middle" fill="black" style={{fontSize:"1.3em", fontWeight:"bold"}}>Split</text>
            <rect x={115} y={75} height={25} width={100} fill="transparent" stroke="black" strokeWidth="2" style={{cursor:"pointer"}} onMouseLeave={()=>mHover(`seperate_graphs`, "fill", "black", "white")} onMouseEnter={()=>mHover(`seperate_graphs`,"fill", "black", "white")} onClick={()=>{setCombineGraphs(false)}}>
                <title>Split Graphs</title>
            </rect>

            {/* Combine Graphs Button */}
            <rect className={`combine_graphs`} x={115} y={100} height={25} width={100} fill={"white"}stroke="black" strokeWidth="2" />
            <text className={`combine_graphs`} x={165} y={115} dominantBaseline="middle" textAnchor="middle" fill="black" style={{fontSize:"1.3em", fontWeight:"bold"}}>Combine</text>
            <rect x={115} y={100} height={25} width={100} fill="transparent" stroke="black" strokeWidth="2" style={{cursor:"pointer"}} onMouseLeave={()=>mHover(`combine_graphs`, "fill", "black", "white")} onMouseEnter={()=>mHover(`combine_graphs`,"fill", "black", "white")} onClick={()=>{setCombineGraphs(true)}}>
                <title>Combine Graphs</title>
            </rect>
          </g>
          <g transform ={useVerticalLayout ? "translate(-250 165) scale(1.25)": "translate(-100)"}>
            {/* Data Graphs*/}
            {graphs}

            {/* Download Button */}
            <rect className={`graph_download`} x={225} y={105} width={32.5} height={20} fill="white" stroke="black" strokeWidth={2}/>
            <svg className={`graph_download`} viewBox="0 0 24 24" width={15} height={15} preserveAspectRatio="xMinYMin" x={233.75} y={107.5} ><path d="M4.97 11.03a.75.75 0 111.06-1.06L11 14.94V2.75a.75.75 0 011.5 0v12.19l4.97-4.97a.75.75 0 111.06 1.06l-6.25 6.25a.75.75 0 01-1.06 0l-6.25-6.25zm-.22 9.47a.75.75 0 000 1.5h14.5a.75.75 0 000-1.5H4.75z"></path></svg>
            <rect x={225} y={105} width={32.5} height={20} fill="transparent" style={{cursor:"pointer"}} onMouseEnter={() =>{mHover(`graph_download`, "fill", "white", "black")}} onMouseLeave={() =>{mHover(`graph_download`, "fill", "white", "black")}} onClick={()=>{download(name)}}/>            

            {/* Reset Button*/}
            <rect className={`graph_reset`} x={260} y={105} width={32.5} height={20} fill="white" stroke="black" strokeWidth={2}/>
            <svg className={`graph_reset`} transform="rotate(" preserveAspectRatio="xMinYMin" x={267.5} y={105} width="20" height="20" fill="black" viewBox="0 0 16 16">
                <g transform="rotate(-90,8,8)">
                    <path fill-rule="evenodd" d="M8 3a5 5 0 1 1-4.546 2.914.5.5 0 0 0-.908-.417A6 6 0 1 0 8 2v1z"/>
                    <path d="M8 4.466V.534a.25.25 0 0 0-.41-.192L5.23 2.308a.25.25 0 0 0 0 .384l2.36 1.966A.25.25 0 0 0 8 4.466z"/>
                </g>
            </svg>
            <rect x={260} y={105} width={32.5} height={20} fill="transparent" style={{cursor:"pointer"}} onMouseEnter={() =>{mHover(`graph_reset`, "fill", "white", "black")}} onMouseLeave={() =>{mHover(`graph_reset`, "fill", "white", "black")}} onClick={()=>{resetMaxMin()}}/>            
          </g>
        </svg> 
      </>
    )
}

export default Multi_SmoothLineGraph
