
import React, { useState, useEffect } from 'react';

function LedArray({ id="overlay", x, y, trigger, row=5, col=5, spacing=17.5, led_settings}) {

  let matrix = [];
  for(let r = 0; r < row; r++)
  {
    for(let c = 0; c < col; c++)
    {// 
      matrix.push(<rect id={`led_array_${r}_${c}_${id}`} fill={led_settings[r][c] == 0 ? "white" : "red"} style={{cursor:'pointer'}} onClick={() => {trigger( led_settings[r][c] == 0 ? "LED_PLOT" : "LED_UNPLOT", { x: c, y: r} ); document.getElementById(`led_array_${r}_${c}_${id}`).setAttribute("fill", led_settings[r][c] == 0 ? "white" : "red")}} class="clickable_gold clickabl_1_5" x={x + (c * spacing)} y={y + (r * spacing) } width={7} height={12}>
                    <title>
                      ({c},{r})
                    </title>  
                  </rect>);;
    }
  }
    
  
  return(
      <>
        {matrix}
      </>
    )
}

export default LedArray