import React, { useState, useEffect } from 'react';
import Layout from '../Debugger_Components/Layouts/Layout.js';
import BBC_Data from '../Debugger_Components/Boards/BBCMicrobit/data.js';
import CPE_Data from '../Debugger_Components/Boards/PlaygroundExpress/data.js';

function Controller({board, reader, writer, displayHelper, displayCode, port, disconnect, reset}) {
  let textBuffer = "";//Remove
  const[deviceData, setDeviceData] =  useState({variables : {},breakpoints: [], external_sensor_names: [], external_sensor_values: {}, serial: "", ...CPE_Data.starter_data});
  const typeToCode = {'RESERVED': "0", 'SYNC': "1", 'PIN_MODE': "2,", 'PIN_READ': "3,",  'DIGITAL': "4,", 'PWM': "5,", 'Print_On': "9", 'Print_Off': "10", 'HOLD': "11",  'RELEASE' : "12", 'STEP': "13", 'STEP_NO_STOP': "15", 'VAR': "18,", 'SENSOR_NAMES': "21", 'EXTERNAL_SENSOR': "22,", 'LED_PLOT': "31,", 'LED_UNPLOT': "32,", 'Neopixel': "34,", 'Neopixel Brightness': "35,", 'Capacitive': "36,",  'Calibrate Compass': "41", 'Compass': "42", 'Magnet': "43", 'Set Acceleration': "44,", 'Motion': "45", 'Rotation': "46",'Gesture': "47", 'Light': "51",'Temp': "52", 'Humidity': "53", 'Pressure': "54", 'eCO2': "56", 'TVOC': "57", 'Soil': "58", 'Microphone': "61", 'Speaker': "62," }

  useEffect(()=>{
   import(`../Debugger_Components/Boards/${board}/data.js`)
      .then(
            ({default: data}) => setDeviceData({variables : {},breakpoints: [], external_sensor_names: [], external_sensor_values: {}, serial: "", ...data.starter_data})) 
      .catch(
            err=> console.log("Unable to load starter data for board: " + board + "\n" + err))
  }, [board]);

  useEffect(() =>{//Ping board for pin data after setting a new port
    if(writer !== null)
    {
      startReader();
      queryBoard("SYNC", {});
      //queryBoard("PIN_READ", {signalType: 0, pin: "D12"});//CP Express specific - Deprecate
    }
  }, [writer]);
    
  /**
   * This method will accept a string message, encode it, and transmit it, via the Serial Writer, to the connected Serial Device.
   * This method ensures the writer is not null, to avoid a potential null exception.
   * 
   * @param {string} dataPackage 
   */
  async function write(dataPackage)
  {
    dataPackage+= ",|";
    await writer.write(dataPackage).catch(() =>{
      console.error("Can't send message. Writer set to null, ensure port has been set");
    });
  }/* End write(dataPackage) */

  /**
   * This method handles most of the Live Debugger's communication with the Serial Device. The selection parameter determines what type 
   * of information is being requested from or passed to the Serial Device. The data parameter is an optional object that holds any values needed
   * for the message being sent.
   * 
   * @param {string} selection 
   * @param {object} data 
   */
  function queryBoard(selection, data)
  {
    if(writer)
    {//Ensure writer has been set/created before trying to query the board
      try{
        //Don't call for new data until all existing data has been received
        //while(textBuffer !== ""){} //Deprecate? With the microcontroller now holding the timer, this could cause commands not to be sent
        let dataPackage = typeToCode[selection];
        let copy = null;
        switch(selection)
        {
          case 'STEP'://Update CC to reflect that the breakpoint has been exited
            setDeviceData(deviceData => { 
              let breakpoints = {breakpoints: deviceData.breakpoints.concat("")};
              return {...deviceData, ...breakpoints }
            });
          break;

          case 'PIN_READ': //Read a pin's digital value
            dataPackage +=  data.signalType.toString() + "," + data.pin.toString().substring(1);
            break;

          case 'PWM': //Have microcontroller update a pin's PWM/AnalogWrite()
            dataPackage += data.pin.toString().substring(1) +"," +   data.value.toString();
            copy = deviceData;
            copy.pwm[data.pin.toString()] = data.value;
            setDeviceData(copy);
            break;
          
          case 'DIGITAL': //Update a pin's digital value
            dataPackage += data.pin.toString().substring(1) +"," +   data.value.toString();
            break;
            
          case 'LED_PLOT': //Turn a specific LED in the matrix on 
            dataPackage += data.x.toString() +"," +   data.y.toString();
            copy = deviceData;
            copy.led_array[data.y][data.x] = 1;
            setDeviceData(copy);
            break;

          case 'LED_UNPLOT':  //Turn a specific LED in the matrix off
            dataPackage += data.x.toString() +"," +   data.y.toString();
            copy = deviceData;
            copy.led_array[data.y][data.x] = 0;
            setDeviceData(copy);
            break;

          case 'VAR': //Handle a variable update
            dataPackage += data.name + "," + data.value +"," +  data.type;
            copy = deviceData;
            copy.variables[data.name].value = data.value;
            setDeviceData(copy);
            break;

          case 'EXTERNAL_SENSOR':
            dataPackage += data.name;
          break;

          case 'PIN_MODE':
            dataPackage += data.pin + "," + data.mode;
          break;

          case 'Neopixel':
            dataPackage += data.pixel + "," + data.red + "," + data.green + "," + data.blue;
          break;

          case 'Speaker':
            dataPackage += data.freq + "," + data.time;
          break;

          case 'Capacitive': //Update a pin's digital value
            dataPackage += data.pin.substring(1);
            break;
            
        }
        write(dataPackage);
      }
      catch(e){
        console.log(e);
      }
    }
  }/* End queryBoard(selection, data) */
  
  /**
   * This method will serve as a listener, constantly reading from the Serial Device and updating the Live Debugger's states based on values read in.
   * This is intended to be the only method that uses the Serial Reader. Any other location may endup consuming the Serial Buffer and cause the system to 
   * miss updates from the microcontroller
   */
  async function startReader(){
    while (true) {  
      let value, done;
      try{
        ({ value, done } = await reader.read());
      }catch(e)
      {
          console.log("Error in Reader Promise.");
          console.log(e);
          console.log("Resetting Reader");
          //reset();    
          break;
      }
        if (value) {
          textBuffer = textBuffer + value;
          if(textBuffer.includes('&'))
          {
            let delim_index = textBuffer.indexOf('&');
            let curly_index = textBuffer.indexOf('{');
            let data = textBuffer.substring(curly_index, delim_index);
            textBuffer = textBuffer.substring(delim_index + 1);//This should only be done if the data value was excepted. Some packages are incomplete and being removed
            try{
              let jsonMsg = JSON.parse(data);
              if(jsonMsg.hasOwnProperty("Message"))
              { //Remove: Was intended for handling Serial Messages, but that may no longer be needed/supported.
                setDeviceData(deviceData => { 
                  let serial = {serial: deviceData.serial.concat(jsonMsg.Message)};
                 /* Code for auto scrolling - add later with on/off feature 
                 try{
                    let monitor = document.getElementById("serial_monitor");
                    if(monitor)
                    {
                      monitor.scrollTop = monitor.scrollHeight;
                    }
                  }catch(e){}*/
                  return {...deviceData, ...serial}
                });
              }
              if(jsonMsg.hasOwnProperty("Reset_LEDs"))
              {
                if(jsonMsg.Reset_LEDs !== undefined)
                {
                  setDeviceData(deviceData =>{
                      let led_array = {led_array: jsonMsg.Reset_LEDs};
                      return {...deviceData, ...led_array }
                  });
                }
              }//Add option for displaying the breakpoint

              //Todo:
              // Setup up a syncing method at the start, where the micro:bit transmits the variable information
              // - Name & Type, this is then returned by CC for validation. Continue until both sides agree
              // 
              if(jsonMsg.hasOwnProperty("Variable"))
              {
                if(jsonMsg.Variable.value !== undefined)
                {
                    setDeviceData(deviceData =>{
                        let variable = {[jsonMsg.Variable.name.replaceAll("|", "")] : {value: jsonMsg.Variable.value, type: jsonMsg.Variable.type}};
                        let variables = {variables: {...deviceData.variables, ...variable }}
                        return {...deviceData, ...variables };
                    });
                    
                }
              }
              if(jsonMsg.hasOwnProperty("Breakpoint"))
              {
                if(jsonMsg.Breakpoint.id !== undefined)
                {
                  setDeviceData(deviceData => { 
                    let breakpoints = {breakpoints: deviceData.breakpoints.concat(jsonMsg.Breakpoint.id)};
                    return {...deviceData, ...breakpoints }
                  });
                } 
              }
              if(jsonMsg.hasOwnProperty("Pins"))
              {
                if(jsonMsg.Pins !== undefined)
                {
                  setDeviceData(deviceData => { 
                    let pin_id = Object.keys(jsonMsg.Pins)[0];
                    let analog_or_digital = pin_id.substring(0, 1);//Remove first character to grab signal type
                    pin_id = pin_id.substring(1);
                    //Check if the pin is analog or digital 
                    let pin_state = analog_or_digital === "A" ? {[pin_id]: deviceData.analog[pin_id].concat(jsonMsg.Pins[Object.keys(jsonMsg.Pins)[0]])} : {[pin_id]: deviceData.digital[pin_id].concat(jsonMsg.Pins[Object.keys(jsonMsg.Pins)[0]])};
                    let updated_pin_data = analog_or_digital === "A" ? {analog: {...deviceData.analog, ...pin_state }} : {digital: {...deviceData.digital, ...pin_state }};
                    return {...deviceData, ...updated_pin_data } ;
                  });
                }
              }
              if(jsonMsg.hasOwnProperty("Motion"))
              {
                if(jsonMsg.Motion !== undefined)
                {
                  setDeviceData(deviceData => { 
                    let motion = {motion: deviceData.motion.concat(jsonMsg.Motion)};
                    return {...deviceData, ...motion }
                  });
                }
              }
              if(jsonMsg.hasOwnProperty("Sensor_Name"))
              {
                if(!deviceData.external_sensor_names.includes("E_" + jsonMsg.Sensor_Name) && jsonMsg.Sensor_Name !== undefined)
                {//Only update if a new sensor names has been provided
                  setDeviceData(deviceData => { 
                      let sensor_names = {external_sensor_names: deviceData.external_sensor_names.concat("E_" + jsonMsg.Sensor_Name)};
                      let sensor_starter = {[jsonMsg.Sensor_Name] : []}
                      let sensor_values = {external_sensor_values: {...deviceData.external_sensor_values, ...sensor_starter}};
                      return {...deviceData, ...sensor_names, ...sensor_values }
                  });
                }
              }
              if(jsonMsg.hasOwnProperty("Sensor"))
              {
                  setDeviceData(deviceData => { 
                    if(deviceData.external_sensor_values.hasOwnProperty(jsonMsg.Sensor.name)){
                      let sensor_data = {[jsonMsg.Sensor.name] : deviceData.external_sensor_values[jsonMsg.Sensor.name].concat(jsonMsg.Sensor.value)};
                      let sensor = {external_sensor_values: {...deviceData.external_sensor_values, ...sensor_data}};
                      return {...deviceData, ...sensor }
                    }
                    else if(jsonMsg.Sensor.name !== undefined)
                    {//It's possible to connect to Circuit Check outside of a breakpoint, meaning the sensor is already transmitting data
                      //This data must be caught and handled to prevent an error.
                      let sensor_names = {external_sensor_names: deviceData.external_sensor_names.concat("E_" + jsonMsg.Sensor.name)};
                      let sensor_starter = {[jsonMsg.Sensor.name] : [jsonMsg.Sensor.value]}
                      let sensor_values = {external_sensor_values: {...deviceData.external_sensor_values, ...sensor_starter}};
                      return {...deviceData, ...sensor_names, ...sensor_values }
                    }
                    else
                      return deviceData;
                  });
              }
              if(jsonMsg.hasOwnProperty("Compass"))
              {
                if(jsonMsg.Compass !== undefined)
                {
                  setDeviceData(deviceData => { 
                    let compass = {compass: deviceData.compass.concat(jsonMsg.Compass)};
                    return {...deviceData, ...compass }
                  });
                }
              }
              if(jsonMsg.hasOwnProperty("Magnet"))
              {
                if(jsonMsg.Magnet !== undefined)
                {
                  setDeviceData(deviceData => { 
                    let magnet = {magnet: deviceData.magnet.concat(jsonMsg.Magnet)};
                    return {...deviceData, ...magnet}
                  });
                }
              }
              if(jsonMsg.hasOwnProperty("Rotation"))
              {
                if(jsonMsg.Rotation !== undefined)
                {
                  setDeviceData(deviceData => { 
                    let rotation = {rotation: deviceData.rotation.concat(jsonMsg.Rotation)};
                    return {...deviceData, ...rotation }
                  });
                }
              }
              if(jsonMsg.hasOwnProperty("Gesture"))
              {
                if(jsonMsg.Gesture !== undefined)
                {
                  setDeviceData(deviceData => { 
                    let gesture = {gesture: deviceData.gesture.concat(jsonMsg.Gesture)};
                    return {...deviceData, ...gesture }
                  });
                }
              }
              if(jsonMsg.hasOwnProperty("Temp"))
              {
                if(jsonMsg.Temp !== undefined)
                {
                  setDeviceData(deviceData => { 
                    let temp = {temp: deviceData.temp.concat(jsonMsg.Temp)};
                    return {...deviceData, ...temp }
                  });
                }
              }
              if(jsonMsg.hasOwnProperty("Humidity"))
              {
                if(jsonMsg.Humidity !== undefined)
                {
                  setDeviceData(deviceData => { 
                    let humidity = {humidity: deviceData.humidity.concat(jsonMsg.Humidity)};
                    return {...deviceData, ...humidity }
                  });
                }
              }
              if(jsonMsg.hasOwnProperty("Pressure"))
              {
                if(jsonMsg.Pressure !== undefined)
                {
                  setDeviceData(deviceData => { 
                    let pressure = {pressure: deviceData.pressure.concat(jsonMsg.Pressure)};
                    return {...deviceData, ...pressure }
                  });
                }
              }
              if(jsonMsg.hasOwnProperty("eCO2"))
              {
                if(jsonMsg.eCO2 !== undefined)
                {
                  setDeviceData(deviceData => { 
                    let eco2 = {eco2: deviceData.eco2.concat(jsonMsg.eCO2)};
                    return {...deviceData, ...eco2 }
                  });
                }
              }
              if(jsonMsg.hasOwnProperty("TVOC"))
              {
                if(jsonMsg.TVOC !== undefined)
                {
                  setDeviceData(deviceData => { 
                    let tvoc = {tvoc: deviceData.tvoc.concat(jsonMsg.TVOC)};
                    return {...deviceData, ...tvoc }
                  });
                }
              }
              if(jsonMsg.hasOwnProperty("Soil"))
              {
                console.log("Entered Soil")
                if(jsonMsg.Soil !== undefined)
                {
                  setDeviceData(deviceData => { 
                    let soil = {soil: deviceData.soil.concat(jsonMsg.Soil)};
                    return {...deviceData, ...soil }
                  });
                }
              }
              if(jsonMsg.hasOwnProperty("Light"))
              {
                if(jsonMsg.Light !== undefined)
                {
                  setDeviceData(deviceData => { 
                    let light = {light: deviceData.light.concat(jsonMsg.Light)};
                    return {...deviceData, ...light }
                  });
                }
              }
              if(jsonMsg.hasOwnProperty("Capacitive"))
              {
                if(jsonMsg.Capacitive !== undefined)
                {
                  setDeviceData(deviceData => { 
                    let pin_id = Object.keys(jsonMsg.Capacitive)[0];
                    //Check if the pin is analog or digital 
                    let pin_state = {[pin_id]: deviceData.capacitive[pin_id].concat(jsonMsg.Capacitive[Object.keys(jsonMsg.Capacitive)[0]])};
                    let updated_pin_data =  {capacitive: {...deviceData.capacitive, ...pin_state }};
                    return {...deviceData, ...updated_pin_data } ;
                  });
                }
                
              }
              if(jsonMsg.hasOwnProperty("Sound"))
              {
                if(jsonMsg.Sound !== undefined)
                {
                  setDeviceData(deviceData => { 
                    let microphone = {microphone: deviceData.microphone.concat(jsonMsg.Sound)};
                    return {...deviceData, ...microphone }
                  });
                }
              }
              //textBuffer = "";//Reset textBuffer - assumes a complete message for each JSON string
            }
            catch(e)
            {
                //console.log("Can't parse yet");
               // console.log(data);
            }
          }
        }
        if (done) {
          console.log('[readLoop] DONE', done);
          try{
          reader.releaseLock();
          }catch(e){
            console.log("Error in releasing reader lock");
            console.log(e);
          }
          break;
        }
      }
  }/* End  startReader() */

  return(
    <>
     <Layout deviceData={deviceData} serialCom={queryBoard} board={board} displayHelper={displayHelper} displayCode={displayCode}/>
    </>
  )
}

export default Controller