
import React, { useState, useEffect } from 'react';
import Code_Display_MakeCode_Blocks from './Code_Display_MakeCode_Blocks.js';
import Code_Display_Text from './Code_Display_Text.js';

function Code_Display({breakpoint_id, code_package, serialCom, x, y, width, height}) {
    //const [code_display, setCodeDisplay] = useState(<></>);
    let code_display = <> </>;
    if(code_package.type === "block")
    {
        code_display = <Code_Display_MakeCode_Blocks breakpoint_id = {breakpoint_id} code_package={code_package} serialCom = {serialCom}  x={x} y={y} width={width} height={height} />
    }
    else
    {//Handle text code here
        code_display = <Code_Display_Text breakpoint_id = {breakpoint_id} code_package={code_package} serialCom = {serialCom}  x={x} y={y} width={width} height={height} />
    }
    
    /*useEffect(() =>{
        
      }, [code_package.code]);*/

   

  return code_display;
}

export default Code_Display