
import React, { Suspense, useState, useEffect } from 'react';
import Breakpoint_Controls from "./Misc_Handlers/Breakpoint_Controls.js"
import Variables_Display from "./Variable_Handlers/Variables_Display.js";
import Sensor_Selector from "./Sensor_Handlers/Sensor_Selector.js";
import Sensor_Display from "./Sensor_Handlers/Sensor_Display.js";
import Code_Display from "./Code_Handlers/Code_Display.js";
import Code_Selector from "./Code_Handlers/Code_Selector.js";
import Code_Updater from "./Code_Handlers/Code_Updater.js";
import BBC_Board from "../Boards/BBCMicrobit/Board.js"
import BBC_BoardOverlay from "../Boards/BBCMicrobit/Board_Overlay.js"
import BBC_Data from "../Boards/BBCMicrobit/data.js"
import CPE_Data from "../Boards/PlaygroundExpress/data.js"
import useWindowDimensions from './WindowDimensions.js';
import Guide from "../Helper/Guide.js"





/**
 *  This component holds the Live Debugger UI (determined by the board chosen by the user). This component requires two data parameters (pinData, deviceData), which hold
 * the current states of the analog/digital pins (pinData) and the states of onboard sensors (deviceData). This component also accepts a serial communication method, which should be
 * (queryBoard(selection, data)), to query the board for new data or update the boards pins/sensors.
 * @param {*} param0 
 */
function Layout({board, serialCom, deviceData, displayCode, displayHelper}) {
  const [hold, setHold] = useState(false);
  const [selectedSensor, setSelectedSensor] = useState("LED");
  const [displayCodeUploader, setDisplayCodeUploader] = useState(false);
  const [selectedPin, setSelectedPin] = useState(CPE_Data.pins[0]);
  const [boardInfo, setBoardInfo] = useState(CPE_Data);
  const [code, setCode] = useState(null);
  const [breakpointList, setBreakpointList] = useState(null);
  const [selectedDataView, setSelectedDataView] = useState("HARDWARE");
  const [BoardGraphic, setBoardSVG] = useState(React.lazy(() => import(`../Boards/PlaygroundClassic/Board.js`)));//<BBC_Board x={displayMode ==="Basic" ? 562.5 : 15} y={displayMode ==="Basic" ? 165 : 0} width={displayMode ==="Basic" ? 600 : 450} height={displayMode ==="Basic" ? 600 : 450} />);
  const [BoardOverlay, setBoardOverlay] = useState(React.lazy(() => import(`../Boards/PlaygroundClassic/Board_Overlay.js`)));//<BBC_BoardOverlay x={displayMode ==="Basic" ? 562.5 : 15} y={displayMode ==="Basic" ? 165 : 0} width={displayMode ==="Basic" ? 600 : 450} height={displayMode ==="Basic" ? 600 : 450} selectedSensor={selectedSensor} selectedPin={selectedPin} setSelectedPin={setSelectedPin} serialCom={serialCom} deviceData ={deviceData} />);
  const { height, width } = useWindowDimensions();
  
  useEffect(()=>{
    setBoardSVG(React.lazy(() => import(`../Boards/${board}/Board.js`)));//.then(BoardGraphic => setBoardSVG()).catch(err=> console.log("Unable to load SVG for board: " + board));
    setBoardOverlay(React.lazy(() => import(`../Boards/${board}/Board_Overlay.js`)));//.then(BoardOverlay => setBoardOverlay()).catch(err=> console.log("Unable to load overlay for board: " + board));
    import(`../Boards/${board}/data.js`)
        .then(({default: data}) => {setSelectedPin(data.pins[0]); setBoardInfo(data);})
        .catch(err=> console.log("Unable to load data for board: " + board));
  }, [board]);

  useEffect(() =>{//Add modifyier warning when pin changes
    if(selectedSensor !== "Pins" && selectedSensor !== "Led Matrix")
    {
       if(selectedSensor.startsWith("E_"))
        {
            serialCom("EXTERNAL_SENSOR", {name: selectedSensor.substr(2)});
        }
        else
        {
            serialCom(selectedSensor, {});
        }
    }
  }, [selectedSensor]);

  useEffect(()=>{
    setDisplayCodeUploader(false);
  }, [code]);

  let codeDisplay =<Code_Updater x={width * 0.52} y={height * 0.11} height={height * 0.525} updateCode={setCode} />;
  if(!displayCodeUploader && code !== null)
  {
      codeDisplay = <>
                        <Code_Display x={width * 0.52} y={height * 0.12} width={width * 0.46} height={height * 0.745} code_package={code} serialCom={serialCom} breakpoint_id={deviceData.breakpoints.length > 0 ? deviceData.breakpoints[deviceData.breakpoints.length - 1] : "NA"}/>
                        <Code_Selector x={width * 0.8375} y={height * 0.115} height={height* 0.0625} showCodeSelector={() => {setDisplayCodeUploader(!displayCodeUploader)}} />
                    </>;
  }
  //Modified for gator:bit, changed order, moved pins, leds, button to the end
  let sensor_list = [  "Pins", "LED", "Button", ...boardInfo.onboard_sensors, ...deviceData.external_sensor_names].filter(x => x !== null);
  let sensorOptions = [];
  for(let i = 0; i < sensor_list.length; i++)
  {
      sensorOptions.push(sensor_list[i] === selectedSensor ?<option value={`${i}`} selected>{sensor_list[i]}</option> : <option value={`${i}`}>{sensor_list[i]}</option>);
  }

  function mHover(class_name, attribute, colorA, colorB){
    document.querySelectorAll("." + class_name).forEach(element =>{
            element.setAttribute(attribute, element.getAttribute(attribute) == colorA ? colorB : colorA);
        }
    );
  }

  function changeSensor()
  {
    var sensorSelect = document.getElementById("sensor_selector_dropdown");
    var value = sensorSelect.options[sensorSelect.selectedIndex].text;
    sensorSelect.selectedIndex = 0;
    if(value !== "Select Sensor")
    {
        setSelectedSensor(value);
    }
  }

  return (
      <>
        <svg 
            /* Research how to make this more responsive/better width & height values width < 700 ? "0 0 720 1100" : width < 1024 ? "0 0 720 760" : "0 0 1440 720" */
            viewBox={`0 0 ${width} ${height * 0.92}`}
            x={0}
            y={height * 0.09}
            preserveAspectRatio="xMinYMin"
        >  
            {displayCode ? 
                <> {/* UI to interact with Code */}
                    <rect  x={width * 0.5} y={0} width={width * 0.5} height={height * 0.9} stroke="var(--bgColor1)" fill="var(--bgColor1)" rx={10}ry={10}/>
                    <rect  x={width * 0.51} y={height * 0.1} width={width * 0.48} height={height * 0.78} stroke="black" fill="white" strokeWidth={3} rx={10}ry={10}/>
                    {codeDisplay}
                    {/*<Code_Selector x={1245} y={5} height={40} showCodeSelector={() => {setDisplayCodeUploader(!displayCodeUploader)}} />  */}
                    <Breakpoint_Controls x={width * 0.535} y={height*0.009} height={height * 0.085} breakpoint_id={deviceData.breakpoints[deviceData.breakpoints.length - 1]} serialCom={serialCom}   />

                    {/* Sensor Selector & Display */}
                    <rect  x={0} y={0} width={width * 0.49} height={height * 0.4} stroke="var(--bgColor1)" fill="var(--bgColor1)" rx={10}ry={10}/>
                    <rect  x={(width * 0.005)} y={(height * 0.05)} width={width * 0.48} height={height * 0.34} stroke="black" strokeWidth={3} fill="white" rx={10}ry={10}/>
                    <rect  x={width * 0.14} y={(height * 0.015)} width={width *0.21} height={height * 0.07} stroke="black" strokeWidth={3} fill="white" rx={10}ry={10}/>
                    <foreignObject x={width * 0.145} y={(height * 0.02)} width={width *0.20} height={height * 0.06}> 
                        <select id="sensor_selector_dropdown" style={{height:"100%", width:"100%", fontSize:`${(width * 0.0015)}em`, fontWeight: "bolder", borderColor:"white", cursor:"pointer"}} onChange={()=>changeSensor()}>
                            {sensorOptions}
                        </select>
                    </foreignObject>
                    <Sensor_Display x={-1} y={(height * 0.09)} width={width * 0.49} serialCom={serialCom} deviceData={deviceData} selectedSensor={selectedSensor} selectedPin={selectedPin} setSelectedPin={setSelectedPin} modeText={boardInfo.modeText} modes={boardInfo.modes} pinList={boardInfo.pins} useVerticalLayout={false} />

                    {/* Variables */}
                    <rect x={0} y={height * 0.42} width={width * 0.49} height={height * 0.48} stroke="var(--bgColor1)" fill="var(--bgColor1)" rx={10}ry={10}/>
                    <rect x={(width * 0.005)} y={(height * 0.47)} width={width * 0.48} height={height * 0.42} stroke="black" strokeWidth={3} fill="white" rx={10}ry={10}/> 
                    <rect x={width * 0.14} y={(height * 0.435)} width={width *0.21} height={height * 0.07} stroke="black" strokeWidth={3} fill="white" rx={10}ry={10}/>
                    <text x={(width * 0.14) + (width *0.21)/2} y={(height * 0.435) + (height * 0.08)/2} alignmentBaseline="middle" textAnchor="middle" fill="black" style={{fontSize:"3em", fontWeight:"bolder"}}>
                        Variables
                    </text>
                    <Variables_Display x={(width * 0.007)} y={(height * 0.525)} width={width * 0.476} height={height * 0.35} serialCom={serialCom} variableData={deviceData.variables} useVerticalLayout={false} />
                </>
                : 
                <> {/* Default view - no Code */}
                {/* Microcontroller Display */}
                <Suspense fallback={<div>Loading...</div>}>
                    <BoardGraphic x={0} y={(height * 0.45) *0.5 - ((width *0.27) * 0.8) *0.5} width={width *0.27} height={(width *0.27) * 0.8} />
                    <BoardOverlay x={0} y={(height * 0.45) *0.5 - ((width *0.27) * 0.8) *0.5} width={width *0.27} height={(width *0.27) * 0.8} selectedSensor={selectedSensor} selectedPin={selectedPin} setSelectedPin={setSelectedPin} serialCom={serialCom} deviceData ={deviceData} />
                </Suspense>

                {/* Sensor Selector */}
                <rect x={width * 0.01} y={(height * 0.455)} width={width *0.25} height={height * 0.1} fill="var(--bgColor1)" rx={width * 0.01} ry={width * 0.01} />
                <foreignObject x={width * 0.035} y={(height * 0.465)} width={width *0.20} height={height * 0.08}> 
                    <select id="sensor_selector_dropdown" style={{height:"100%", width:"100%", fontSize:`${(width * 0.002)}em`, fontWeight: "bolder", borderColor:"var(--bgColor1)"}} onChange={()=>changeSensor()}>
                        {sensorOptions}
                    </select>
                </foreignObject>

                {/* Pause/Resume Code Controls */}
                <rect x={width * 0.01} y={(height * 0.5675)} width={width *0.25} height={height * 0.334} fill="white" stroke="var(--bgColor1)" strokeWidth={height * 0.01} rx={width * 0.01} ry={width * 0.01}/>
                <rect className="pause_code_button" x={width * 0.01} y={(height * 0.5675)} width={width *0.25} height={height * 0.334} fill="white"  rx={width * 0.01} ry={width * 0.01}/>
                <rect x={width * 0.035} y={(height * 0.5925)} width={width *0.2} height={height * 0.284} fill="white"  rx={width * 0.01} ry={width * 0.01}/>
                
                    <text x={width * 0.01 + (width *0.25)/2} y={(height * 0.6595)} alignmentBaseline="middle" textAnchor="middle" fill="black" style={{fontSize:`${width >= 1400 ? 3.5 : width >= 1200 ? 3 : 2}em`, fontWeight:"bold"}}>
                            Click to
                    </text>                    
                    {!hold ? 
                        <text x={width * 0.01 + (width *0.25)/2} y={(height * 0.7345)} alignmentBaseline="middle" textAnchor="middle" fill="red" style={{fontSize:`${width >= 1400 ? 4.5 : width >= 1200 ? 4 : 3}em`, fontWeight:"bold"}}>
                                Pause
                        </text> : 
                        <text x={width * 0.01 + (width *0.25)/2} y={(height * 0.7345)} alignmentBaseline="middle" textAnchor="middle" fill="green" style={{fontSize:`${width >= 1400 ? 4.5 : width >= 1200 ? 4 : 3}em`, fontWeight:"bold"}}>
                                Resume
                        </text>
                    }
                    <text x={width * 0.01 + (width *0.25)/2} y={(height * 0.8095)} alignmentBaseline="middle" textAnchor="middle" fill="black" style={{fontSize:`${width >= 1400 ? 3.5 : width >= 1200 ? 3 : 2}em`, fontWeight:"bold"}}>
                            Your Code
                    </text>
                <rect x={width * 0.01} y={(height * 0.5675)} width={width *0.25} height={height * 0.334} fill="transparent" rx={width * 0.01} ry={width * 0.01} cursor="pointer" onMouseEnter={()=>{mHover("pause_code_button", "fill", "var(--bgColor1)", "white")}} onMouseLeave={()=>{mHover("pause_code_button", "fill", "var(--bgColor1)", "white")}} onClick={() => {setHold(hold => {hold ? serialCom("RELEASE", {}) : serialCom("HOLD", {}); return !hold;})} } />
                
                {/* Sensor Display */}
                <rect x={(width*0.27)} y={0} width={width * 0.73 * (displayHelper ? 0.49 : 1)}  height={(height * 0.45)} fill="var(--bgColor1)" rx={width * 0.01} ry={width * 0.01}>
                    <title>Data Display</title>
                </rect> 
                <Sensor_Display x={(width*0.265)} y={0} width={width * 0.73 * (displayHelper ? 0.49 : 1)} height={(height * 0.45)} serialCom={serialCom} deviceData={deviceData} selectedSensor={selectedSensor} selectedPin={selectedPin} setSelectedPin={setSelectedPin} modeText={boardInfo.modeText} modes={boardInfo.modes} pinList={boardInfo.pins} useVerticalLayout={width < 800} />
                
                {/* Software Controls height={(height * 0.12)} */}
                <rect x={(width*0.27)} y={(height * 0.455)} width={width * 0.73 * (displayHelper ? 0.49 : 1)}  height={(height * 0.45)} fill="var(--bgColor1)" rx={width * 0.01} ry={width * 0.01}>
                    <title>Software Controls</title>
                </rect>
                <Variables_Display x={(width*0.27)} y={(height * 0.47)} width={width * 0.73} height={(height * 0.42)} serialCom={serialCom} variableData={deviceData.variables} useVerticalLayout={width < 800} />
                {/* Use the Breakpoint Name to determine if CC is running <Breakpoint_Controls x={(width*0.27 + (width * 0.73 - (width * 0.5))/2)} y={(height * 0.465)} width={width * 0.5}  serialCom={serialCom} breakpoint_id={deviceData.breakpoints.length > 0 ? deviceData.breakpoints[deviceData.breakpoints.length - 1] : "NA"} isWideLayout={false} useVerticalLayout={width < 1200}  /> */}
                {/*displayHelper ? <Guide x= {(width*0.27) + width * 0.73 * 0.5} y={0} width={width * 0.73 * 0.49} height={height * 0.9} corners={width * 0.01} /> : <></>*/}
                
                </>
            }             
        </svg>
     </>

    );
}

export default Layout